import { Injectable } from "@angular/core";
import { IRecruitmentPlanModel } from "app/recruitment/models/plan/recruitment-plan";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class RecruitmentPlanService extends AppClientService {
  constructor() {
    super();
  }

  getAll() {
    return this.get<IResponse<IRecruitmentPlanModel[]>>(
      "Recruitment/Plan/GetAll"
    );
  }
}
