import { Component, EventEmitter, Input, Output } from "@angular/core";
import { AbstractControl, FormControl } from "@angular/forms";
import { IPolicyGroupType } from "app/org-management/model/policy-group";
import { PolicyGroupService } from "app/org-management/services/policy-group.service";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "policy-group-type-dropdown",
  templateUrl: "./policy-group-type-dropdown.component.html",
  styleUrls: ["./policy-group-type-dropdown.component.scss"]
})
export class PolicyGroupTypeDropdownComponent extends BaseDropdown {
  @Input() control: FormControl | AbstractControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "policyGroupType";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IPolicyGroupType[]>();
  list: IPolicyGroupType[];

  constructor(private policyGroupService: PolicyGroupService) {
    super();
  }


  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  getAll() {
    this.policyGroupService
      .getAllPolicyGroupType()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPolicyGroupType[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.list = res.data;
        } else {
          this.errorList(res?.message?.messages);
        }
      });
  }
}
