import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";
import {
  IEmpWorkExperienceDisplay,
  IEmpWorkExperienceModel,
} from "app/personnel-setup/models/employee-career/emp-work-experience";
import {Idropdown} from "app/shared/interfaces/dropdown.interface";
import {IEmployeeEducationModel} from "app/personnel-setup/models/employee-career/emp-education";

export function empCareerDisplay(
  data: IEmpWorkExperienceModel[]
): IEmpWorkExperienceDisplay[] {
  return data.map((work) => {
    return <IEmpWorkExperienceDisplay>{
      ...work,
      id: work.id,
      companyName: work.companyName,
      department: work.org_Name,
      position: work.job_Title_Name,
      startDate: HelpersFunctions.mapDate(work.from_Date),
      endDate: HelpersFunctions.mapDate(work.to_Date),
    };
  });
}

export function empEducationDisplay(
  data: IEmployeeEducationModel[]
): IEmployeeEducationModel[] {
  return data.map((education) => {
    return <IEmployeeEducationModel>{
      ...education,
      institution: msgTranslate(
        education.comm_Institution?.ar_Name,
        education.comm_Institution?.en_Name
      ),
      degree: msgTranslate(
        education.comm_Education_Degree?.ar_Name,
        education.comm_Education_Degree?.en_Name
      ),
      grade: msgTranslate(
        education.comm_Grade_Type?.ar_Name,
        education.comm_Grade_Type?.en_Name
      ),
      gpaGrade:
        `${education.gpa ?? '-'} - ` +
        msgTranslate(
          education.comm_Grade_Type?.ar_Name,
          education.comm_Grade_Type?.en_Name
        ),
    };
  });
}

export function empEducationYearMap(data: string[]): Idropdown[] {
  return data.map((year) => {
    return <Idropdown>{
      id: year,
      ar_Name: year,
      en_Name: year,
    };
  });
}
