import { Component, Input, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IEducationDegreeModel } from "app/system-setup/hr-setup/models/education-degree";
import { EducationDegreeService } from "app/system-setup/hr-setup/services/education-degree.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-education-degree-dropdown",
  templateUrl: "./education-degree-dropdown.component.html",
  styleUrls: ["./education-degree-dropdown.component.scss"],
})
export class EducationDegreeDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "degree";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IEducationDegreeModel[]>();

  degreeList: IEducationDegreeModel[];

  constructor(private educationDegreeService: EducationDegreeService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllDegrees();
  }

  getAllDegrees() {
    this.educationDegreeService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEducationDegreeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.degreeList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(values: IEducationDegreeModel[]) {
    this.change.emit(values);
  }
}
