import {
  ILocViolationModel,
  ILocViolationResponse,
} from "../models/localization-violations";
import { msgTranslate } from "app/shared/resources/helpers-functions";

export function locViolationToMap(violations: ILocViolationResponse[]): ILocViolationModel[] {
  return violations.map((violation) => {
    return <ILocViolationModel>{
      ...violation.loc_Violation,
      variableCode: violation.loc_Variable?.code,
      violationType: msgTranslate(violation.loc_Violation?.loc_Violation_Type?.name),
      typeName: msgTranslate(violation.loc_Violation?.loc_Type?.en_Name),
      display_In_Result: violation.loc_Variable?.display_In_Result,
      //   moneyOrPenalty: violation.loc_Violation.ismoney ? "money" : "penalty"
    };
  });
}
