import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { IJobFamilyModel, IJobFamilyRequest } from "app/system-setup/org-management-setup/models/job-families";
import { JobFamiliesService } from "app/system-setup/org-management-setup/services/job-families.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-job-families",
  templateUrl: "./job-families.component.html",
  styleUrls: ["./job-families.component.scss"],
})
export class JobFamiliesComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  mandatoryColomns: string[] = ["Ser", "id", "en_Name", "ar_Name", "actions"];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IJobFamilyModel>;

  constructor(
    public jobFamiliesService: JobFamiliesService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<JobFamiliesComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      ar_name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_name: [null, [Validators.required, CustomValidator.whiteSpace]],
    });
    if (this.data.value) {
      this.formCtrls.ar_name.setValue(this.data.value);
      this.formCtrls.en_name.setValue(this.data.value);
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    if (!this.data.isDialog) {
      this.jobFamiliesService.getList(params)
        .pipe(this.cancelRequest())
        .subscribe((res: IResponseList<IJobFamilyModel[]>) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.dataSource = new MatTableDataSource(res.data);
            this.isLoaded = true;
            this.page.length = res.meta.totalItemCount;
            this.page.meta = res.meta;
          }
          else this.errorList(res?.message?.messages);
        });
    }
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IJobFamilyRequest = {
        ID: this.form.value.id,
        Ar_Name: this.form.value.ar_name,
        En_Name: this.form.value.en_name,
      };
      model.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IJobFamilyRequest) {
    this.showLoading();
    this.jobFamiliesService.create(model).subscribe((res: IResponse<IJobFamilyModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.clearForm();
        this.getList(this.lastPage, this.page.pageSize);
        this.addSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.data.isDialog) this.dialogRef.close();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  update(model: IJobFamilyRequest) {
    this.showLoading();
    this.jobFamiliesService.update(model).subscribe((res: IResponse<IJobFamilyModel>) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.clearForm();
        this.getList(this.currentPage, this.page.pageSize); // current page after update
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  jobFamilyToEdit(jobFamily: IJobFamilyModel) {
    this.form.patchValue(jobFamily);
    this.formCtrls.ar_name.setValue(jobFamily.ar_Name);
    this.formCtrls.en_name.setValue(jobFamily.en_Name);
    // console.log(this.form.value);
  }

  onDelete(jobFamily: IJobFamilyModel): void {
    let msg = this.msgTranslate(jobFamily.ar_Name, jobFamily.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(jobFamily);
    });
  }

  delete(jobFamily: IJobFamilyModel) {
    this.showLoading();
    this.jobFamiliesService.Delete(jobFamily.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.pageSize);
        }
        if (jobFamily.id == this.formCtrls.id.value) this.clearForm();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

  onCloseDialog() {
    this.dialogRef.close();
  }
}
