import { Component, EventEmitter, Input, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { ContractTypeComponent } from "app/system-setup/hr-setup/components/contract-type/contract-type.component";
import { IContractTypeModel } from "app/system-setup/hr-setup/models/contract-type";
import { ContractTypeService } from "app/system-setup/hr-setup/services/contract-type.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-contract-type-dropdown",
  templateUrl: "./contract-type-dropdown.component.html",
  styleUrls: ["./contract-type-dropdown.component.scss"],
})
export class ContractTypeDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "contractType";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IContractTypeModel[]>();

  contractTypesList: IContractTypeModel[];
  viewName: string;

  constructor(private contractService: ContractTypeService) {
    super();
    this.viewName = ViewsManager.contract_type;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllContractType();
  }

  getAllContractType() {
    this.contractService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IContractTypeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.contractTypesList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(values: IContractTypeModel[]) {
    this.change.emit(values);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(ContractTypeComponent, value)
      .afterClosed()
      .subscribe(() => {
        this.getAllContractType();
      });
  }
}
