import { EmployeeHiringDocumentService } from "app/personnel-setup/services/employees/employement-information/employee-hiring-document.service";
import { Component, Input, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";
import { IAttachmentTypesModel } from "app/system-setup/general-setup/models/attachment-types";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { HireDocumentsComponent } from "app/system-setup/hr-setup/components/documents/hire-documents/hire-documents.component";

@Component({
  selector: "app-attachment-types-by-employee-id-dropdown",
  templateUrl: "./attachment-types-by-employee-id-dropdown.component.html",
  styleUrls: ["./attachment-types-by-employee-id-dropdown.component.scss"],
})
export class AttachmentTypesByEmployeeIdDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() employeeInstanceId?: number;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "documentName";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IAttachmentTypesModel[]>();
  viewName: string;

  hiringDocumenttList: IAttachmentTypesModel[];

  constructor(
    private employeeHiringDocumentService: EmployeeHiringDocumentService
  ) {
    super();
    this.viewName = ViewsManager.hire_documents;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.employeeInstanceId > 0 && this.checkNoBusinessOrCompany())
      this.getAllHiringDocument();
  }

  getAllHiringDocument() {
    this.employeeHiringDocumentService
      .getAllAttachment(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IAttachmentTypesModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.hiringDocumenttList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  onChange(values: IAttachmentTypesModel[]) {
    this.change.emit(values);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(HireDocumentsComponent, value, 8)
      .afterClosed()
      .subscribe((res) => {
        this.getAllHiringDocument();
      });
  }
}
