import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { AddSystemInstanceComponent } from "app/system-setup/sap-business-one-setup/components/system-instance/add-system-instance/add-system-instance.component";
import { ISapSystemInstanceModel } from "app/system-setup/sap-business-one-setup/models/system-instance";
import { SystemInstanceService } from "app/system-setup/sap-business-one-setup/services/system-instance.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
	selector: "app-sap-system-instance-dropdown",
	templateUrl: "./sap-system-instance-dropdown.component.html",
	styleUrls: ["./sap-system-instance-dropdown.component.scss"],
})
export class SapSystemInstanceDropdownComponent extends BaseDropdown implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "sapSystemInstance";
	@Input() appendTo: string | HTMLElement = "body";
	viewName: string;

	systemInstanceList: ISapSystemInstanceModel[];

	constructor(private systemInstanceService: SystemInstanceService) {
		super();
		this.viewName = ViewsManager.add_system_instance;
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getAllSystemInstances();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllSystemInstances();
	}

	getAllSystemInstances() {
		this.systemInstanceService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ISapSystemInstanceModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.systemInstanceList = res.data;
        } else this.errorList(res?.message?.messages);
      });
	}

	createDialog(value: string) {
		this.dialogsService
			.addFromDropdownDialog(AddSystemInstanceComponent, value)
			.afterClosed()
			.subscribe((res) => {
				this.getAllSystemInstances();
			});
	}
}
