import { Component, Inject, OnInit } from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { DomSanitizer } from "@angular/platform-browser";

@Component({
  selector: "app-dashboard-dispaly-details",
  templateUrl: "./dashboard-dispaly-details.component.html",
  styleUrls: ["./dashboard-dispaly-details.component.scss"],
})
export class DashboardDispalyDetailsComponent implements OnInit {
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private sanitizer: DomSanitizer,
    public dialogRef: MatDialogRef<DashboardDispalyDetailsComponent>
  ) {}

  ngOnInit(): void {}

  openLink(url: string) {
    return this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }

  onCloseDialog() {
    this.dialogRef.close();
  }
}
