import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IContactTypeModel, IContactTypeRequest } from "../models/contact-type";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class ContactTypeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IContactTypeRequest) {
    return this.post<IResponse<IContactTypeModel>>(
      "HRSetup/ContactType/Create",
      data
    );
  }

  update(data: IContactTypeRequest) {
    return this.post<IResponse<IContactTypeModel>>(
      "HRSetup/ContactType/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IContactTypeModel[]>>(
      "HRSetup/ContactType/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IContactTypeModel[]>>(
      "HRSetup/ContactType/GetAll"
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("HRSetup/ContactType/Delete", {
      params: { Id: id },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
