import {Component, Input, OnInit} from "@angular/core";
import {AbstractControl, FormControl} from "@angular/forms";
import {AddBankComponent} from "app/system-setup/hr-setup/components/bank/add-bank/add-bank.component";
import {IBankModel} from "app/system-setup/hr-setup/models/bank";
import {BankService} from "app/system-setup/hr-setup/services/bank/bank.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import {ViewsManager} from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-bank-dropdown",
  templateUrl: "./bank-dropdown.component.html",
  styleUrls: ["./bank-dropdown.component.scss"],
})
export class BankDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl | AbstractControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "bank";
  @Input() appendTo: string | HTMLElement = "body";
  viewName: string;
  bankList: IBankModel[];

  constructor(private bankService: BankService) {
    super();
    this.viewName = ViewsManager.add_bank;
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllBanks();
  }

  getAllBanks() {
    this.bankService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IBankModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.bankList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(AddBankComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllBanks();
      });
  }
}
