import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IPlanTrainingNeedsEmployeeDeleteParams,
  IPlanTrainingNeedsEmployeeModel,
  IPlanTrainingNeedsRequest,
  ITNAPlanSearchRequest,
} from "app/training/models/training-plan/training-needs-analysis";

@Injectable({
  providedIn: "root",
})
export class TrainingNeedsAnalysisService extends AppClientService {
  constructor() {
    super();
  }

  searchEmployees(model: ITNAPlanSearchRequest) {
    return this.post<IResponse<IPlanTrainingNeedsEmployeeModel[]>>(
      "Training/TNA/GetSearchTNAResults",
      model
    );
  }

  create(data: IPlanTrainingNeedsRequest) {
    return this.post<IResponse<IPlanTrainingNeedsEmployeeModel>>(
      "Training/TNA/Create",
      data
    );
  }

  Delete(params: IPlanTrainingNeedsEmployeeDeleteParams) {
    return this.delete<IResponse>("Training/TNA/Delete", {
      params: params,
    });
  }

  DeleteAll(planId: number) {
    return this.delete<IResponse>("Training/TNA/DeleteAll", {
      params: { plan_id: planId },
    });
  }
}
