  //     if (!Storage.getCompanyID)
  //       this.onSelectCompany(res.data[0]?.companies[0].id);
  //     this.BusinessList$.next(res.data);
  //   });
  // }

  getCompanyAndBusiness() {
    return this.get<IResponse<IBusinessEntityModel[]>>(
      "Login/AuthorizationAndLogin/GetUserUserCompanyAndBusiness",
      {},
      false
    );
  }

