import { Component, OnInit, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
  IContactCategoryDisplay,
  IContactCategoryRequest,
  IContactCategoryResponse,
} from "app/system-setup/hr-setup/models/contact-category";
import { ContactCategoryService } from "app/system-setup/hr-setup/services/contact-category.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { contactCategoryToMap } from "app/system-setup/hr-setup/mapper/contac-category";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";

@Component({
  selector: "app-contact-category",
  templateUrl: "./contact-category.component.html",
  styleUrls: ["./contact-category.component.scss"],
})
export class ContactCategoryComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  mandatoryColomns: string[] = [
    "Ser",
    "id",
    "en_Name",
    "ar_Name",
    "icon",
    "contactType",
    "actions",
  ];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IContactCategoryDisplay>;

  constructor(
    public contactCategoryService: ContactCategoryService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<ContactCategoryComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      Comm_Contact_Type_ID: [null, Validators.required],
      icon: [null],
    });

    if (this.data.value) {
      this.formCtrls.ar_Name.setValue(this.data.value);
      this.formCtrls.en_Name.setValue(this.data.value);
      this.formCtrls.Comm_Contact_Type_ID.setValue(this.data.id);
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.isLoaded = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.contactCategoryService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IContactCategoryResponse[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            contactCategoryToMap(res.data)
          );
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IContactCategoryRequest = {
        Comm_Contact_Category: {
          ID: this.form.value.id,
          Ar_Name: this.form.value.ar_Name,
          En_Name: this.form.value.en_Name,
          Icon: this.form.value.icon,
        },
        Comm_Contact_Type_ID: this.form.value.Comm_Contact_Type_ID,
      };

      model.Comm_Contact_Category.ID == 0
        ? this.create(model)
        : this.update(model);
    }
  }

  create(model: IContactCategoryRequest) {
    this.showLoading();
    this.contactCategoryService
      .create(model)
      .subscribe((res: IResponse<IContactCategoryResponse>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.firstPage, this.page.pageSize);
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.data.isDialog) this.dialogRef.close();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: IContactCategoryRequest) {
    this.showLoading();
    this.contactCategoryService
      .update(model)
      .subscribe((res: IResponse<IContactCategoryResponse>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getList(this.currentPage, this.page.pageSize); // current page after update
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  contactCategoryToEdit(category: IContactCategoryDisplay) {
    this.form.patchValue(category);
    //this.formCtrls.Comm_Contact_Type_ID.setValue(contractType.comm_Contact_Type_Category[0].id)
  }

  onDelete(category: IContactCategoryDisplay): void {
    let msg = this.msgTranslate(category.ar_Name, category.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(category);
    });
  }

  delete(category: IContactCategoryDisplay) {
    this.showLoading();
    this.contactCategoryService
      .Delete(category.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.dataSource.data.length == 1 && this.currentPage > 1) {
            // if last item in list: get previous page
            this.getList(this.prevPage, this.page.pageSize);
          }
          else {
            // if not last item in list: get current page
            this.getList(this.currentPage, this.page.pageSize);
          }
          if (category.id == this.formCtrls.id.value) this.clearForm();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

  onCloseDialog() {
    this.dialogRef.close();
  }


  iconsList = [
    {id: "fab fa-linkedin info", name: "linkedin"},
    {id: "fab fa-github black", name: "github"},
    {id: "fab fa-facebook-square info", name: "facebook"},
    {id: "fab fa-twitter-square info", name: "twitter"},
  ];
}
