import { msgTranslate } from "app/shared/resources/helpers-functions";
import {
  IAssignJobToOrgModel,
  IJobCareerPathModel,
  IJobCompetenceModel,
  IJobKpiModel,
  IJobTitleModel,
  IJobTrainingPathModel,
} from "../model/job-title";

export function jobCareerToMap(
  jobCareers: IJobCareerPathModel[]
): IJobCareerPathModel[] {
  return jobCareers.map((jobCareer) => {
    return <IJobCareerPathModel>{
      ...jobCareer,
      nextJobTitle: msgTranslate(
        jobCareer.next_Job_Title?.ar_Name,
        jobCareer.next_Job_Title?.en_Name
      ),
    };
  });
}

export function jobCompetenceToMap(
  jobCompetences: IJobCompetenceModel[]
): IJobCompetenceModel[] {
  return jobCompetences.map((jobCompetence) => {
    return <IJobCompetenceModel>{
      ...jobCompetence,
      competence: msgTranslate(
        jobCompetence.core_Competence_Detail?.core_Competence?.ar_Name,
        jobCompetence.core_Competence_Detail?.core_Competence?.en_Name
      ),
      competenceLevel: msgTranslate(
        jobCompetence.core_Competence_Detail?.core_Competence_Level?.ar_Name,
        jobCompetence.core_Competence_Detail?.core_Competence_Level?.en_Name
      ),
      competenceType: msgTranslate(
        jobCompetence.core_Competence_TYPE?.ar_NAme,
        jobCompetence.core_Competence_TYPE?.en_Name
      ),
    };
  });
}

export function jobTrainingToMap(
  jobTrainings: IJobTrainingPathModel[]
): IJobTrainingPathModel[] {
  return jobTrainings.map((jobTraining) => {
    return <IJobTrainingPathModel>{
      ...jobTraining,
      course: msgTranslate(
        jobTraining.trA_Course?.ar_Name,
        jobTraining.trA_Course?.en_Name
      ),
      priority: msgTranslate(
        jobTraining.comm_Priority?.ar_Name,
        jobTraining.comm_Priority?.en_Name
      ),
    };
  });
}

export function jobToMap(jobs: IJobTitleModel[]): IJobTitleModel[] {
  return jobs.map((job) => {
    return <IJobTitleModel>{
      ...job,
      jobFamily: msgTranslate(
        job.core_Job_Title_Family?.ar_Name,
        job.core_Job_Title_Family?.en_Name
      ),
      parentJobTitleName: msgTranslate(
        job.parentJobTitle?.ar_Name,
        job.parentJobTitle?.en_Name
      ),
    };
  });
}

export function assignJobToOrgToMap(
  assignJobToOrgs: IAssignJobToOrgModel[]
): IAssignJobToOrgModel[] {
  return assignJobToOrgs.map((assignJobToOrg) => {
    return <IAssignJobToOrgModel>{
      ...assignJobToOrg,
      jobTitle: msgTranslate(
        assignJobToOrg.core_Job_Title?.ar_Name,
        assignJobToOrg.core_Job_Title?.en_Name
      ),
      manPowerCapacity:
        assignJobToOrg.max_Employee != 0
          ? assignJobToOrg.max_Employee
          : assignJobToOrg.manPowerCapacity,
    };
  });
}

export function jobKpiToMap(data: IJobKpiModel[]): IJobKpiModel[] {
  return data.map((ele) => {
    return <IJobKpiModel>{
      ...ele,
      kpi: msgTranslate(ele.core_KPI?.ar_Name, ele.core_KPI?.en_Name),
    };
  });
}
