import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IPaymentModel } from "app/payroll/models/payment";
import { PaymentService } from "app/payroll/services/payment.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
	selector: "app-payment-dropdown",
	templateUrl: "./payment-dropdown.component.html",
	styleUrls: ["./payment-dropdown.component.scss"],
})
export class PaymentDropdownComponent extends BaseDropdown implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "payment";

	paymentList: IPaymentModel[];

	constructor(private paymentService: PaymentService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getAllPayments();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllPayments();
	}

	getAllPayments() {
		this.paymentService
      .GetAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPaymentModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.paymentList = res.data;
        } else this.errorList(res?.message?.messages);
      });
	}
}
