import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { PropertiesService } from "app/system-setup/dashboard-setup/services/properties.service";
import { IPropertyModel } from "app/system-setup/dashboard-setup/models/properties";
import { PropertiesComponent } from "app/system-setup/dashboard-setup/components/properties/properties.component";

@Component({
  selector: "app-properties-dropdown",
  templateUrl: "./properties-dropdown.component.html",
  styleUrls: ["./properties-dropdown.component.scss"],
})
export class PropertiesDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "properties";
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<IPropertyModel[]>();
  viewName: string;
  propertyList: IPropertyModel[];

  constructor(private propertiesService: PropertiesService) {
    super();
    this.viewName = ViewsManager.properties;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.propertiesService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPropertyModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          // ar_Name not required
          res.data.map((ele) => {
            if (!ele.ar_Name) ele.ar_Name = ele.en_Name;
          });
          this.propertyList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(values: IPropertyModel[]) {
    this.change.emit(values);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(PropertiesComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }
}
