import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { CandidateService } from "app/recruitment/services/candidate/candidate.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-sourcing-type-dropdown",
  templateUrl: "./sourcing-type-dropdown.component.html",
  styleUrls: ["./sourcing-type-dropdown.component.scss"],
})
export class SourcingTypeDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() label: string = "sourcingType";
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<Idropdown[]>();

  sourcingTypeList: Idropdown[];

  constructor(private candidateService: CandidateService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.candidateService
      .getAllSourcingType()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<Idropdown[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.sourcingTypeList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(sourcingType: Idropdown[]) {
    this.change.emit(sourcingType);
  }
}
