import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IResponse } from "app/shared/interfaces/responces";
import { IJobFamilyModel } from "app/system-setup/org-management-setup/models/job-families";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { JobFamiliesService } from "app/system-setup/org-management-setup/services/job-families.service";
import { JobFamiliesComponent } from "app/system-setup/org-management-setup/components/job-families/job-families.component";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-job-family-dropdown",
  templateUrl: "./job-family-dropdown.component.html",
  styleUrls: ["./job-family-dropdown.component.scss"]
})
export class JobFamilyDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = true;
  @Input() disable: boolean = false;
  @Input() label: string = "jobFamily";
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<IJobFamilyModel[]>();

  viewName: string;
  jobFamilyList: IJobFamilyModel[] = [];

  constructor(private jobFamiliesService: JobFamiliesService) {
    super();
  }

  ngOnInit(): void {
  }

  override onModulesChanged() {
    this.viewName = ViewsManager.job_families;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.jobFamiliesService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IJobFamilyModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.jobFamilyList = res.data;
        }
        else this.errorList(res?.message?.messages);
      });
  }


  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(JobFamiliesComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }


  onChange(values: IJobFamilyModel[]) {
    this.change.emit(values);
  }

}
