import { inject, Injectable } from "@angular/core";
import { IGUIModel, IRowActionsModel, ISubRowActionsModel, IUM_ModulesModel, IUM_ServicesModel, } from "app/shared/interfaces/user-views";
import { AppClientService } from "app/shared/services/app-client.service";
import { msgTranslate } from "app/shared/resources/helpers-functions";
import { BehaviorSubject } from "rxjs";
import { map } from "rxjs/operators";
import { BreadcrumbService } from "xng-breadcrumb";
import { ParamsService } from "app/shared/services/params.service";
import { IResponse } from "app/shared/interfaces/responces";

@Injectable({
  providedIn: "root",
})
export class UserViewsService extends AppClientService {
  menuItems$ = new BehaviorSubject<IUM_ServicesModel[] | IUM_ModulesModel[]>([]);
  UserModules$ = new BehaviorSubject<IUM_ModulesModel[]>(null);
  UserModules: IUM_ModulesModel[];
  // selectedModuleId$ = new BehaviorSubject<number>(Storage.getSelectedModule);
  paramsService = inject(ParamsService);
  moduleId: number;
  parentModuleId: number;

  constructor(private breadcrumbService: BreadcrumbService) {
    super();
    this.moduleId = this.paramsService.moduleId$.getValue();
    this.parentModuleId = this.paramsService.parentModuleId$.getValue();

    this.paramsService.moduleId$.subscribe((val) => {
      this.moduleId = val;
    });
    this.paramsService.parentModuleId$.subscribe((val) => {
      this.parentModuleId = val;
    });
  }

  getUserModules() {
    return this.get<IResponse<IUM_ModulesModel[]>>("Login/UserAuthorizatin/GetUserModules");
  }

  getUserModulesMaped() {
    this.menuItems$.next([]);
    return this.getUserModules().pipe(
      map((res: IResponse<IUM_ModulesModel[]>) => {
        res.data.sort((a, b) => a.order_No - b.order_No);
        res?.data.map((module: IUM_ModulesModel) => {
          module.url = module.path;
          module.displayName = msgTranslate(module.arbName, module.engName);
          module.uM_Services.sort((a, b) => a.order_No - b.order_No);
          delete module.path;
          if (module.children) {
            module.children.map((child: IUM_ModulesModel) => {
              child.url = child.path;
              child.parantId = module.id;
              child.displayName = msgTranslate(child.arbName, child.engName);
              delete child.path;
              child.isExternalLink = false;
              child.icon = child.imageClass;
              child.uM_Services.sort((a, b) => a.order_No - b.order_No);
              child.uM_Services.map((service: IUM_ServicesModel) => {
                return this.serviceMap(child, service);
              });
              child.class = "dropdown has-sub";
              child.submenu = child.uM_Services;
            });
          }
          else {
            module?.uM_Services.map((service: IUM_ServicesModel) => {
              return this.serviceMap(module, service);
            });
          }
          module.submenu = module.uM_Services;
          module.icon = module.imageClass;
          delete module.imageClass;
          // mod.path = `${mod.path}/${mod.id}`;
        });
        this.UserModules = res.data;
        this.UserModules$.next(res.data);

        // console.log("modules", this.modules);
        //console.log("UserModules", this.UserModules);

        return res;
      })
    )
      .subscribe();
  }

  serviceMap(module: IUM_ModulesModel, service: IUM_ServicesModel) {
    let rowFunctions = {} as IRowActionsModel; // (first level row functions)
    let basic: IGUIModel[] = []; // basic row functions  (first level)
    let dropdown: IGUIModel[] = []; // dropdown row functions (first level)
    let subRow: ISubRowActionsModel[] = []; // (second level row functions)
    let globalFunctions: IGUIModel[] = []; // global functions (first level)
    let views: IGUIModel[] = []; // view (first level) #index page only
    let customViews: IGUIModel[] = []; // customViews is an GUI not (isGlobal or isView or isRow or have parentId) using for internal view like(tabs in "add employee")
    let customViewChildren: IGUIModel[] = []; // is an GUI not (isGlobal or isView or isRow and have parentId) using for internal views Children like(sections in tabs content in "add employee")

    // Start Set uM_GUI [globalFunctions, rowFunctions, views, subRowFunctions, isCustomView]
    service?.uM_GUI?.map((GUI: IGUIModel) => {
      GUI.icon = GUI.imageClass;
      GUI.isCustomView = !GUI.isGlobal && !GUI.isRow && !GUI.isView ? true : false;
      if (GUI?.path != null) {
        GUI.path = `/${module.url}/${module.id}/${GUI?.path}`;
      }
      delete GUI.imageClass;

      // push globals
      if (GUI.isGlobal) {
        globalFunctions.push(GUI);
      }

      // push views
      if (GUI.isView) {
        views.push(GUI);
      }

      // push Custom Views (using as view but not display in sidebar almost used for TABS)
      if (GUI.isCustomView && !GUI.parantId) {
        customViews.push(GUI);
      }

      if (GUI.isCustomView && GUI.parantId) {
        customViewChildren.push(GUI);
      }

      // push row function (first level view)
      if (GUI.isRow && GUI.parantId === null) {
        if (!GUI.isDropdown) {
          basic.push(GUI);
        }
        else {
          dropdown.push(GUI);
        }
        rowFunctions.basic = basic;
        rowFunctions.dropdown = dropdown;
      }

      // push sub row function (second level view)
      if (GUI.isRow && GUI.parantId !== null) {
        let subRowItem = {} as ISubRowActionsModel;
        let subRowList = {} as IRowActionsModel;
        let subRowBasic: IGUIModel[] = []; // Edit && Delete sub row functions
        let subRowDropdown: IGUIModel[] = []; // dropdown sub row functions

        let parentRowId = service.uM_GUI.findIndex((e) => e.id == GUI.parantId);

        subRowItem.viewName = service.uM_GUI[parentRowId]?.viewName;
        subRowItem.parantId = GUI.parantId;
        if (!GUI.isDropdown) {
          subRowBasic.push(GUI);
        }
        else {
          subRowDropdown.push(GUI);
        }

        subRowList.basic = subRowBasic;
        subRowList.dropdown = subRowDropdown;
        subRowItem.row = subRowList;
        subRow.push(subRowItem);
      }

      this.breadcrumbService?.set(GUI.name, {
        label: GUI.name,
        disable: true,
      });
    });

    // Set service local data
    service.displayName = msgTranslate(service.arbName, service.engName);
    service.icon = service.imageClass;
    delete service.imageClass;
    service.class = "dropdown nav-item";
    service.isExternalLink = false;
    service.submenu = [];
    service.rows = rowFunctions;
    service.global = globalFunctions;
    service.customViews = customViews;
    service.customViewChildren = customViewChildren;
    service.views = views;
    service.subRow = subRow; //  this.groupByService.groupBy(subRow, 'viewName');
    // delete service.uM_GUI;
    //service.path = `${module.path}/${module.id}/${service?.views[0]?.path}`;

    // first view is default
    service.path = service?.views[0]?.path;
    service.viewName = service?.views[0]?.viewName;
    service.icon = service?.views[0]?.icon;

    return service;
  }

  selectedModuleIndex(moduleId: number): number {
    return this.UserModules?.findIndex((e: IUM_ModulesModel) => {
      return e.id == moduleId;
      // return e.children
      //   ? e.children.find((child) => child.id == moduleId)
      //   : e.id == moduleId;
    });
  }

  getModuleServices(moduleId: number) {
    this.menuItems$.next([]);
    let i = this.selectedModuleIndex(moduleId);

    if (i >= 0) {
      this.menuItems$.next(
        this.UserModules[i].children
          ? // second level menu
          this.UserModules[i].children
          : // first level menu
          this.UserModules[i].uM_Services
      );
    }
  }

  getRowFunctions(viewName: string): IRowActionsModel {
    if (viewName != null && this.UserModules?.length > 0) {
      // find selected module data
      let modI = this.selectedModuleIndex(this.moduleId);
      let pModI = this.selectedModuleIndex(this.parentModuleId);

      let uM_Services: IUM_ServicesModel[] = null;
      if (this.UserModules[pModI]?.children) {
        let subI = this.UserModules[pModI].children.findIndex(
          (child: IUM_ModulesModel) => child.id == this.moduleId
        );

        uM_Services = this.UserModules[pModI]?.children[subI]?.uM_Services;
      }
      else {
        uM_Services = this.UserModules[modI]?.uM_Services;
      }

      // find selected module services (current view)
      let serI = uM_Services?.findIndex((e) => e.viewName == viewName) ?? null;

      // find current view global Functions
      let row: IRowActionsModel;
      if (serI != null) {
        row = uM_Services[serI]?.rows ?? null;
      }

      return row;
    }
  }

  getSubRowFunctions(parentView: string, viewName: string): IRowActionsModel {
    // parentView means (Service viewName)
    // viewName means (GUI viewName)
    if (parentView != null && this.UserModules?.length > 0) {
      // find selected module data
      let modI = this.selectedModuleIndex(this.moduleId);
      let pModI = this.selectedModuleIndex(this.parentModuleId);

      let uM_Services: IUM_ServicesModel[] = null;
      if (this.UserModules[pModI]?.children) {
        let subI = this.UserModules[pModI].children.findIndex(
          (child: IUM_ModulesModel) => child.id == this.moduleId
        );
        uM_Services = this.UserModules[pModI]?.children[subI]?.uM_Services;
      }
      else {
        uM_Services = this.UserModules[modI]?.uM_Services;
      }

      // find selected module services (current view)
      let serI = uM_Services?.findIndex((e) => e.viewName == parentView) ?? null;

      let list: IRowActionsModel = {
        basic: [],
        dropdown: [],
      };
      if (serI != null) {
        uM_Services[serI]?.subRow.forEach((e) => {
          if (e.viewName === viewName) {
            if (e.row.basic.length > 0) {
              list.basic.push(e.row.basic[0]);
            }
            if (e.row.dropdown.length > 0) {
              list.dropdown.push(e.row.dropdown[0]);
            }
          }
        });
      }
      return list;
    }
  }

  getGlobalFunctions(viewName: string): IGUIModel[] {
    if (viewName != null && this.UserModules?.length > 0) {
      // find selected module data
      let modI = this.selectedModuleIndex(this.moduleId);
      let pModI = this.selectedModuleIndex(this.parentModuleId);

      let uM_Services: IUM_ServicesModel[] = null;
      if (this.UserModules[pModI]?.children) {
        let subI = this.UserModules[pModI]?.children.findIndex(
          (child: IUM_ModulesModel) => child.id == this.moduleId
        );
        uM_Services = this.UserModules[pModI]?.children[subI]?.uM_Services;
      }
      else {
        uM_Services = this.UserModules[modI]?.uM_Services;
      }

      // find selected module services (current view)
      let serI = uM_Services?.findIndex((e) => e.viewName == viewName) ?? null;

      // find current view global Functions
      let global: IGUIModel[];
      if (serI != null) {
        global = uM_Services[serI]?.global?.reverse() ?? null;
      }

      return global ?? [];
    }
  }

  getCustomViews(viewName: string): IGUIModel[] {
    if (viewName != null && this.UserModules?.length > 0) {
      // find selected module data
      let modI = this.selectedModuleIndex(this.moduleId);
      let pModI = this.selectedModuleIndex(this.parentModuleId);

      let uM_Services: IUM_ServicesModel[] = null;
      if (this.UserModules[pModI]?.children) {
        let subI = this.UserModules[pModI]?.children.findIndex(
          (child: IUM_ModulesModel) => child.id == this.moduleId
        );
        uM_Services = this.UserModules[pModI]?.children[subI]?.uM_Services;
      }
      else {
        uM_Services = this.UserModules[modI]?.uM_Services;
      }

      // find selected module services (current view)
      let serI = uM_Services?.findIndex((e) => e.viewName == viewName) ?? null;

      // find current view global Functions
      let Views: IGUIModel[];
      if (serI != null) {
        Views = uM_Services[serI]?.customViews ?? null;
      }
      return Views ?? [];
    }
  }

  getCustomViewChildren(parentView: string, viewName: string): IGUIModel[] {
    if (this.UserModules?.length > 0) {
      // find selected module data
      let modI = this.selectedModuleIndex(this.moduleId);
      let pModI = this.selectedModuleIndex(this.parentModuleId);

      let uM_Services: IUM_ServicesModel[] = null;
      if (this.UserModules[pModI]?.children) {
        let subI = this.UserModules[pModI]?.children.findIndex(
          (child: IUM_ModulesModel) => child.id == this.moduleId
        );
        uM_Services = this.UserModules[pModI]?.children[subI]?.uM_Services;
      }
      else {
        uM_Services = this.UserModules[modI]?.uM_Services;
      }

      // find selected module services (current view)
      let serI = uM_Services?.findIndex((e) => e.viewName == parentView) ?? null;
      let parentId = uM_Services[serI]?.uM_GUI?.find((gui) => gui.viewName == viewName).id;

      // find current view global Functions
      let ViewChildren: IGUIModel[];
      ViewChildren =
        uM_Services[serI]?.customViewChildren.filter((gui) => gui.parantId === parentId) ?? [];
      return ViewChildren;
    }

  }

  isViewAllowed(viewNamm: string): boolean {
    let isAllow = false;
    this.UserModules?.forEach((module) => {
      if (module.children?.length > 0) {
        module.children?.forEach((child) => {
          child.uM_Services?.forEach((service) => {
            service?.uM_GUI?.forEach((GUI) => {
              if (GUI.viewName === viewNamm) {
                isAllow = true;
              }
            });
          });
        });
      }
      else {
        module.uM_Services?.forEach((service) => {
          service?.uM_GUI?.forEach((GUI) => {
            if (GUI.viewName === viewNamm) {
              isAllow = true;
            }
          });
        });
      }
    });

    return isAllow;
  }


  getViewPath(viewNamm: string): string {
    let path: string;
    this.UserModules?.forEach((module) => {
      if (module.children?.length > 0) {
        module.children?.forEach((child) => {
          child.uM_Services?.forEach((service) => {
            service?.uM_GUI?.forEach((GUI) => {
              if (GUI.viewName === viewNamm) {
                path = GUI.path;
              }
            });
          });
        });
      }
      else {
        module.uM_Services?.forEach((service) => {
          service?.uM_GUI?.forEach((GUI) => {
            if (GUI.viewName === viewNamm) {
              path = GUI.path;
            }
          });
        });
      }
    });

    return path;
  }


  getServiceId(viewName: string): number {
    if (viewName != null && this.UserModules?.length > 0) {
      // find selected module data
      let modI = this.selectedModuleIndex(this.moduleId);
      let pModI = this.selectedModuleIndex(this.parentModuleId);

      let servicesId: number;
      if (this.UserModules[pModI]?.children) {
        let subI = this.UserModules[pModI]?.children.findIndex(
          (child: IUM_ModulesModel) => child.id == this.moduleId
        );

        servicesId = this.UserModules[pModI]?.children[subI]?.uM_Services.find(e => e.viewName === viewName)?.id;
      }
      else {
        servicesId = this.UserModules[modI]?.uM_Services.find(e => e.viewName === viewName)?.id;
      }
      return servicesId;
    }
  }


  getRowFunByViewname(viewName: string): IRowActionsModel {
    let row: IRowActionsModel;
    this.UserModules?.forEach((module) => {
      if (module.children?.length > 0) {
        module.children?.forEach((child) => {
          let data = child.uM_Services.filter(e => e.viewName == viewName)[0]?.rows;
          if (data) row = child.uM_Services.filter(e => e.viewName == viewName)[0]?.rows;
        });
      }
      else {
        let data = module.uM_Services.filter(e => e.viewName == viewName)[0]?.rows;
        if (data) row = module.uM_Services.filter(e => e.viewName == viewName)[0]?.rows;
      }
    });
    return row;
  }
}
