import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IRequestReasonModel } from "app/recruitment/models/request-reason";
import { RequestReasonService } from "app/recruitment/services/request-reason.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-request-reason-dropdown",
  templateUrl: "./request-reason-dropdown.component.html",
  styleUrls: ["./request-reason-dropdown.component.scss"],
})
export class RequestReasonDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() label: string = "requestReason";
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<IRequestReasonModel[]>();

  requestReasonList: IRequestReasonModel[];

  constructor(private requestReasonService: RequestReasonService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.requestReasonService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IRequestReasonModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.requestReasonList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(requestReason: IRequestReasonModel[]) {
    this.change.emit(requestReason);
  }
}
