import { db } from "app/shared/resources/database-tabels";
import { Injectable } from "@angular/core";
import {
  IEditEmployeeEducationRequest,
  IEmployeeEducationDeleteParams,
  IEmployeeEducationModel,
  IEmployeeEducationRequest,
} from "app/personnel-setup/models/employee-career/emp-education";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeEducationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeEducationRequest) {
    return this.post<IResponse<IEmployeeEducationModel>>(
      "Personnel/Employees/Create_EmployeeEducation",
      data
    );
  }

  update(data: IEditEmployeeEducationRequest) {
    return this.post<IResponse<IEmployeeEducationModel>>(
      "Personnel/Employees/Edit_EmployeeEducation",
      data
    );
  }

  getAll(employeeId: number) {
    return this.get<IResponse<IEmployeeEducationModel[]>>(
      "Personnel/Employees/GetAll_EmployeeEducation",
      {
        params: {
          $filter: `PersonID eq ${employeeId}`,
          $expand: `${db.comm_Education_Degree},${db.comm_Institution},${db.comm_Grade_Type}`,
        },
      }
    );
  }

  getList(employeeId: number, params: IPageParams) {
    return this.get<IResponse<IEmployeeEducationModel[]>>(
      "Personnel/Employees/GetList_EmployeeEducation",
      {
        params: {
          ...params,
          $filter: `PersonID eq ${employeeId}`,
          $expand: `${db.comm_Education_Degree},${db.comm_Institution},${db.comm_Grade_Type}`,
        },
      }
    );
  }

  Delete(params: IEmployeeEducationDeleteParams) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeEducation",
      {
        params: params,
      }
    );
  }

  getAllYears() {
    return this.get<IResponse<string[]>>("Personnel/Employees/GetYears");
  }
}
