import { Component, Input, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IdentityTypeComponent } from "app/system-setup/hr-setup/components/identity-type/identity-type.component";
import { IIdentityTypeModel } from "app/system-setup/hr-setup/models/identity-type";
import { IdentityTypeService } from "app/system-setup/hr-setup/services/identity-type.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-identity-type-dropdown",
  templateUrl: "./identity-type-dropdown.component.html",
  styleUrls: ["./identity-type-dropdown.component.scss"],
})
export class IdentityTypeDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "identityType";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IIdentityTypeModel[]>();

  identityTypetList: IIdentityTypeModel[];
  viewName: string;

  constructor(private identityTypeService: IdentityTypeService) {
    super();
    this.viewName = ViewsManager.identity_type;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllIdentityType();
  }

  getAllIdentityType() {
    this.identityTypeService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IIdentityTypeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.identityTypetList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(IdentityTypeComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllIdentityType();
      });
  }

  onChange(values: IIdentityTypeModel[]) {
    this.change.emit(values);
  }
}
