import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { FormControl } from "@angular/forms";
import { ILocPenaltyModel } from "app/system-setup/localization-setup/models/localization-penalties";
import { LocPenaltiesService } from "app/system-setup/localization-setup/services/localization-penalties.service";
import { ViewsManager } from "app/shared/resources/views-manager";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { LocPenaltiesComponent } from "app/system-setup/localization-setup/components/localization-penalties/loc-penalties.component";

@Component({
  selector: "loc-penalties-dropdown",
  templateUrl: "./penalties-dropdown.component.html",
  styleUrls: ["./penalties-dropdown.component.scss"]
})
export class PenaltiesDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = true;
  @Input() label: string = "penalty";
  @Input() appendTo: string | HTMLElement | any;
  @Output() change = new EventEmitter<ILocPenaltyModel[]>();
  list: ILocPenaltyModel[];
  viewName: string;

  constructor(private penaltiesService: LocPenaltiesService) {
    super();
    this.viewName = ViewsManager.localization_penalties;
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  override onChangeVersionOrCountry() {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.penaltiesService
      .getAll()
      .subscribe((res: IResponse<ILocPenaltyModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.list = res.data;
        }
        else this.errorList(res?.message?.messages);
      });
  }


  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(LocPenaltiesComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }

  onChange(versions: ILocPenaltyModel[]) {
    this.change.emit(versions);
  }

}
