import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IPropertyModel, IPropertyRequest } from "../models/properties";

@Injectable({
  providedIn: "root",
})
export class PropertiesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IPropertyRequest) {
    return this.post<IResponse<IPropertyModel>>(
      "Dashboard/Property/Create",
      data
    );
  }

  update(data: IPropertyRequest) {
    return this.post<IResponse<IPropertyModel>>(
      "Dashboard/Property/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IPropertyModel[]>>(
      "Dashboard/Property/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponseList<IPropertyModel[]>>(
      "Dashboard/Property/GetAll"
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Dashboard/Property/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
