import { Component, Input, OnInit, SimpleChanges } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IBankBranchModel, IBankBranchRequest, IBankModel } from "app/system-setup/hr-setup/models/bank";
import { BankBranchService } from "app/system-setup/hr-setup/services/bank/bank-branch.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-bank-branch",
  templateUrl: "./bank-branch.component.html",
  styleUrls: ["./bank-branch.component.scss"],
})
export class BankBranchComponent extends AppHelpers implements OnInit {
  @Input() bankId: number;
  dataSource: MatTableDataSource<IBankBranchModel>;
  form: FormGroup;
  submitted: boolean = false;
  mandatoryColomns: string[] = [
    "Ser",
    "id",
    "en_Name",
    "ar_Name",
    "swiftCode",
    "bankCode",
    "actions",
  ];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];

  constructor(private bankBranchService: BankBranchService) {
    super();
  }

  ngOnChanges(change: SimpleChanges): void {
    if (change.bankId && this.form) this.formCtrls.bankId.setValue(this.bankId);
    if (this.checkNoBusinessOrCompany() && this.bankId > 0) {
      this.getBankBranches(this.firstPage);
    }
  }

  ngOnInit(): void {
    // if (this.businessId == 0 && this.companyId == 0) {
    //   console.log("ngOnInit");
    //   this.getBankBranches(this.currentPage);
    // }

    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      bankId: [this.bankId, {nonNullable: true}],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      swiftCode: [null],
      bankCode: [null],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany() && this.bankId > 0) this.getBankBranches(this.firstPage);
    else this.isLoaded = true;
  }

  getBankBranches(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.bankBranchService
      .getBankBranches(params, this.bankId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IBankModel[]>) => {
        //  console.log("getBranches", res);
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data[0].comm_Branch);
          this.isLoaded = true;
        }
        else {
          this.errorList(res?.message?.messages);
        }
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IBankBranchRequest = {
        ID: this.form.value.id,
        Ar_Name: this.form.value.ar_Name,
        En_Name: this.form.value.en_Name,
        BankCode: this.form.value.bankCode,
        SwiftCode: this.form.value.swiftCode,
        bankID: this.form.value.bankId,
      };
      model.ID == 0 ? this.create(model) : this.update(model);
    }
  }

  create(model: IBankBranchRequest) {
    this.showLoading();
    this.bankBranchService.create(model).subscribe((res: IResponse<IBankBranchModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.clearForm();
        this.getBankBranches(this.lastPage, this.bankId);
        this.addSuccessfullyMsg(res?.message?.messageTypeName);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  update(model: IBankBranchRequest) {
    this.showLoading();
    this.bankBranchService.update(model).subscribe((res: IResponse<IBankBranchModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.clearForm();
        this.getBankBranches(this.currentPage, this.bankId); // current page after update
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  branchToEdit(branch: IBankBranchModel) {
    this.form.patchValue(branch);
    console.log(this.form.value);
  }

  onDelete(bankBranch: IBankBranchModel): void {
    let msg = this.msgTranslate(bankBranch.ar_Name, bankBranch.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(bankBranch);
    });
  }

  delete(bank: IBankBranchModel) {
    this.showLoading();
    this.bankBranchService.Delete(bank.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          this.getBankBranches(this.prevPage, this.page.pageSize);
        }
        else {
          this.getBankBranches(this.currentPage, this.page.pageSize);
        }
        if (bank.id == this.formCtrls.id.value) this.clearForm();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({id: 0, bankId: this.bankId});
    this.submitted = false;
  }
}
