import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
} from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IRowActionsModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { ViewsManager } from "app/shared/resources/views-manager";
import {
  planCourseEmployeeToDisplay,
  planCourseToDisplay,
} from "app/training/mapper/training-plan";
import {
  IPlanCourseEmployeeDeleteParams,
  IPlanCourseEmployeesSearchParams,
  IPlanCourseModel,
  IPlanCourseRequest,
  IPlanCoursesEmployeeModel,
} from "app/training/models/training-plan/plan-courses-expenses";
import { PlanCoursesService } from "app/training/services/traning-plan/plan-courses.service";
import { PlanExpensesComponent } from "../plan-expenses/plan-expenses.component";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-plan-courses-and-expensis",
  templateUrl: "./plan-courses-and-expensis.component.html",
  styleUrls: ["./plan-courses-and-expensis.component.scss"],
})
export class PlanCoursesAndExpensisComponent
  extends AppHelpers
  implements OnInit {
  @Input() planId: number = 0;
  @Input() isView: boolean = false;
  showOnlyIndex = false;
  @Output() updateViewDisplay = new EventEmitter<boolean>();
  @Output() completed = new EventEmitter<boolean>();

  // change dropdown && employees
  @Input() updateCourseDropdown: boolean;

  @ViewChild(PlanExpensesComponent) expenseComponent: PlanExpensesComponent;

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomnsSearch: string[] = [
    // "Ser",
    "code",
    "employeeNameEn",
    "employeeNameAr",
    "job",
    "selectAll",
  ];
  isLoadedSearch: boolean = false;
  dataSourceSearch: MatTableDataSource<IPlanCoursesEmployeeModel>;

  mandatoryColomnsEmployee: string[] = [
    // "Ser",
    "code",
    "employeeNameEn",
    "employeeNameAr",
    "job",
    "actions",
  ];
  isLoadedEmployee: boolean = false;
  dataSourceEmployee: MatTableDataSource<IPlanCoursesEmployeeModel>;

  mandatoryColomnsCourses: string[] = [
    // "Ser",
    "title",
    "plan",
    "course",
    "fromDate",
    "toDate",
    "online",
    "budget",
    "actions",
  ];
  isLoadedCourses: boolean = false;
  dataSourceCourses: MatTableDataSource<IPlanCourseModel>;

  optionalColomns: string[] = [];

  showIndexAfterSubmit: boolean = false;

  rowFunctionsSelectedEmployees: IRowActionsModel;

  constructor(private planCoursesService: PlanCoursesService) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group(
      {
        id: [0, {nonNallable: true}],
        course_ID: [null, [Validators.required]],
        planCourseTitle: [null, [Validators.required]],
        from_Date: [null, [Validators.required]],
        to_Date: [null, [Validators.required]],
        instructor_ID: [, [Validators.required]],
        note: [null],
        isOnline: [true],
        tsP_ID: [null],
      },
      {
        validator: CustomValidator.compareDates("from_Date", "to_Date"),
      }
    );

    this.formCtrls.course_ID.valueChanges.subscribe((courseId) => {
      if (courseId > 0) this.searchEmployees(courseId);
    });

    if (this.isView) this.showOnlyIndex = true;
  }

  override onChangeBusinessAndCompanyId(): void {
    // if (this.dataSourceSearch?.data?.length > 0)
    //   this.dataSourceSearch = new MatTableDataSource(null);
    if (this.dataSourceCourses?.data?.length > 0)
      this.dataSourceCourses = new MatTableDataSource(null);

    if (this.checkNoBusinessOrCompany() && this.planId > 0) {
      this.getList(this.firstPage);
    }
    else {
      this.isLoadedCourses = true;
    }
  }

  override onModulesChanged(): void {
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.plan_course
    );

    this.rowFunctionsSelectedEmployees = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.plan_course_general_data
    );
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.updateCourseDropdown && this.form) {
      this.searchEmployees(this.formCtrls?.course_ID.value);
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  // //////////////// section 1 >> course & employees //////////////// //

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IPlanCourseRequest = {
        plan_Course: {
          id: this.form.value.id,
          Plan_ID: this.planId,
          Course_ID: this.form.value.course_ID,
          Name: this.form.value.planCourseTitle,
          From_Date: HelpersFunctions.mapDateReverse(this.form.value.from_Date),
          To_Date: HelpersFunctions.mapDateReverse(this.form.value.to_Date),
          TSP_ID: this.form.value.tsP_ID,
          Instructor_ID: this.form.value.instructor_ID,
          Note: this.form.value.note,
        },
        IsOnline: this.form.value.isOnline ?? false,
        SelectedEmployeeInstanceIDs: [], // get from datasource employees
      };
      if (model.IsOnline) model.plan_Course.TSP_ID = null;

      this.dataSourceSearch?.data
        ?.filter((ele) => ele.isSelected)
        .forEach((selected) => {
          model.SelectedEmployeeInstanceIDs.push(selected.person_Instance_ID);
        });

      // employees added before
      this.dataSourceEmployee?.data?.forEach((emp) => {
        model.SelectedEmployeeInstanceIDs.push(emp.person_Instance_ID);
      });

      if (model.SelectedEmployeeInstanceIDs.length == 0) {
        this.warning("selectOneEmployee");
        return;
      }

      model.plan_Course.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  // add course and employees
  create(model: IPlanCourseRequest) {
    this.showLoading("courses");
    this.planCoursesService
      .create(model)
      .subscribe((res: IResponse<IPlanCourseRequest>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.formCtrls.id.setValue(res.data.plan_Course.id);
          if (this.isView) this.getList(this.currentPage);
          // update employees tables
          this.searchEmployees(model.plan_Course.Course_ID);
          this.getSelectedEmployees(model.plan_Course.Course_ID);
          this.completed.emit(true);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("courses");
      });
  }

  update(model: IPlanCourseRequest) {
    this.showLoading("courses");
    this.planCoursesService
      .update(model)
      .subscribe((res: IResponse<IPlanCourseRequest>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          if (this.isView) this.getList(this.currentPage);
          if (
            model.SelectedEmployeeInstanceIDs.length !=
            this.dataSourceEmployee?.data?.length
          ) {
            // update employees tables
            this.searchEmployees(model.plan_Course.Course_ID);
            this.getSelectedEmployees(model.plan_Course.Course_ID);
          }
          this.completed.emit(true);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("courses");
      });
  }

  // get employees when select course
  searchEmployees(courseId: number) {
    this.dataSourceSearch = new MatTableDataSource(null);
    if (courseId > 0) {
      this.isLoadedSearch = false;
      let searchParams: IPlanCourseEmployeesSearchParams = {
        planId: this.planId,
        Course_ID: courseId,
      };
      this.planCoursesService
        .getPlanEmployees(searchParams)
        .pipe(this.cancelRequest())
        .subscribe((res: IResponse<IPlanCoursesEmployeeModel[]>) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.dataSourceSearch = new MatTableDataSource(
              planCourseEmployeeToDisplay(res.data)
            );
            this.isLoadedSearch = true;
          }
          else this.errorList(res?.message?.messages);
        });
    }
  }

  // get employees selected >> in edit course
  getSelectedEmployees(courseId: number) {
    this.dataSourceEmployee = new MatTableDataSource(null);
    if (courseId > 0) {
      this.isLoadedEmployee = false;
      let searchParams: IPlanCourseEmployeesSearchParams = {
        planId: this.planId,
        Course_ID: courseId,
      };
      this.planCoursesService
        .getSelectedEmployees(searchParams)
        .pipe(this.cancelRequest())
        .subscribe((res: IResponse<IPlanCoursesEmployeeModel[]>) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.dataSourceEmployee = new MatTableDataSource(
              planCourseEmployeeToDisplay(res.data)
            );
            this.isLoadedEmployee = true;
          }
          else this.errorList(res?.message?.messages);
        });
    }
  }

  changeIsOnline(value: boolean) {
    if (value) this.formCtrls.tsP_ID.clearValidators();
    else this.formCtrls.tsP_ID.setValidators([Validators.required]);
    this.formCtrls.tsP_ID.updateValueAndValidity();
  }

  onDeleteEmployee(employee: IPlanCoursesEmployeeModel): void {
    let msg = this.msgTranslate(
      employee.employeeNameAr,
      employee.employeeNameEn
    );
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.deleteEmployee(employee);
    });
  }

  deleteEmployee(employee: IPlanCoursesEmployeeModel) {
    let deleteParams: IPlanCourseEmployeeDeleteParams = {
      person_instance_id: employee.person_Instance_ID,
      plan_course_id: this.formCtrls.id.value,
    };
    this.showLoading("courses");
    this.planCoursesService
      .DeleteEmployee(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          // update employees tables
          this.searchEmployees(this.formCtrls.course_ID.value);
          this.getSelectedEmployees(this.formCtrls.course_ID.value);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("courses");
      });
  }

  onClearAll(): void {
    let msg = this.translate("allEmployees");
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.clearAll();
      }
    });
  }

  clearAll() {
    this.showLoading("courses");
    this.planCoursesService
      .DeleteAllEmployees(this.formCtrls.id.value)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          // update employees tables
          this.searchEmployees(this.formCtrls.course_ID.value);
          this.dataSourceEmployee = new MatTableDataSource([]);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("courses");
      });
  }

  clearForm() {
    this.form.reset({id: 0, isOnline: true});
    this.submitted = false;

    this.dataSourceEmployee = new MatTableDataSource(null);
  }

  // //////////////// section 3 > index //////////////// //

  getList(page: number, pageSize?: number) {
    this.dataSourceCourses = new MatTableDataSource(null);
    this.isLoadedCourses = false;
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.planCoursesService
      .getList(this.planId, params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IPlanCourseModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSourceCourses = new MatTableDataSource(
            planCourseToDisplay(res.data)
          );
          this.isLoadedCourses = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  courseAndExpensesToEdit(course: IPlanCourseModel) {
    this.form.patchValue(course);
    // this.formCtrls.course_ID.setValue(course.course_ID);
    this.formCtrls.planCourseTitle.setValue(course.name);
    this.formCtrls.isOnline.setValue(course.tsP_ID ? false : true);
    if (this.isView) this.showAddInsideView();
    this.getSelectedEmployees(course.course_ID);
  }

  onDelete(course: IPlanCourseModel): void {
    let msg = course.title + " - " + course.course;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(course);
    });
  }

  delete(course: IPlanCourseModel) {
    this.showLoading("courses");
    this.planCoursesService.Delete(course.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSourceCourses.data.length == 1 && this.currentPage > 1) {
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          this.getList(this.currentPage, this.page.pageSize);
        }
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading("courses");
    });
  }

  // //////////////// submit all //////////////// //

  submitAll() {
    this.getList(this.currentPage);
    if (this.isView) this.returnToView();
    else {
      this.clearForm();
      this.expenseComponent.clearForm();
      this.showIndexAfterSubmit = true;
    }
  }

  // //////////////// view //////////////// //

  showAddInsideView() {
    this.updateViewDisplay.emit(true);
    this.showOnlyIndex = false;
    window.scrollTo({top: 0, behavior: "smooth"});
  }

  returnToView() {
    this.updateViewDisplay.emit(false);
    this.showOnlyIndex = true;
    this.clearForm();
    this.expenseComponent.clearForm();
  }
}
