import { ISTPModel } from "../models/tsp";

export function tspTodisplay(TSP: ISTPModel[]): ISTPModel[] {
  return TSP.map((tsp) => {
    return <ISTPModel>{
      id: tsp.id,
      ar_Name: tsp.ar_Name,
      en_Name: tsp.en_Name,
      breif_Name: tsp.breif_Name,
      address: tsp.address,
      location: tsp.location,
      webSite_URL: tsp.webSite_URL,
      email: tsp.email,
      mobile: tsp.mobile,
      phone: tsp.phone,
      fax: tsp.fax,
      contactPersonName: tsp.contactPersonName,
      contactPersonEmail: tsp.contactPersonEmail,
      contactPersonMobile: tsp.contactPersonMobile,
      contactPersonPhone: tsp.contactPersonPhone,
      i_M_C_Code: tsp.i_M_C_Code,
      note: tsp.note,
    };
  });
}
