import { Component, EventEmitter, Inject, Input, OnInit, Output, ViewChild } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { SwalComponent } from "@sweetalert2/ngx-sweetalert2";
import {
  IEmployee,
  IEmployeeRequest,
  ISaveEmpGeneralInfo,
  ISharedCompaniesRequest,
} from "app/personnel-setup/models/employees";
import { EmpGeneralInfoService } from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import { EmployeesService } from "app/personnel-setup/services/employees/employees.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { AssetsManager } from "app/shared/resources/assets-manager";
import { Constants } from "app/shared/resources/constants";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { SweetAlertService } from "app/shared/services/sweet-alert.service";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "emp-general-info-form",
  templateUrl: "./emp-general-info-form.component.html",
  styleUrls: ["./emp-general-info-form.component.scss"],
})
export class EmpGeneralInfoFormComponent extends AppHelpers implements OnInit {
  employeeId: number = 0;
  employee: IEmployee;
  @Input() isGeneral: boolean;
  @Input() isPersonal: boolean;
  @Input() isSocialInsurance: boolean;
  @Input() isBlood_type: boolean;
  @Output() onSave = new EventEmitter<ISaveEmpGeneralInfo>();
  @ViewChild("Swal") public readonly swal!: SwalComponent;
  form: FormGroup;
  submitted: boolean = false;
  disabledIds: number[] = [];
  existingEmployee: IEmployee;
  assetsManager = new AssetsManager();

  constructor(
    private employeesService: EmployeesService,
    private empGeneralInfoService: EmpGeneralInfoService,
    private sweetService: SweetAlertService,
    @Inject(MAT_DIALOG_DATA) public dataFromDropdown: ICreateDialog,
    public dialogRef: MatDialogRef<EmpGeneralInfoFormComponent>,
  ) {
    super();
    this.employeeId = EmpGeneralInfoService.EmployeeId;
    this.employee = this.empGeneralInfoService.employee$.getValue();
    this.empGeneralInfoService.employee$.subscribe((emp) => {
      this.employee = emp;
      // Edit Mode
    });
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      code: [null, [Validators.required, CustomValidator.whiteSpace]],
      CompanyCode: [null],
      gender_ID: [null],
      external_Code: [null],
      nationalid: [
        null,
        [
          Validators.required,
          CustomValidator.whiteSpace,
          CustomValidators.number,
          CustomValidator.Length(Constants.nationalId),
        ],
      ],
      religioN_ID: [null],
      dob: [null], // isPersonal
      hireDate: [null, [Validators.required, CustomValidators.date]],
      blooD_TYPE_ID: [null], // isBlood_type
      insurance_Years: [null], // isInsurance
      insurance_No: [null], // isInsurance
      nationality: [null],
      country: [null], // isPersonal
      isShared: [false],
      isMainCompany: [false],
      companyIds: [null],
      SharedCompanies: [this.fb.array([])],
    });

    // Insurance Info section Validation
    if (this.isSocialInsurance) {
      this.formCtrls.insurance_Years.addValidators([Validators.required, CustomValidators.number]);
      this.formCtrls.insurance_No.addValidators([Validators.required]);
    }

    // Personal Info section Validation
    if (this.isPersonal) {
      this.formCtrls.dob.addValidators([Validators.required, CustomValidators.date]);
      // this.formCtrls.country.addValidators([Validators.required]);
    }

    // Blood Type section Validation
    if (this.isBlood_type) {
      this.formCtrls.blooD_TYPE_ID.addValidators([Validators.required]);
    }

    // Edit Mode
    if (this.employee?.person?.id) {
      this.employeeId = this.employee?.person?.id;
      this.employeeToEdit(this.employee);
    }

    // to change dropdown (company, business) > use dynamic
    if (this.dataFromDropdown?.value) {
      this.isGeneral = true;
      this.formCtrls.ar_Name.setValue(this.dataFromDropdown.value);
      this.formCtrls.en_Name.setValue(this.dataFromDropdown.value);
    }

  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const sharedCompanies: ISharedCompaniesRequest[] = [];
      this.formCtrls.companyIds?.value?.forEach((company) => {
        sharedCompanies.push({company_Id: company, isDefaultCompany: false});
      });
      const model: IEmployeeRequest = {
        person: {
          id: this.form.value.id,
          ar_Name: this.form.value.ar_Name,
          en_Name: this.form.value.en_Name,
          code: this.form.value.code,
          external_Code: this.form.value.external_Code,
          gender_ID: this.form.value.gender_ID,
          blooD_TYPE_ID: this.form.value.blooD_TYPE_ID,
          dob: HelpersFunctions.mapDateReverse(this.form.value.dob),
          hireDate: HelpersFunctions.mapDateReverse(this.form.value.hireDate),
          insurance_Years: this.form.value.insurance_Years,
          insurance_No: this.form.value.insurance_No,
          nationalid: this.form.value.nationalid,
          religioN_ID: this.form.value.religioN_ID,
        },
        companyCode: this.form.value.CompanyCode,
        nationality: this.form.value.nationality ? {id: this.form.value.nationality} : null,
        country: this.form.value.country ? {id: this.form.value.country} : null,
        sharedCompanies: sharedCompanies,
      };

      model.person.id == 0 ? this.create(model) : this.update(model);
    }
    // else this.toastr.error("General.formNotValid");
  }

  create(model: IEmployeeRequest) {
    this.showLoading("emp-general-info");
    this.employeesService.create(model).subscribe((res: IResponse<IEmployeeRequest>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.addSuccessfullyMsg(res?.message?.messageTypeName);
        this.employeeId = res.data.person.id;
        this.onSave.emit({
          isUpdate: false,
          data: res.data,
          isGeneral: this.isGeneral,
          isPersonal: this.isPersonal,
          isInsurance: this.isSocialInsurance,
        });
        if (this.dataFromDropdown.isDialog) this.dialogRef.close();
      }
      else {
        res?.message?.messages.forEach((msg) => {
          if (msg.code == 200) {
            // code(200) employee Exist and Resigned in anthor company
            this.getExistingEmployee(msg.refrenceID);
          }
        });
        this.errorList(res?.message?.messages);
      }
      this.hideLoading("emp-general-info");
    });
  }

  update(model: IEmployeeRequest) {
    this.showLoading("emp-general-info");
    this.employeesService.update(model).subscribe((res: IResponse<IEmployeeRequest>) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        this.empGeneralInfoService.getEmployeeShared(this.employeeId);
        this.onSave.emit({
          isUpdate: true,
          data: res.data,
          isGeneral: this.isGeneral,
          isPersonal: this.isPersonal,
          isInsurance: this.isSocialInsurance,
        });
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading("emp-general-info");
    });
  }

  employeeToEdit(employee: IEmployee) {
    this.form.patchValue(employee.person);
    this.formCtrls.nationality.setValue(employee.nationality?.id);
    this.formCtrls.country.setValue(employee.country?.id);
    this.formCtrls.CompanyCode.setValue(employee.companyCode);
    this.formCtrls.isShared.setValue(employee.isShared);
    this.formCtrls.isMainCompany.setValue(employee.isMainCompany);
    let sharedCompanies: number[] = [];
    employee.sharedCompanies.forEach((company) => {
      sharedCompanies.push(company.company_Id);
      this.formCtrls.companyIds.setValue(sharedCompanies);
      if (company.isDefaultCompany) this.disabledIds.push(company.company_Id);
    });
  }

  // if this employee Exist in anthor company and Resigned,
  getExistingEmployee(employeeId: number) {
    this.empGeneralInfoService
      .getEmployee(employeeId, {stopCompanyFilter: true})
      .subscribe((res: IResponse<IEmployee>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.existingEmployee = res.data;
          this.swal.swalOptions = this.swalOptions;
          this.swal.fire();
        }
      });
  }

  // if this employee Exist in anthor company and Resigned, return to current company
  ShareToCurrentCompany(personelId: number) {
    this.empGeneralInfoService.shareToCurrentCompany(personelId).subscribe((res) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.onSave.emit({
          isUpdate: null,
          data: null,
          isGeneral: this.isGeneral,
          isPersonal: this.isPersonal,
          isInsurance: this.isSocialInsurance,
        });
        let url = `/personnel-setup/${this.moduleId}/employees/edit/${personelId}`;
        this.router.navigate([url], {queryParams: this.parentParam});
      }
    });
  }

  swalOptions = {
    confirmButtonText: this.translate("goEmployee"),
    cancelButtonText: this.translate("General.cancel"),
    ...this.sweetService.customSwalOptions,
  };

  // clearForm() {
  // 	if (this.employeeId == 0)
  // 		this.form.reset({
  // 			id: 0,
  // 			SharedCompanies: false,
  // 			businessIds: [this.businessCompanyParams.BusinessEntityID],
  // 			companyIds: [this.businessCompanyParams.CompanyID],
  // 		});
  // 	else this.employeeToEdit();
  // 	this.submitted = false;
  // 	// console.log(this.form.value);
  // }
}
