import { Component, Input, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { empEducationYearMap } from "app/personnel-setup/mapper/emp-career";
import { EmployeeEducationService } from "app/personnel-setup/services/employees/career/employee-education.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-education-year-dropdown",
  templateUrl: "./education-year-dropdown.component.html",
  styleUrls: ["./education-year-dropdown.component.scss"],
})
export class EducationYearDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "year";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<Idropdown[]>();

  yearList: Idropdown[];

  constructor(private employeeEducationService: EmployeeEducationService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllMajors();
  }

  getAllMajors() {
    this.employeeEducationService
      .getAllYears()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<string[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.yearList = empEducationYearMap(res.data);
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(values: Idropdown[]) {
    this.change.emit(values);
  }
}
