import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import { planTnaToDisplay } from "app/training/mapper/training-plan";
import {
  IPlanTrainingNeedsEmployeeDeleteParams,
  IPlanTrainingNeedsEmployeeModel,
  IPlanTrainingNeedsRequest,
  ITNAPlanSearchRequest,
} from "app/training/models/training-plan/training-needs-analysis";
import { TrainingNeedsAnalysisService } from "app/training/services/traning-plan/training-needs-analysis.service";
import { CustomValidators } from "ngx-custom-validators";
import { Constants } from "app/shared/resources/constants";

@Component({
  selector: "app-training-needs-analysis",
  templateUrl: "./training-needs-analysis.component.html",
  styleUrls: ["./training-needs-analysis.component.scss"],
})
export class TrainingNeedsAnalysisComponent
  extends AppHelpers
  implements OnInit {
  @Input() planId: number = 0;
  @Input() isView: boolean = false;
  showOnlyIndex = false;
  @Output() updateViewDisplay = new EventEmitter<boolean>();
  @Output() updateCourseDropdown = new EventEmitter<any>();
  @Output() completed = new EventEmitter<boolean>();
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomnsSearch: string[] = [
    // "Ser",
    // "code",
    "employee",
    "department",
    "job",
    "noOfTakenCourses",
    Constants.selectColName,
  ];
  isLoadedSearch: boolean = false;
  dataSourceSearch: MatTableDataSource<IPlanTrainingNeedsEmployeeModel>;
  showSearchSection: boolean = false;

  mandatoryColomnsEmployees: string[] = [
    // "Ser",
    // "code",
    "employee",
    "department",
    "job",
    "noOfTakenCourses",
    "actions",
  ];
  isLoadedEmployees: boolean = false;
  dataSourceEmployees: MatTableDataSource<IPlanTrainingNeedsEmployeeModel>;

  optionalColomns: string[] = [];

  lastSearchModel: ITNAPlanSearchRequest;
  showIndexAfterAdd: boolean = false;

  constructor(private tNAService: TrainingNeedsAnalysisService) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      // training path
      trainingPath: [false],
      TrainhgPathPrioritySelected: [[]],
      // training requests
      trainingRequests: [false],
      reasonId: [[], [Validators.required]],
      selfServiceRequest: [true],
      // pms
      pms: [false],
      appraisalFrom: [null],
      appraisalTo: [null],
      PMS_Grade: [0, [CustomValidators.gte(0)]],
      competenceId: [[], [Validators.required]],
      // with implementation
      withImplementation: [false],
    });

    if (this.isView) {
      this.showOnlyIndex = true;
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    // if (this.dataSourceEmployees?.data?.length > 0) {
    //   this.dataSourceEmployees = new MatTableDataSource(null);
    // }

    if (this.dataSourceSearch?.data?.length > 0) {
      this.dataSourceSearch = new MatTableDataSource(null);
    }

    if (this.checkNoBusinessOrCompany() && this.planId > 0) {
      this.getAll();
    }
    else {
      this.isLoadedEmployees = true;
    }
  }

  override onModulesChanged(): void {
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.plan_tna
    );
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.dataSourceEmployees = new MatTableDataSource(null);
    this.isLoadedEmployees = false;

    let searchModel: ITNAPlanSearchRequest = {
      Plan_ID: this.planId,
      IsEditMode: true,
      TrainingPath: false,
      TrainhgPathPrioritySelected: [],
      TrainingRequest: false,
      TrainingRequestReasonsSelected: [],
      TrainingRequestFromSelfService: false,
      PMS: false,
      AppraisalFromDate: null,
      AppraisalToDate: null,
      PMS_Grade: 0,
      PMS_competenciesSelected: [],
      WithImplementation: false,
    };

    this.tNAService
      .searchEmployees(searchModel)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPlanTrainingNeedsEmployeeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSourceEmployees = new MatTableDataSource(
            planTnaToDisplay(res.data.filter((emp) => emp.isSelected))
          );
          // console.log(
          //   planTnaToDisplay(res.data.filter((emp) => emp.isSelected))
          // );
          this.isLoadedEmployees = true;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IPlanTrainingNeedsRequest = {
        Plan_ID: this.planId,
        person_Courses: [],
      };

      this.dataSourceSearch.data
        .filter((ele) => ele.isSelected)
        .forEach((selected) => {
          model.person_Courses.push({
            Course_ID: selected.course_ID,
            Person_Instance_ID: selected.person_Instance_ID,
          });
        });

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IPlanTrainingNeedsRequest) {
    this.showLoading("tna");
    this.tNAService
      .create(model)
      .subscribe((res: IResponse<IPlanTrainingNeedsEmployeeModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (this.isView) {
            this.returnToView();
          }
          else {
            this.showIndexAfterAdd = true;
          }
          // update course list in next tab
          this.updateCourseDropdown.emit();
          this.completed.emit(true);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("tna");
      });
  }

  onDelete(employee: IPlanTrainingNeedsEmployeeModel): void {
    let msg = employee.employee;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.delete(employee);
      }
    });
  }

  delete(employee: IPlanTrainingNeedsEmployeeModel) {
    let params: IPlanTrainingNeedsEmployeeDeleteParams = {
      plan_id: this.planId,
      course_id: employee.course_ID,
      person_instance_id: employee.person_Instance_ID,
    };
    this.showLoading("tna");
    this.tNAService.Delete(params).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        this.getAll();
        this.searchEmployees(this.lastSearchModel);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading("tna");
    });
  }

  onClearAll(): void {
    let msg = this.translate("allEmployees");
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.clearAll();
      }
    });
  }

  clearAll() {
    this.showLoading("tna");
    this.tNAService.DeleteAll(this.planId).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        this.dataSourceEmployees = new MatTableDataSource([]);
        if (this.lastSearchModel) {
          this.searchEmployees(this.lastSearchModel);
        }
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading("tna");
    });
  }

  onSubmitSearchEmployees() {
    this.submitted = true;
    if (this.form.valid) {
      // if no filter box checked
      if (
        !this.formCtrls.trainingPath.value &&
        !this.formCtrls.trainingRequests.value &&
        !this.formCtrls.pms.value &&
        !this.formCtrls.withImplementation.value
      ) {
        this.warning("selectAnyFilter");
        return;
      }

      // if no priority selected in training path
      if (
        this.formCtrls.trainingPath.value &&
        this.formCtrls.TrainhgPathPrioritySelected.value.length == 0
      ) {
        return;
      }

      this.showSearchSection = true;

      let searchModel: ITNAPlanSearchRequest = {
        Plan_ID: this.planId,
        IsEditMode: false,
        TrainingPath: this.form.value.trainingPath,
        TrainhgPathPrioritySelected:
          this.form.value.TrainhgPathPrioritySelected ?? [],
        TrainingRequest: this.form.value.trainingRequests,
        TrainingRequestReasonsSelected: this.form.value.reasonId ?? [],
        TrainingRequestFromSelfService:
          this.form.value.selfServiceRequest ?? false,
        PMS: this.form.value.pms,
        AppraisalFromDate: this.form.value.appraisalFrom,
        AppraisalToDate: this.form.value.appraisalTo,
        PMS_Grade: this.form.value.PMS_Grade ?? 0,
        PMS_competenciesSelected: this.form.value.competenceId ?? [],
        WithImplementation: this.form.value.withImplementation,
      };
      if (!searchModel.TrainingPath) {
        searchModel.TrainhgPathPrioritySelected = [];
      }
      // console.log("search", searchModel);

      this.searchEmployees(searchModel);
    }
  }

  searchEmployees(searchModel: ITNAPlanSearchRequest) {
    this.dataSourceSearch = new MatTableDataSource(null);
    this.isLoadedSearch = false;

    this.tNAService
      .searchEmployees(searchModel)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPlanTrainingNeedsEmployeeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSourceSearch = new MatTableDataSource(
            planTnaToDisplay(res.data)
          );
          this.isLoadedSearch = true;
          this.lastSearchModel = searchModel;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  clearForm() {
    this.form.reset({
      id: 0,
      selfServiceRequest: true,
      PMS_Grade: 0,
      trainingPath: false,
      trainingRequests: false,
      pms: false,
      withImplementation: false,
      TrainhgPathPrioritySelected: [],
      reasonId: [],
      competenceId: [],
    });
    this.submitted = false;
  }

  showAddInsideView() {
    this.updateViewDisplay.emit(true);
    this.showOnlyIndex = false;
    window.scrollTo({top: 0, behavior: "smooth"});
  }

  returnToView() {
    this.updateViewDisplay.emit(false);
    this.showOnlyIndex = true;
    this.clearForm();
    this.dataSourceSearch = new MatTableDataSource(null);
    this.showSearchSection = false;
  }
}
