import { Component, EventEmitter, Input, Output } from "@angular/core";
import { MatPaginator, PageEvent } from "@angular/material/paginator";
import { Page } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-mat-paginator",
  templateUrl: "./mat-paginator.component.html",
  styleUrls: ["./mat-paginator.component.scss"],
})
export class MatPaginatorComponent {
  @Input() page: Page;
  @Input() paginator: MatPaginator;
  @Input() isDialogSize: boolean = false;
  @Output() onChangePage = new EventEmitter<PageEvent>();


  constructor() {
  }

  onChange($event: PageEvent) {
    this.onChangePage.emit($event);
  }
}
