import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AddCourseComponent } from "app/training/components/courses/add-course/add-course-core/add-course.component";
import { ICourseToDisplay } from "app/training/models/course/course";
import { CourseService } from "app/training/services/course/course.service";
import { AddCourseFormComponent } from "app/training/components/courses/add-course/add-course-form/add-course-form.component";

@Component({
  selector: "app-course-by-course-category-dropdown",
  templateUrl: "./course-by-course-category-dropdown.component.html",
  styleUrls: ["./course-by-course-category-dropdown.component.scss"],
})
export class CourseByCourseCategoryDropdownComponent extends BaseDropdown
  implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "course";
  @Input() appendTo: string | HTMLElement;
  @Input() disable: boolean = false;
  courseList: ICourseToDisplay[];
  viewName: string;
  @Input() courseCategoryControl: FormControl;

  constructor(private courseService: CourseService) {
    super();
    this.viewName = ViewsManager.add_course;
  }

  ngOnInit(): void {
    this.control.disable();
    this.courseCategoryControl.valueChanges.subscribe((courseCategoryId) => {
      this.control.setValue(null);
      if (courseCategoryId > 0) {
        this.getAllCourseByCourseCategoryId();
        if (!this.disable) this.control.enable();
      }
      else this.control.disable();
    });
  }

  getAllCourseByCourseCategoryId() {
    this.courseService
      .getCourseByCategoryId(this.courseCategoryControl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICourseToDisplay[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.courseList = res.data;
        }
        else {
          this.errorList(res.message?.messages);
        }
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(AddCourseFormComponent, value, this.courseCategoryControl.value, {
        height: "90vh",
        maxHeight: "90vh",
        width: "85vw",
        maxWidth: "100vw"
      })
      .afterClosed()
      .subscribe((res) => {
        this.getAllCourseByCourseCategoryId();
      });
  }
}
