import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";
import {
  IPlanCourseExpensesModel,
  IPlanCourseModel,
  IPlanCoursesEmployeeModel,
} from "../models/training-plan/plan-courses-expenses";
import { IPlanTrainingNeedsEmployeeModel } from "../models/training-plan/training-needs-analysis";

export function planTnaToDisplay(
  data: IPlanTrainingNeedsEmployeeModel[]
): IPlanTrainingNeedsEmployeeModel[] {
  return (
    data
      // .filter((ele) => ele.core_Person != null)
      .map((ele) => {
        return <IPlanTrainingNeedsEmployeeModel>{
          ...ele,
          // code: ele.core_Person?.code,
          // employee: msgTranslate(
          //   ele.core_Person?.ar_Name,
          //   ele.core_Person?.en_Name
          // ),
          employee: msgTranslate(ele.personNameAr, ele.personNameEn),
          department: ele.core_Org
            ?.map((org) => msgTranslate(org.ar_Name, org.en_Name))
            .join(", "),
          job: ele.core_Job_Title
            ?.map((job) => msgTranslate(job.ar_Name, job.en_Name))
            .join(", "),
          noOfTakenCourses: `${msgTranslate(
            ele.courseNameAr,
            ele.courseNameEn
          )} - (${ele.courseCount})`,
          // isAdded: ele.isSelected,
        };
      })
  );
}

export function planCourseEmployeeToDisplay(
  data: IPlanCoursesEmployeeModel[]
): IPlanCoursesEmployeeModel[] {
  return data.map((ele) => {
    return <IPlanCoursesEmployeeModel>{
      ...ele,
      employeeNameAr: ele.ar_Name,
      employeeNameEn: ele.en_Name,
      job: msgTranslate(ele.ar_Job, ele.en_Job),
      isSelected: false,
    };
  });
}

export function planCourseToDisplay(
  data: IPlanCourseModel[]
): IPlanCourseModel[] {
  return data.map((ele) => {
    return <IPlanCourseModel>{
      ...ele,
      title: ele.name,
      course: msgTranslate(ele.trA_Course?.ar_Name, ele.trA_Course?.en_Name),
      plan: msgTranslate(ele.trA_PLan?.ar_Name, ele.trA_PLan?.en_Name),
      fromDate: HelpersFunctions.mapDate(ele.from_Date),
      toDate: HelpersFunctions.mapDate(ele.to_Date),
      online: ele.tsP_ID ? false : true,
      budget: ele.trA_PLan?.budget,
    };
  });
}

export function planCourseExpenseToDisplay(
  data: IPlanCourseExpensesModel[]
): IPlanCourseExpensesModel[] {
  return data.map((ele) => {
    return <IPlanCourseExpensesModel>{
      ...ele,
      // id: ele.plan_Course_ID,
      expense: msgTranslate(ele.trA_Expense?.ar_Name, ele.trA_Expense?.en_Name),
      value: ele.expense_Value,
    };
  });
}
