import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { AppClientService } from "app/shared/services/app-client.service";
import { ICourseModel } from "app/training/models/course/course";
import {
  IPlanCourseEmployeeDeleteParams,
  IPlanCourseEmployeesSearchParams,
  IPlanCourseModel,
  IPlanCourseRequest,
  IPlanCoursesEmployeeModel,
} from "app/training/models/training-plan/plan-courses-expenses";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class PlanCoursesService extends AppClientService {
  constructor() {
    super();
  }

  getList(planId: number, params: IPageParams) {
    return this.get<IResponseList<IPlanCourseModel[]>>(
      "Training/PlanCourses/GetList",
      {
        params: {
          ...params,
          $filter: `plan_ID eq ${planId}`,
          $expand: `${db.trA_PLan},${db.trA_Course}`,
        },
      }
    );
  }

  getAll(planId: number) {
    return this.get<IResponse<IPlanCourseModel[]>>(
      "Training/PlanCourses/GetAll",
      {
        params: {
          $filter: `plan_ID eq ${planId}`,
        },
      }
    ).pipe(
      map((res) => {
        res.data.map((item: IPlanCourseModel) => {
          item.ar_Name = `${item.name} (${HelpersFunctions.mapDate(
            item.from_Date
          )} - ${HelpersFunctions.mapDate(item.to_Date)})`;
          item.en_Name = `${item.name} (${HelpersFunctions.mapDate(
            item.from_Date
          )} - ${HelpersFunctions.mapDate(item.to_Date)})`;
        });
        return res;
      })
    );
  }

  create(data: IPlanCourseRequest) {
    return this.post<IResponse<IPlanCourseRequest>>(
      "Training/PlanCourses/Create",
      data
    );
  }

  update(data: IPlanCourseRequest) {
    return this.post<IResponse<IPlanCourseRequest>>(
      "Training/PlanCourses/Edit",
      data
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("Training/PlanCourses/Delete", {
      params: { Id: id },
    });
  }

  // get employees by course // not selected
  getPlanEmployees(params: IPlanCourseEmployeesSearchParams) {
    return this.get<IResponse<IPlanCoursesEmployeeModel[]>>(
      "Training/PlanCourses/GetPlanEmployees",
      {
        params: params,
      }
    );
  }

  // get employees by course // not selected
  getSelectedEmployees(params: IPlanCourseEmployeesSearchParams) {
    return this.get<IResponse<IPlanCoursesEmployeeModel[]>>(
      "Training/PlanCourses/GetSelectedEmployees",
      {
        params: params,
      }
    );
  }

  DeleteEmployee(params: IPlanCourseEmployeeDeleteParams) {
    return this.delete<IResponse>("Training/PlanCourses/Clear", {
      params: params,
    });
  }

  DeleteAllEmployees(planCourseId: number) {
    return this.delete<IResponse>("Training/PlanCourses/ClearAll", {
      params: { plan_course_id: planCourseId },
    });
  }

  // get courses by plan (courses can add to this plan)
  getAllCourses(planId: number) {
    return this.get<IResponse<ICourseModel[]>>(
      "Training/PlanCourses/DropDownListCourses",
      {
        params: {
          planId: planId,
        },
      }
    );
  }
}
