import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ILocViolationAllType,
  ILocViolationResponse,
  ILocViolationRequest,
  ILocViolationModel,
} from "../models/localization-violations";
import { map } from "rxjs/operators";
import { IPageParams } from "app/shared/interfaces/params";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class LocViolationsService extends AppClientService {
  constructor() {
    super();
  }

  expand = `${db.Loc_Attendance_Element}.${db.Loc_Violation}/${db.Loc_Type},${db.Loc_Attendance_Element}.${db.Loc_Violation}/${db.Loc_Violation_Type}`;

  create(data: ILocViolationRequest) {
    return this.post<IResponse<ILocViolationModel>>(
      "Violation/Create",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  update(data: ILocViolationRequest) {
    return this.post<IResponse<ILocViolationModel>>(
      "Violation/Edit",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ILocViolationResponse[]>>("Violation/GetList", {
      params: {
        ...params,
        ...this._paramsService.versionCountryParams,
        $expand: this.expand,
      },
    });
  }

  getAll() {
    return this.get<IResponse<ILocViolationResponse[]>>("Violation/GetAll", {
      params: {
        ...this._paramsService.versionCountryParams,
        $expand: this.expand,
      },

    });
  }

  getById(violationId: number) {
    return this.get<IResponse<ILocViolationResponse[]>>("Violation/GetAll", {
      params: {
        $filter: `id eq ${violationId}`,
        $expand: this.expand,
        ...this._paramsService.versionCountryParams,
      },
    });
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Violation/Delete", {
      params: {Id: Id, ...this._paramsService.versionCountryParams},
    });
  }

  // minutes / day / month
  getAllTypes() {
    return this.get<IResponse<ILocViolationAllType[]>>("Violation/GetAll_Type", {
      params: this._paramsService.versionCountryParams,
    }).pipe(
      map((res) => {
        res.data.map((item: ILocViolationAllType) => {
          item.ar_Name = item.en_Name;
        });
        return res;
      })
    );
  }

  exportExcel() {
    this.getAll();
  }
}
