import { Injectable } from "@angular/core";
import { IEmployee } from "app/personnel-setup/models/employees";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject, Subject } from "rxjs";
import { takeUntil } from "rxjs/operators";
import { IEmpStatusCardDisplayModel } from "app/personnel-setup/models/employee-employment-info";

@Injectable({
  providedIn: "root",
})
export class EmpGeneralInfoService extends AppClientService {
  employee$ = new BehaviorSubject<IEmployee>(undefined);
  static employeeStatusPosition$ = new BehaviorSubject<IEmpStatusCardDisplayModel>(undefined);
  static employeeId$ = new BehaviorSubject<number>(undefined);
  static employeeInstanceId$ = new BehaviorSubject<number>(undefined);
  public cancelRequest$ = new Subject<void>();

  constructor() {
    super();
  }

  getEmployeeShared(id: number, params?: { stopCompanyFilter: boolean }) {
    this.employee$.next(null);
    this.getEmployee(id, params)
      .pipe(takeUntil(this.cancelRequest$))
      .subscribe((res: IResponse<IEmployee>) => {
        if (res.message.messageType == MessageTypes.Success) {

          this.employee$.next(res.data);
          let employeeInstanceId = res.data.person.core_Person_Instance.filter(
            (e) => e.person_Type_ID == 1
          )[0].id;

          if (employeeInstanceId != EmpGeneralInfoService.EmployeeInstanceId)
            EmpGeneralInfoService.employeeInstanceId$.next(employeeInstanceId);
        }
      });
  }

  getEmployee(id: number, params?: { stopCompanyFilter: boolean }) {
    return this.get<IResponse<IEmployee>>("Personnel/Employees/GetEmployee", {
      params: {
        PerID: id,
        $expand: `${db.CORE_GENDER},${db.Core_Religion},${db.Core_Blood_Type}`,
        ...params,
      },
    });
  }

  shareToCurrentCompany(personelId: number) {
    return this.post<IResponse>(
      "Personnel/Employees/ShareToCurrentCompany",
      null,
      {
        params: {
          PerID: personelId,
        },
      }
    );
  }

  public static get EmployeeId(): number {
    return this.employeeId$.getValue();
  }

  public static get EmployeeInstanceId(): number {
    return this.employeeInstanceId$.getValue();
  }

}
