import { Component, EventEmitter, Input, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { ContactTypeComponent } from "app/system-setup/hr-setup/components/contact-type/contact-type.component";
import { IContactTypeModel } from "app/system-setup/hr-setup/models/contact-type";
import { ContactTypeService } from "app/system-setup/hr-setup/services/contact-type.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-contact-type-dropdown",
  templateUrl: "./contact-type-dropdown.component.html",
  styleUrls: ["./contact-type-dropdown.component.scss"],
})
export class ContactTypeDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "contactType";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IContactTypeModel[]>();

  contactTypesList: IContactTypeModel[];
  viewName: string;

  constructor(private contactService: ContactTypeService) {
    super();
    this.viewName = ViewsManager.contact_type;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllContactType();
  }

  getAllContactType() {
    this.contactService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IContactTypeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.contactTypesList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(values: IContactTypeModel[]) {
    this.change.emit(values);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(ContactTypeComponent, value)
      .afterClosed()
      .subscribe(() => {
        this.getAllContactType();
      });
  }
}
