import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponseList } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ILocVersion, ILocVersionsGetAllDropdown, } from "app/system-setup/localization-setup/models/localization-version";
import { LocVersionService } from "app/system-setup/localization-setup/services/localization-version.service";
import {
  LocAddVersionComponent
} from "app/system-setup/localization-setup/components/localization-version/localization-add-version/loc-add-version.component";
import { Storage } from "app/shared/resources/local-storage";

@Component({
  selector: "app-versions-dropdown",
  templateUrl: "./versions-dropdown.component.html",
  styleUrls: ["./versions-dropdown.component.scss"],
})
export class VersionsDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = true;
  @Input() setDefaultVal: boolean = false;
  @Input() hideCreateNew: boolean = false;
  @Input() label: string = "versions";
  @Input() appendTo: string | HTMLElement | any;
  @Output() change = new EventEmitter<ILocVersion[]>();

  list: ILocVersionsGetAllDropdown[];
  viewName: string;

  constructor(private versionService: LocVersionService) {
    super();
    this.viewName = ViewsManager.version_index;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllVersions();
  }

  override onChangeVersionOrCountry() {
    if (this.checkNoBusinessOrCompany()) this.getAllVersions();
  }

  getAllVersions() {
    this.versionService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<ILocVersionsGetAllDropdown[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          res.data.map((e) => {
            e.ar_Name = e.name;
            e.en_Name = e.name;
          });
          this.list = res.data;
          if (this.setDefaultVal && !Storage.getVersionID)
            this.control.setValue(res.data[0].id);
        }
        else this.errorList(res.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(LocAddVersionComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllVersions();
      });
  }

  onChange(versions: ILocVersionsGetAllDropdown[]) {
    this.change.emit(versions);
  }
}
