import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {FormArray, FormGroup, Validators} from "@angular/forms";
import {IAttendancePlanModel, IAttendancePlanRequest, IAttPlanShiftList,} from "app/system-setup/attendance-setup/models/plan";
import {IShiftsModel} from "app/system-setup/attendance-setup/models/shifts";
import {AttendancePlanService} from "app/system-setup/attendance-setup/services/attendance-plan.service";
import {ShiftsService} from "app/system-setup/attendance-setup/services/shifts.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {ICreateDialog} from "app/shared/interfaces/dropdown.interface";
import {IResponse} from "app/shared/interfaces/responces";
import {AppHelpers} from "app/shared/resources/app-helpers";
import {HelpersFunctions} from "app/shared/resources/helpers-functions";
import {CustomValidator} from "app/shared/validators/custom.validator";
import {differenceInDays} from "date-fns";
import {CustomValidators} from "ngx-custom-validators";

@Component({
  selector: "app-plan-form",
  templateUrl: "./plan-form.component.html",
  styleUrls: ["./plan-form.component.scss"],
})
export class PlanFormComponent extends AppHelpers implements OnInit {
  @Input() showDaysTable: boolean = true;
  @Input() disabelControls: boolean = false;
  @Input() isExtendWithChange: boolean = false;
  @Input() showInOutCheckBox: boolean = false;
  @Input() pageTitle: string = "attendance_Plans";
  @Output() onSubmit = new EventEmitter<IAttendancePlanRequest>();
  form: FormGroup;
  submitted: boolean = false;
  planId: number = 0;
  shiftList: IShiftsModel[];

  // dialog data
  @Input() data: ICreateDialog;
  @Output() closeDialog = new EventEmitter();
  // private destroy$ = new Subject<void>();

  constructor(
    public planService: AttendancePlanService,
    public shiftsService: ShiftsService
  ) {
    super();
    this.activatedRoute.paramMap.subscribe((params) => {
      this.planId = Number(params.get("planId")) ?? 0;
      if (this.planId > 0 && this.checkNoBusinessOrCompany()) {
        this.getPlanById();
        this.getShifts();
      }
    });

    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      start_Date: [null, [Validators.required]],
      end_Date: [null, [Validators.required]],
      group_Days: [
        null,
        [Validators.required, CustomValidators.digits, Validators.min(0)],
      ],
      description: [null],
      Att_Plan_ShiftList: this.fb.array([]),
      isCheckInOut: [null],
    });
  }

  ngOnInit(): void {
    this.formCtrls.group_Days.valueChanges.subscribe((val) => {
      if (val > 0 && this.showDaysTable) this.updateGroupsOfDay();
    });
    this.getShifts();

    if (this.data?.value) {
      this.formCtrls.ar_Name.setValue(this.data?.value);
      this.formCtrls.en_Name.setValue(this.data?.value);
    }
  }

  ngOnChanges() {
    if (this.disabelControls) {
      this.controlsDisabeld();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  // get installments array
  get planShiftDetailsArr() {
    return this.form.controls["Att_Plan_ShiftList"] as FormArray;
  }

  getPlanById() {
    this.showLoading();
    this.planService
      .getById(this.planId)
      .subscribe((res: IResponse<IAttendancePlanModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          let data = res.data[0];
          this.form.patchValue(data);
          if (this.isExtendWithChange) {
            var new_startDate: Date = new Date(data.end_date);
            var new_endDate: Date = new Date(data.end_date);
            new_startDate.setDate(new_startDate.getDate() + 1); // new start date = end date + (1 day)
            new_endDate.setDate(new_startDate.getDate() + 1 + data.group_Days); // new end date start from  new start date + (1 day) + group of days
            this.formCtrls.start_Date.setValue(new_startDate);
            this.formCtrls.end_Date.setValue(new_endDate);
            // this.formCtrls.end_Date.addValidators([
            // 	CustomValidator.dateBefore("end_Date", new_endDate),
            // ]);
          }
        }
        this.hideLoading();
      });
  }

  controlsDisabeld() {
    let controls = ["ar_Name", "en_Name", "group_Days", "start_Date"];
    controls.forEach((control: string) => {
      this.form.get(control).disable();
    });
  }

  submitForm() {
    this.submitted = true;
    this.planShiftDetailsArr.clearValidators();
    console.log(this.form);
    if (this.form.valid) {
      let planDetailsList: IAttPlanShiftList[] = [];
      this.planShiftDetailsArr.controls.map((form: FormGroup, i) => {
        planDetailsList.push({
          id: form.controls.id.value,
          plan_ID: form.controls.plan_ID.value,
          shift_ID: form.controls.shift_ID.value,
          is_WeekEnd: form.controls.is_WeekEnd.value,
          is_Basic: form.controls.is_Basic.value ?? false,
          date: HelpersFunctions.mapDateReverse(form.controls.date.value),
          iterationNumber: 1,
        });
      });

      let model: IAttendancePlanRequest = {
        plan: {
          id: this.formCtrls.id.value,
          en_Name: this.formCtrls.en_Name.value,
          ar_Name: this.formCtrls.ar_Name.value,
          description: this.formCtrls.description.value,
          start_Date: HelpersFunctions.mapDateReverse(
            this.formCtrls.start_Date.value
          ),
          end_Date: HelpersFunctions.mapDateReverse(
            this.formCtrls.end_Date.value
          ),
          group_Days: this.formCtrls.group_Days.value,
        },
        Att_Plan_ShiftList: planDetailsList,
      };
      if (this.showInOutCheckBox)
        model.plan.isCheckInOut = this.formCtrls.isCheckInOut.value ?? false;

      // console.log(model);
      this.onSubmit.emit(model);
    }
  }

  // add new from for plan shift details
  addPlanShiftDetailsForm(date: Date) {
    let planShiftDetailsForm = this.fb.group({
      id: [0],
      plan_ID: [this.formCtrls.id.value],
      shift_ID: [null, [Validators.required]],
      dateString: [this.datePipe.transform(date, "dd/MM/yyyy, h:mm:ss a")],
      date: [date],
      is_WeekEnd: [false],
      is_Basic: [false],
      dayName: [HelpersFunctions.getDayName(date)],
      // iterationNumber: [1],
    });

    this.planShiftDetailsArr.push(planShiftDetailsForm);
  }

  updateGroupsOfDay() {
    this.planShiftDetailsArr.clear();
    const startDate = new Date(this.formCtrls.start_Date.value);
    const endDate = new Date(this.formCtrls.end_Date.value);
    const groupOfDays = this.formCtrls.group_Days.value;
    const range = differenceInDays(endDate, startDate);
    if (groupOfDays <= range) {
      for (let i = 0; i < groupOfDays; i++) {
        let curDate = this.getDateInRange(startDate, i);
        this.addPlanShiftDetailsForm(curDate);
      }
    }
  }

  // get selected form
  onChangeIsWeekend(value: boolean, index: number) {
    if (!value) {
      let selectedForm = this.planShiftDetailsArr.controls[index] as FormGroup;
      selectedForm.controls.is_Basic.setValue(false);
    }
  }

  getDateInRange(startDate: Date, index: number): Date {
    let newDate = new Date(startDate);
    newDate.setDate(startDate.getDate() + index);
    return newDate;
  }

  // repeat shifts
  repeatShifts(index: number) {
    let selectedShiftId: number; // 1
    this.planShiftDetailsArr.controls.map((form: FormGroup, i) => {
      if (i == index) selectedShiftId = form.controls.shift_ID.value;
      if (i > index) form.controls.shift_ID.setValue(selectedShiftId);
    });
  }

  getShifts() {
    this.showLoading();
    this.shiftsService
      .getAll()
      //  .pipe(takeUntil(this.destroy$))
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IShiftsModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.shiftList = res.data;
        } else this.errorList(res.message?.messages);
        this.hideLoading();
      });
  }

  onCloseDialog() {
    this.closeDialog.emit();
  }
  // ngOnDestroy() {
  //   // Unsubscribe from all observables to prevent memory leaks
  //   this.destroy$.next();
  //   this.destroy$.complete();
  // }
}
