import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import {
  ISapSystemInstanceModel,
  ISapSystemInstanceRequest,
} from "app/system-setup/sap-business-one-setup/models/system-instance";
import { SystemInstanceService } from "app/system-setup/sap-business-one-setup/services/system-instance.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-add-system-instance",
  templateUrl: "./add-system-instance.component.html",
  styleUrls: ["./add-system-instance.component.scss"],
})
export class AddSystemInstanceComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  // systemInstance: ISapSystemInstanceModel;
  systemInstanceId: number;

  constructor(
    private systemInstanceService: SystemInstanceService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<AddSystemInstanceComponent>
  ) {
    super();
  }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.systemInstanceId = Number(params.get("systemInstanceId")) ?? 0;
      // if (this.systemInstanceId > 0 && this.CheckNoBusinessOrCompany())
      //   this.getById();
    });

    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      serverName: [null, [Validators.required, CustomValidator.whiteSpace]],
      licenseServer: [null, [Validators.required]],
      dbUserName: [null, [Validators.required, CustomValidator.whiteSpace]],
      dbPassWord: [null, [Validators.required, CustomValidator.whiteSpace]],
      companyDB: [null, [Validators.required, CustomValidator.whiteSpace]],
      userName: [null, [Validators.required, CustomValidator.whiteSpace]],
      password: [null, [Validators.required, CustomValidator.whiteSpace]],
      unit_Mesaure: [null],
      enumDbServerType: [null, [Validators.required, CustomValidator.whiteSpace]],
    });
    if (this.data.value) {
      this.formCtrls.ar_Name.setValue(this.data.value);
      this.formCtrls.en_Name.setValue(this.data.value);
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    // this.systemInstance = null;
    if (this.systemInstanceId > 0 && this.checkNoBusinessOrCompany()) this.getById();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getById() {
    this.showLoading();
    this.systemInstanceService
      .getById(this.systemInstanceId)
      .subscribe((res: IResponse<ISapSystemInstanceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // this.systemInstance = res.data[0];
          this.systemInstanceToEdit(res.data[0]);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  systemInstanceToEdit(systemInstance: ISapSystemInstanceModel) {
    this.form.patchValue(systemInstance);
    // console.log(this.form.value);
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ISapSystemInstanceRequest = {
        ID: this.form.value.id,
        Ar_Name: this.form.value.ar_Name,
        En_Name: this.form.value.en_Name,
        ServerName: this.form.value.serverName,
        LicenseServer: this.form.value.licenseServer,
        DBUserName: this.form.value.dbUserName,
        DBPassWord: this.form.value.dbPassWord,
        CompanyDB: this.form.value.companyDB,
        UserName: this.form.value.userName,
        Password: this.form.value.password,
        Unit_Mesaure: this.form.value.unit_Mesaure,
        enumDbServerType: this.form.value.enumDbServerType,
      };

      model.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: ISapSystemInstanceRequest) {
    this.showLoading();
    this.systemInstanceService.create(model).subscribe((res: IResponse<ISapSystemInstanceModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        // this.clearForm();
        this.systemInstanceId = res.data.id;
        this.formCtrls.id.setValue(this.systemInstanceId);
        let url = `/sap-business-one-setup/${this.moduleId}/system-instance/edit/${this.systemInstanceId}`;
        if (!this.data.isDialog) this.navigateTo(url);
        this.addSuccessfullyMsg(res?.message?.messageTypeName);
        // this.systemInstance = res.data;
        if (this.data.isDialog) this.dialogRef.close();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  update(model: ISapSystemInstanceRequest) {
    this.showLoading();
    this.systemInstanceService.update(model).subscribe((res: IResponse<ISapSystemInstanceModel>) => {
      if (res.message.messageType == MessageTypes.Success) {
        // this.clearForm();
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        // this.systemInstance = res.data;
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    // if (this.systemInstanceId == 0)
    this.form.reset({id: 0});
    // else this.systemInstanceToEdit();
    this.submitted = false;
  }

  onCloseDialog() {
    this.dialogRef.close();
  }
}
