import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { OrganizationTypesComponent } from "app/system-setup/org-management-setup/components/organization-types/organization-types.component";
import { IOrganizationTypeModel } from "app/system-setup/org-management-setup/models/organization-types";
import { OrganizationTypesService } from "app/system-setup/org-management-setup/services/organization-types.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
	selector: "app-org-types-dropdown",
	templateUrl: "./org-types-dropdown.component.html",
	styleUrls: ["./org-types-dropdown.component.scss"],
})
export class OrgTypesDropdownComponent extends BaseDropdown implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "organizationTypes";
	@Input() appendTo: string | HTMLElement = "body";
	@Input() optionDisabled: string = "isDisabled";
	@Output() change = new EventEmitter<IOrganizationTypeModel[]>();

	// use in org strcuture
	@Input() disableCompany: boolean = false;
	// @Input() selectCompanyOnly: boolean = false;

	organizationTypeList: IOrganizationTypeModel[];
	viewName: string;

	constructor(private organizationTypesService: OrganizationTypesService) {
		super();
		this.viewName = ViewsManager.organization_types;
	}

	ngOnInit(): void {}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllOrganizationTypes();
	}

	getAllOrganizationTypes() {
		// this.showLoading();
		this.organizationTypesService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IOrganizationTypeModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          if (this.disableCompany)
            res.data.map((type) => (type.isDisabled = this.isCompany(type)));
          this.organizationTypeList = res.data;

          // if (this.selectCompanyOnly) {
          //   this.control.setValue(1);
          //   this.control.disable();
          // }
        } else this.errorList(res?.message?.messages);
        // this.hideLoading();
      });
	}

	onChange(orgType: IOrganizationTypeModel[]) {
		this.change.emit(orgType);
	}

	isCompany(orgType: IOrganizationTypeModel) {
		return (
			orgType.en_Name.toLowerCase() == "company" ||
			orgType.ar_Name.includes("") ||
			orgType.ar_Name.includes("")
		);
	}

	createDialog(value: string) {
		this.dialogsService
			.addFromDropdownDialog(OrganizationTypesComponent, value)
			.afterClosed()
			.subscribe((res) => {
				this.getAllOrganizationTypes();
			});
	}
}
