import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IResponse } from "app/shared/interfaces/responces";
import {
  IModuleModel,
  IUM_ModulesModel,
} from "app/shared/interfaces/user-views";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-module-dropdown",
  templateUrl: "./module-dropdown.component.html",
  styleUrls: ["./module-dropdown.component.scss"],
})
export class ModuleDropdaownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "module";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<any>();

  modules: IModuleModel[] = [];
  constructor() {
    super();
  }

  ngOnInit(): void {
    this.getAllmodules();
  }

  onChange(moduleId:number) {
    this.change.emit(moduleId);
  }

  getAllmodules() {
    this.viewsService
      .getUserModules()
      .subscribe((res: IResponse<IUM_ModulesModel[]>) => {
        res.data.map((data) => {
          let item = {
            id: data.id,
            ar_Name: data.arbName,
            en_Name: data.engName,
          } as IModuleModel;
          this.modules.push(item);
        });
      });
  }
}
