import { Component, EventEmitter, Input, Output } from "@angular/core";
import { MatStepper } from "@angular/material/stepper";
import { Router } from "@angular/router";
import { Btn } from "app/shared/enums/btns.enum";
import { LanguageService } from "app/shared/services/language.service";

@Component({
  selector: "steps-footer",
  templateUrl: "./steps-footer.component.html",
  styleUrls: ["./steps-footer.component.scss"],
})
export class StepsFooterComponent {
  @Input() stepper: MatStepper;
  @Input() parentParam: any;
  @Input() pageParams: any;
  @Input() url: string;
  @Input() showCancel: boolean = true;
  @Input() returnAfterFinish: boolean = true;
  @Output() finish = new EventEmitter();
  btn = Btn;

  constructor(
    public languageService: LanguageService,
    private router: Router
  ) {}

  next() {
    // if (this.activeIndex < this.items.length - 1) {
    //   this.activeIndex++;
    // }

    if (this.stepper.selectedIndex < this.stepper.steps.length - 1) {
      this.stepper.next();
    }
  }

  back() {
    // if (this.activeIndex > 0) {
    //   this.activeIndex--;
    // }

    if (this.stepper.selectedIndex > 0) {
      this.stepper.previous();
    }
  }

  onFinish() {
    this.finish.emit();
    if (this.returnAfterFinish)
      this.router.navigate([this.url], { queryParams: this.pageParams });
  }
}
