import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { AttachmentTypesService } from "app/system-setup/general-setup/services/attachment-types.service";
import { IAttachmentTypesModel } from "app/system-setup/general-setup/models/attachment-types";

@Component({
  selector: "app-attachment-dropdown",
  templateUrl: "./attachment-dropdown.component.html",
  styleUrls: ["./attachment-dropdown.component.scss"],
})
export class AttachmentDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  @Input() multiple: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "attachments";
  @Input() appendTo: string = "body";
  @Output() change = new EventEmitter<IAttachmentTypesModel[]>();

  attachmentsList: IAttachmentTypesModel[];

  constructor(private attachmentTypesService: AttachmentTypesService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllAttachments();
  }

  getAllAttachments() {
    this.attachmentTypesService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IAttachmentTypesModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.attachmentsList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  onChange(values: IAttachmentTypesModel[]) {
    this.change.emit(values);
  }
}
