import {Component, Input, OnInit} from "@angular/core";
import {FormControl} from "@angular/forms";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import {IPolicyGroupModel} from "app/org-management/model/policy-group";
import {PolicyGroupService} from "app/org-management/services/policy-group.service";

@Component({
  selector: "policy-groups-by-company-dropdown",
  templateUrl: "./policy-groups-by-company-dropdown.component.html",
  styleUrls: ["./policy-groups-by-company-dropdown.component.scss"],
})
export class PolicyGroupsByCompanyDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "policyGroups";
  @Input() companyCtrl?: FormControl;
  policyGroupList: IPolicyGroupModel[] = [];

  constructor(private userPoliciesService: PolicyGroupService) {
    super();
  }

  ngOnInit(): void {
  }

  ngOnChanges() {
    this.control.disable();
    this.companyCtrl?.valueChanges.subscribe((companyId) => {
      if (companyId > 0) {
        this.getPoliceiesByCompany();
        if (!this.disable) this.control.enable();
      } else {
        this.policyGroupList = [];
        this.control.disable();
      }
    });
  }

  getPoliceiesByCompany() {
    const params = {
      compId: this.companyCtrl.value,
    };

    this.userPoliciesService
      .getPolicesByCompany(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPolicyGroupModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.policyGroupList = res.data;
        } else {
          this.errorList(res?.message?.messages);
        }
      });
  }
}
