import { Component, ElementRef, EventEmitter, Input, OnInit, Output, Renderer2, ViewChild, ViewEncapsulation, } from "@angular/core";

@Component({
  selector: "side-helping-menu",
  templateUrl: "./side-helping-menu.component.html",
  styleUrls: ["./side-helping-menu.component.scss"],
  encapsulation: ViewEncapsulation.None
})
export class SideHelpingMenuComponent implements OnInit {
  @Input() title: string;
  @Input() subTitle: string;
  @Input() icon: string = "fas fa-info-circle";
   @ViewChild("helpingMenu") helpingMenu: ElementRef;
  size: string;
  isOpen = false;
  public config: any = {};

  constructor(private renderer: Renderer2) {}

  @Output() directionEvent = new EventEmitter<Object>();

  ngOnInit() {}

  toggleCustomizer() {
    if (this.isOpen) {
      this.renderer.removeClass(this.helpingMenu.nativeElement, "open");
      this.isOpen = false;
    }
    else {
      this.renderer.addClass(this.helpingMenu.nativeElement, "open");
      this.isOpen = true;
    }
  }

  closeCustomizer() {
    this.renderer.removeClass(this.helpingMenu.nativeElement, "open");
    this.isOpen = false;
  }

}
