import { Component, Input, OnInit, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { CountryComponent } from "app/system-setup/hr-setup/components/country/country.component";
import { ICountryModel } from "app/system-setup/hr-setup/models/country";
import { CountryService } from "app/system-setup/hr-setup/services/country.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponseList } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { Storage } from "app/shared/resources/local-storage";

@Component({
  selector: "app-country-dropdown",
  templateUrl: "./country-dropdown.component.html",
  styleUrls: ["./country-dropdown.component.scss"],
})
export class GovernorateDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() setDefaultVal: boolean = false;
  @Input() label: string = "country";
  @Input() appendTo: string | HTMLElement | any;
  @Output() change = new EventEmitter<ICountryModel[]>();

  countrytList: ICountryModel[];
  viewName: string;

  constructor(private countryService: CountryService) {
    super();
    this.viewName = ViewsManager.country;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllCountry();
  }

  getAllCountry() {
    this.countryService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<ICountryModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.countrytList = res.data;
          if (this.setDefaultVal && !Storage.getCountryID) this.control.setValue(res.data[0].id);
        }
        else this.errorList(res.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(CountryComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllCountry();
      });
  }

  onChange(countries: ICountryModel[]) {
    this.change.emit(countries);
  }
}
