import { ActiveToast, IndividualConfig, ToastrService } from "ngx-toastr";
import { inject, Injectable } from "@angular/core";
import { IResponseMsg } from "../interfaces/responces";
import { LanguageService } from "./language.service";

@Injectable({
  providedIn: "root",
})
export class MessagesService {
  private lang = inject(LanguageService);
  private toastr = inject(ToastrService);
  options = {
    autoDismiss: false,
    timeOut: 0,
    extendedTimeOut: 0,
    closeButton: true,
    //  autoDismiss: false,
    // timeOut: 7000,
    // timeOut: 0,
    // extendedTimeOut: 0,
    // closeButton: true,
  };

  constructor() {}

  success(message: string, title?: string, override?: Partial<IndividualConfig>): ActiveToast<any> {
    let msg = this.toastr.success(this.lang.translate(message), this.lang.translate(title), override);
    return msg;
  }

  error(message: string, title?: string, override?: Partial<IndividualConfig>): ActiveToast<any> {
    let msg = this.toastr.error(this.lang.translate(message), this.lang.translate(title), {...override, ...this.options});
    return msg;
  }

  info(message: string, title?: string, override?: Partial<IndividualConfig>): ActiveToast<any> {
    let msg = this.toastr.info(this.lang.translate(message), this.lang.translate(title), override);
    return msg;
  }

  warning(message: string, title?: string, override?: Partial<IndividualConfig>): ActiveToast<any> {
    let msg = this.toastr.warning(this.lang.translate(message), this.lang.translate(title), override);
    return msg;
  }

  show(message: string, title?: string, override?: Partial<IndividualConfig>): ActiveToast<any> {
    let msg = this.toastr.show(this.lang.translate(message), this.lang.translate(title), override);
    return msg;
  }

  clear(toastId?: number) {
    this.toastr.clear(toastId);
  }

  errorList(errorsList: any[]) {
    if (errorsList?.length > 0) {
      errorsList?.forEach((err: any) => {
        this.error(err?.message);
      });
    }
  }

  infoList(errorsList: IResponseMsg[]) {
    if (errorsList?.length > 0) {
      errorsList?.forEach((err: IResponseMsg) => {
        this.info(err.message);
      });
    }
  }
}
