import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {FormControl} from '@angular/forms';
import {IResponse} from 'app/shared/interfaces/responces';
import {MessageTypes} from 'app/shared/enums/message-types.enum';
import {EmployeesService} from 'app/personnel-setup/services/employees/employees.service';
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import {IEmployeeSharedCompany} from 'app/personnel-setup/models/employees';

@Component({
  selector: 'employees-shared-company',
  templateUrl: './employees-shared-company.component.html',
  styleUrls: ['./employees-shared-company.component.scss']
})
export class EmployeesSharedCompanyComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() person_ID: number;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() appendTo: string | HTMLElement = 'body';
  @Input() label: string = 'SharedCompany';
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<IEmployeeSharedCompany[]>();

  list: IEmployeeSharedCompany[];

  // viewName: string;

  constructor(private employeesService: EmployeesService) {
    super();
    // this.viewName = ViewsManager.expense;
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.person_ID > 0) {
      this.getAllSharedCompany();
    }
  }

  getAllSharedCompany() {
    this.employeesService
      .getAllSharedCompany(this.person_ID)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmployeeSharedCompany[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.list = res.data;
          console.log(this.list)
        } else {
          this.errorList(res?.message?.messages);
        }
      });
  }

  onChange(expense: IEmployeeSharedCompany[]) {
    this.change.emit(expense);
  }

  // createDialog(value: string) {
  //   this.dialogsService
  //     .addFromDropdownDialog(ExpenseComponent, value)
  //     .afterClosed()
  //     .subscribe((res) => {
  //       this.getAllSharedCompany();
  //     });
  // }
}
