import { Injectable } from "@angular/core";
import {
  ICandidateCardListModel,
  ICandidateGenaralIsDoneRequest,
  ICandidateGenaralRequest,
  ICandidateListModel,
} from "app/recruitment/models/candidate/candidate";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { BehaviorSubject } from "rxjs";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class CandidateService extends AppClientService {
  submitted$ = new BehaviorSubject<boolean>(false);
  isDone$ = new BehaviorSubject<ICandidateGenaralIsDoneRequest>({
    GeneralData: false,
    MaritalData: false,
    MilitaryData: false,
    AddressData: false,
    ContactData: false,
    EducationData: false,
    TrainingData: false,
    CertificationData: false,
    WorkExperincesData: false,
    DocumentData: false,
    RefrenceData: false,
    JobInfoData: false,
    relatedData: false,
  });
  candidateData$ = new BehaviorSubject<ICandidateGenaralRequest>({
    GeneralData: null,
    MaritalData: null,
    MilitaryData: null,
    AddressData: null,
    ContactData: [],
    EducationData: [],
    TrainingData: [],
    CertificationData: [],
    WorkExperincesData: [],
    DocumentData: [],
    RefrenceData: [],
    JobInfoData: [],
    relatedData: [],
  });

  constructor() {
    super();
  }

  // create(data: ICandidateRequest) {
  //   return this.post<IResponse<ICandidateModel>>(
  //     "Recruitment/Candidate/Create",
  //     data
  //   );
  // }

  // update(data: ICandidateRequest) {
  //   return this.post<IResponse<ICandidateModel>>(
  //     "Recruitment/Candidate/Edit",
  //     data
  //   );
  // }

  createGeneral(data: FormData) {
    return this.post<IResponse<ICandidateGenaralRequest>>(
      "Recruitment/Candidate/Create",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ICandidateListModel[]>>(
      "Recruitment/GeneralData/GetList",
      {
        params: {
          ...params,
        },
      }
    );
  }

  getListCard() {
    return this.get<IResponseList<ICandidateCardListModel[]>>(
      "Recruitment/GeneralData/getListCard"

    );
  }

  getAll() {
    return this.get<IResponse<any[]>>("Recruitment/GeneralData/GetAll");
  }

  getById(id: number) {
    return this.get<IResponse<ICandidateListModel[]>>(
      "Recruitment/GeneralData/GetAll",
      {
        params: {
          $filter: `Person_ID  eq ${id}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Recruitment/GeneralData/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    return this.getAll().pipe(
      map((res: IResponse<any[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // res.data = recruitmentRequestToDisplay(res.data);
          return res;
        }
      })
    );
  }

  getAllSourcingType() {
    return this.get<IResponse<Idropdown[]>>("Recruitment/Resource/GetAll");
  }
}
