import {
  Component,
  EventEmitter,
  inject,
  Input,
  OnChanges,
  Output,
  SimpleChanges,
  ViewChild,
} from "@angular/core";
import { FormControl } from "@angular/forms";
import { Params } from "@angular/router";
import { IFileBinary, IFileData } from "app/shared/interfaces/files";
import { SvgManager } from "app/shared/resources/assets-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { LanguageService } from "app/shared/services/language.service";
import { MessagesService } from "app/shared/services/messages.service";
import { environment } from "environments/environment";
import { FileUpload } from "primeng/fileupload";

export interface UploadEvent {
  originalEvent: Event;
  files: File[];
}

@Component({
  selector: "primeng-file-upload",
  templateUrl: "./file-upload.component.html",
  styleUrls: ["./file-upload.component.scss"],
})
export class FileUploadComponent extends BaseDropdown implements OnChanges {
  @ViewChild("fileUpload", {static: false}) fileUpload: FileUpload;
  @Input() control: FormControl;
  @Input() multiple: boolean = true;
  @Input() accept: string = "";
  @Input() label: string = "";
  @Input() submitted: boolean = false;
  @Input() isHexImg: boolean = false;
  @Input() disabled: boolean = false;
  @Input() removeFiles: boolean = false;
  @Input() isEdit: boolean = false;
  @Input() isDragDrop: boolean = false;
  @Input() existingFiles: IFileData[] = [];
  @Output() onselect = new EventEmitter<any>();
  @Output() onremove = new EventEmitter<any>();
  @Output() ondelete = new EventEmitter<any>();
  // use in direct upload
  @Input() url: string = null;
  @Input() fileNameAtBackend: string = "files";
  @Input() isFormValid: boolean = true;
  @Input() params: any;
  @Output() onsubmit = new EventEmitter<boolean>();
  @Output() afterupload = new EventEmitter<boolean>();

  baseUrl = `${environment.baseUrl}/api/`;

  languageService = inject(LanguageService);
  messagesService = inject(MessagesService);
  assets: SvgManager = new SvgManager();

  ngOnChanges(changes: SimpleChanges): void {
    // console.log(changes);

    if (changes.removeFiles?.currentValue || changes.isEdit) {
      this.fileUpload?.clear();
    }

    if (
      changes.isEdit?.currentValue &&
      !this.multiple &&
      this.control.value &&
      this.isHexImg
    ) {
      this.fileUpload.files = [
        this.fileService.createFileFromHex(this.control.value, "", "image/*"),
      ];
    }
  }

  async uploadHexImage(files) {
    this.isEdit = false;
    await this.fileService.uploadHexImage(files).then((file: IFileBinary) => {
      setTimeout(() => {
        this.control?.setValue(file.hexString);
      }, 1000);
    });
  }

  onDeleteFile(file?: any): void {
    let msg = this.languageService.translate("attachment").toLocaleLowerCase();
    this.dialogsService.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) {
        this.removeFileFromList(file);
      }
    });
  }

  removeFileFromList(file?: IFileData) {
    this.ondelete.emit(file);
    // console.log(file);
  }

  removeFileNotUploaded(file?: File) {
    if (!this.multiple) {
      this.control?.setValue(null);
      this.fileUpload.clear();
      // console.log("not multiple");
    }
    else {
      let i = this.fileUpload.files.findIndex(
        (ele) => ele.name == file.name && ele.size == file.size
      );
      this.fileUpload.files.splice(i, 1);
      this.select(this.fileUpload.files, true);
    }
  }

  select(files, afterRemove: boolean = false) {
    if(!this.isHexImg) {
      this.isEdit = false;
      for (let i = 0; i < files.length; i++) {
        delete files[i].objectURL;
      }
      this.control?.setValue(this.multiple ? files : files[0]);
      if (!afterRemove) this.onselect.emit(files);
      else this.onremove.emit(files);
    }
  }

  // // files with form submit
  // public uploadFiles = (files) => {
  //   this.isEdit = false;
  //   this.control.setValue(this.fileService.upload(files));
  // };

  // async uploadBase64(files) {
  //   this.isEdit = false;
  //   await this.fileService.uploadBase64(files).then((file: IFileBase64) => {
  //     setTimeout(() => {
  //       this.control?.setValue(file);
  //     }, 100);
  //   });
  // }

  beforeUpload() {
    this.onsubmit.emit(true);
  }

  afterUpload(event) {
    this.messagesService.success(
      event?.originalEvent?.body?.message?.messageTypeName
    );
    this.afterupload.emit(true);
  }

  getUrl(): string {
    if (this.isFormValid) {
      const params: Params = {
        ...this.businessCompanyParams,
        ...this.params,
      };

      return (
        this.baseUrl + this.url + "?" + new URLSearchParams(params).toString()
      );
    }
    else return null;
  }

  chooseFile() {
    this.fileUpload.choose();
  }
}
