import { Directive, ElementRef, HostListener } from "@angular/core";

@Directive({
  selector: "[appScrollableHorizontal]"
})
export class ScrollableHorizontalDirective {
  private isDragging: boolean = false;
  private startX: number = 0;
  private startScrollX: number = 0;

  constructor(private el: ElementRef) { }

  @HostListener("mousedown", ["$event"])
  onMouseDown(event: MouseEvent) {
    this.isDragging = true;
    this.startX = event.clientX;
    this.startScrollX = this.el.nativeElement.scrollLeft;
  }

  @HostListener("document:mouseup")
  onMouseUp() {
    this.isDragging = false;
  }

  @HostListener("document:mousemove", ["$event"])
  onMouseMove(event: MouseEvent) {
    if (this.isDragging) {
      const deltaX = event.clientX - this.startX;
      this.el.nativeElement.scrollLeft = this.startScrollX - deltaX;
    }
  }
}
