import {
  HelpersFunctions,
  msgTranslate,
} from "app/shared/resources/helpers-functions";
import {
  ITrainingRequestIndexModel,
  ITrainingToDisplayModel,
} from "../models/training-request";

export function trainingRequestToDisplay(
  trainingRequests: ITrainingRequestIndexModel[]
): ITrainingToDisplayModel[] {
  return trainingRequests.map((trainingRequest) => {
    return <ITrainingToDisplayModel>{
      id: trainingRequest.id,
      fromDate: HelpersFunctions.mapDateReverse(trainingRequest.from_Date),
      toDate: HelpersFunctions.mapDateReverse(trainingRequest.to_Date),
      course_ID: trainingRequest.trA_Course.id,
      course_name: msgTranslate(
        trainingRequest.trA_Course?.ar_Name,
        trainingRequest.trA_Course?.en_Name
      ),
      course_Category_ID: trainingRequest.trA_Course?.course_Category_ID,

      reason_id: trainingRequest.trA_Reason?.id,
      reason: msgTranslate(
        trainingRequest.trA_Reason?.ar_Name,
        trainingRequest.trA_Reason?.en_Name
      ),
      note: trainingRequest.note,
    };
  });
}
