import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { IServiceModel } from "app/system-setup/general-setup/models/service";
import { ServiceService } from "app/system-setup/general-setup/services/service.service";

@Component({
  selector: "app-service-by-module-id-dropdown",
  templateUrl: "./service-by-module-id-dropdown.component.html",
  styleUrls: ["./service-by-module-id-dropdown.component.scss"],
})
export class ServiceByModuleIdDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() moduleControl: FormControl;

  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "service";
  servicesList: IServiceModel[];

  constructor(private serviceService: ServiceService) {
    super();
  }

  ngOnInit(): void {
    if (this.checkNoBusinessOrCompany()) {
    }
    this.control.disable();
    this.moduleControl.valueChanges.subscribe((moduleId) => {
      if (moduleId) {
        this.getAllService();
        this.control.enable();
      }
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getAllService();
    }
  }

  getAllService() {
    let params = {
      ...this.businessCompanyParams,
      $filter: `ModuleID eq ${this.moduleControl.value}`,
    };
    this.serviceService
      .GetAll(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IServiceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.servicesList = res.data;
        } else {
          this.errorList(res.message?.messages);
        }
      });
  }
}
