import { Component, Inject, OnInit, ViewChild } from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatStepper } from "@angular/material/stepper";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-plan-core",
  templateUrl: "./plan-core.component.html",
  styleUrls: ["./plan-core.component.scss"],
})
export class PlanCoreComponent extends AppHelpers implements OnInit {
  planId: number = 0;
  @ViewChild("stepper") stepper: MatStepper;

  updateCourseDropdown: boolean = false;
  isGeneraCompleted: boolean = false;
  isTNACompleted: boolean = false;
  isExpensisCompleted: boolean = false;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<PlanCoreComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((param) => {
      this.planId = Number(param.id) ?? 0;
    });

    if (this.data.id > 0) {
      this.planId = this.data.id;
    }
  }

  stepChanged() {
    // prevent inputs to be invalid after next & back
    this.stepper.selected.interacted = false;
  }

  getPlanId(value) {
    this.planId = value;
  }

  onCloseDialog() {
    this.dialogRef.close();
  }
}
