import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {FormControl} from "@angular/forms";
import {ICompetenceLevelModel} from "app/system-setup/org-management-setup/models/competence-level";
import {CompetenceLevelService} from "app/system-setup/org-management-setup/services/competence-level.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-competence-level-by-competence-dropdown",
  templateUrl: "./competence-level-by-competence-dropdown.component.html",
  styleUrls: ["./competence-level-by-competence-dropdown.component.scss"],
})
export class CompetenceLevelByCompetenceDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() competenceControl: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "competenceLevel";
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<ICompetenceLevelModel[]>();
  // viewName: string;
  competenceLevelList: ICompetenceLevelModel[] = [];

  constructor(private competenceLevelService: CompetenceLevelService) {
    super();
    // this.viewName = ViewsManager.add_competence_details;
  }

  ngOnInit(): void {
    this.control.disable();

    this.competenceControl?.valueChanges.subscribe((competenceId) => {
      this.control.setValue(null);
      if (competenceId > 0) {
        this.getAllCompetenceLevelsByCompetence();
        if (!this.disable) this.control.enable();
      } else this.control.disable();
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.competenceControl.value > 0 && this.checkNoBusinessOrCompany())
      this.getAllCompetenceLevelsByCompetence();
  }

  getAllCompetenceLevelsByCompetence() {
    this.competenceLevelService
      .getAllByCompetenceId(this.competenceControl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICompetenceLevelModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.competenceLevelList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(competenceLevel: ICompetenceLevelModel[]) {
    this.change.emit(competenceLevel);
  }

  // createDialog(value: string) {
  //   this.dialogsService
  //     .addFromDropdownDialog(
  //       /* add competence details */,
  //       value,
  //       this.competenceControl.value
  //     )
  //     .afterClosed()
  //     .subscribe((res) => {
  //       this.getAllCompetenceLevelsByCompetence();
  //     });
  // }
}
