import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { LoanTypesComponent } from "app/system-setup/payroll-setup/components/loan types/loan-types/loan-types.component";
import { ILoanTypeModel } from "app/system-setup/payroll-setup/models/loan-types";
import { LoanTypesService } from "app/system-setup/payroll-setup/services/loan-types.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
	selector: "app-loan-types-dropdown",
	templateUrl: "./loan-types-dropdown.component.html",
	styleUrls: ["./loan-types-dropdown.component.scss"],
})
export class LoanTypesDropdownComponent extends BaseDropdown implements OnInit {
	@Input() control: FormControl;
	@Input() submitted: boolean = false;
	@Input() multi: boolean = false;
	@Input() filter: boolean = false;
	@Input() label: string = "loanType";
	loanTypeList: ILoanTypeModel[];
	viewName: string;

	constructor(private loanTypesService: LoanTypesService) {
		super();
		this.viewName = ViewsManager.loan_types;
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getAllLoanTypes();
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.checkNoBusinessOrCompany()) this.getAllLoanTypes();
	}

	getAllLoanTypes() {
		this.loanTypesService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ILoanTypeModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.loanTypeList = res.data;
        } else this.errorList(res?.message?.messages);
      });
	}

	createDialog(value: string) {
		this.dialogsService
			.addFromDropdownDialog(LoanTypesComponent, value)
			.afterClosed()
			.subscribe((res) => {
				this.getAllLoanTypes();
			});
	}
}
