import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { IGUIModel, IRowActionsModel } from "app/shared/interfaces/user-views";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-row-functions",
  templateUrl: "./row-functions.component.html",
  styleUrls: ["./row-functions.component.scss"]
})
export class RowFunctionsComponent implements OnInit {
  @Input() actions: IRowActionsModel;
  @Input() element: any;
  @Input() iconSize: string = "font-medium-1";
  @Input() pageIndex: number = 1;
  @Output() deleteRow = new EventEmitter<any>();
  @Output() editRow = new EventEmitter<any>();
  @Output() onAction = new EventEmitter<any>();
  appSettings: AppHelpers = new AppHelpers();
  parentView: string;
  params;

  constructor() {
  }

  ngOnInit(): void {
    this.parentView = this.appSettings.getViewName;
    this.params = {
      parentView: this.parentView,
      page: this.pageIndex,
      ...this.appSettings.parentModuleIdParam,
    };
  }

  onActionClicked(fun: IGUIModel, data?: any,) {
    if (fun.name.toLocaleLowerCase().search("edit") != -1) {
      this.edit(data, fun);
    }
    if (fun.name.toLocaleLowerCase().search("delete") != -1) {
      this.delete(data);
    } else {
      this.onAction.emit({data, fun: fun});
    }
  }


  edit(data, fun: IGUIModel) {
    //let row = {} as any;
    if (data) {
      data.function = fun;
    }
    this.editRow.emit(data);
  }

  // delete row event
  delete(data) {
    this.deleteRow.emit(data);
  }

}
