import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IJobStatusEventModel } from "app/system-setup/hr-setup/models/emp-job-status";
import { EmpJobStatusService } from "app/system-setup/hr-setup/services/emp-job-status.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-job-status-event-by-status-dropdown",
  templateUrl: "./job-status-event-by-status-dropdown.component.html",
  styleUrls: ["./job-status-event-by-status-dropdown.component.scss"],
})
export class JobStatusEventByStatusDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() statusControl: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "statusEvent";
  @Input() disable: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<IJobStatusEventModel[]>();

  // added from current structure
  statusEventList: IJobStatusEventModel[] = [];

  constructor(private empJobStatusService: EmpJobStatusService) {
    super();
  }

  ngOnInit(): void {
    this.control.disable();

    this.statusControl?.valueChanges.subscribe((statusId) => {
      this.control.setValue(null);
      if (statusId > 0) {
        this.getAllStatusEventByStatus();
        if(!this.disable) this.control.enable();
      } else this.control.disable();
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.statusControl.value > 0 && this.checkNoBusinessOrCompany())
      this.getAllStatusEventByStatus();
  }

  getAllStatusEventByStatus() {
    this.empJobStatusService
      .getAllStatusEvents(this.statusControl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IJobStatusEventModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.statusEventList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(statusEvent: IJobStatusEventModel[]) {
    this.change.emit(statusEvent);
  }
}
