import {AfterViewInit, Component, EventEmitter, Input, Output} from '@angular/core';
import {FormControl} from '@angular/forms';
import {HireDocumentsComponent} from 'app/system-setup/hr-setup/components/documents/hire-documents/hire-documents.component';
import {attachmentToMap} from 'app/system-setup/hr-setup/mapper/hire-document';
import {IHireDocumentsModel} from 'app/system-setup/hr-setup/models/hire-documents';
import {HireDocumentsService} from 'app/system-setup/hr-setup/services/hire-documents/hire-documents.service';
import {MessageTypes} from 'app/shared/enums/message-types.enum';
import {IResponse} from 'app/shared/interfaces/responces';
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import {ViewsManager} from 'app/shared/resources/views-manager';
import { IAttachmentTypesModel } from 'app/system-setup/general-setup/models/attachment-types';

@Component({
  selector: 'app-hiring-document-by-service-dropdown',
  templateUrl: './hiring-document-by-service-dropdown.component.html',
  styleUrls: ['./hiring-document-by-service-dropdown.component.scss'],
})
export class HiringDocumentByServiceDropdownComponent extends BaseDropdown implements AfterViewInit {
  @Input() control: FormControl;
  @Input() serviceControl?: FormControl;
  @Input() submitted: boolean = false;
  @Input() multiple: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = 'hiringDocument';
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IAttachmentTypesModel[]>();
  viewName: string;

  serviceId?: number;
  hiringDocumentList: IAttachmentTypesModel[];

  constructor(private hireDocumentsService: HireDocumentsService) {
    super();
    this.viewName = ViewsManager.hire_documents;
    this.serviceId = this.paramsService.serviceId$.getValue();
  }


  ngAfterViewInit() {
    // console.log(this.serviceControl)
    if (!this.serviceControl) {
      this.paramsService.serviceId$.subscribe(serviceId => {
        // console.log('serviceId$.subscribe', serviceId)
        this.serviceId = serviceId
        if (serviceId > 0) {
          this.getAllHiringDocument();
        }
      })
    } else {
      this.control.disable();

      this.serviceControl?.valueChanges.subscribe((serviceId) => {
        this.serviceId = serviceId;
        this.control.setValue(null);
        if (serviceId > 0) {
          this.getAllHiringDocument();
          if (!this.disable) {
            this.control.enable();
          }
        } else {
          this.control.disable();
        }
      });
    }
  }

  // ngOnInit(): void {
  // this.paramsService.serviceId$.subscribe(serviceId => {
  //   console.log('serviceId$.subscribe', serviceId)
  //   this.serviceId = serviceId
  //   if (serviceId > 0) {
  //     this.getAllHiringDocument();
  //   }
  // })
  // for employee
  // if (this.serviceId > 0) {
  //   return;
  // }
  // if (!this.serviceControl) {
  //   this.serviceId = this.paramsService.serviceId$.getValue();
  //   this.hiringDocumentList = null;
  //   this.getAllHiringDocument();
  // } else {
  //   this.control.disable();
  //
  //   this.serviceControl?.valueChanges.subscribe((serviceId) => {
  //     this.serviceId = serviceId;
  //     this.control.setValue(null);
  //     if (serviceId > 0) {
  //       this.getAllHiringDocument();
  //       if (!this.disable) {
  //         this.control.enable();
  //       }
  //     } else {
  //       this.control.disable();
  //     }
  //   });
  // }
//  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.serviceId > 0) {
      this.getAllHiringDocument();
    }
  }

  getAllHiringDocument() {
    // console.log(this.serviceId)
    this.hireDocumentsService
      .GetAllByServiceId(this.serviceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IHireDocumentsModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.hiringDocumentList = attachmentToMap(res.data);
        } else {
          this.errorList(res.message?.messages);
        }
      });
  }

  onChange(values: IAttachmentTypesModel[]) {
    this.change.emit(values);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(HireDocumentsComponent, value, this.serviceId)
      .afterClosed()
      .subscribe((res) => {
        this.getAllHiringDocument();
      });
  }
}
