import { Component, Input, OnInit, SimpleChanges } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { ViewsManager } from "app/shared/resources/views-manager";
import { planCourseExpenseToDisplay } from "app/training/mapper/training-plan";
import {
  IPlanCourseExpensesDeleteParams,
  IPlanCourseExpensesModel,
  IPlanCourseExpensesRequest,
} from "app/training/models/training-plan/plan-courses-expenses";
import { PlanExpensesService } from "app/training/services/traning-plan/plan-expenses.service";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-plan-expenses",
  templateUrl: "./plan-expenses.component.html",
  styleUrls: ["./plan-expenses.component.scss"],
})
export class PlanExpensesComponent extends AppHelpers implements OnInit {
  @Input() planId: number = 0;
  @Input() planCourseId: number = 0;

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    // "Ser",
    "expense",
    "value",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;
  dataSource: MatTableDataSource<IPlanCourseExpensesModel>;

  constructor(private planExpensesService: PlanExpensesService) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      // id: [0, { nonNallable: true }],
      expenseId: [null, [Validators.required]],
      budget: [null, [Validators.required, CustomValidators.gte(0)]],
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes.planCourseId?.currentValue > 0 &&
      this.checkNoBusinessOrCompany()
    )
      this.getAll();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);

    if (this.checkNoBusinessOrCompany() && this.planCourseId > 0) this.getAll();
    else this.isLoaded = true;
  }

  override onModulesChanged(): void {
    this.rowFunctions = this.getSubRowFunctions(
      this.getParentViewName,
      ViewsManager.plan_course_expense
    );
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.planExpensesService
      .getAll(this.planCourseId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPlanCourseExpensesModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            planCourseExpenseToDisplay(res.data)
          );
          this.isLoaded = true;
        } else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IPlanCourseExpensesRequest = {
        plan_Course_ID: this.planCourseId,
        expense_ID: this.form.value.expenseId,
        expense_Value: this.form.value.budget,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IPlanCourseExpensesRequest) {
    this.showLoading("expenses");
    this.planExpensesService
      .create(model)
      .subscribe((res: IResponse<IPlanCourseExpensesModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.clearForm();
          this.getAll();
        } else this.errorList(res?.message?.messages);
        this.hideLoading("expenses");
      });
  }

  onDelete(expense: IPlanCourseExpensesModel): void {
    let msg = expense.expense + " - " + expense.value;
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(expense);
    });
  }

  delete(expense: IPlanCourseExpensesModel) {
    let deleteParams: IPlanCourseExpensesDeleteParams = {
      plan_course_id: expense.plan_Course_ID,
      expense_id: expense.expense_ID,
    };
    this.showLoading("expenses");
    this.planExpensesService
      .Delete(deleteParams)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        } else this.errorList(res?.message?.messages);
        this.hideLoading("expenses");
      });
  }

  clearForm() {
    this.form.reset(/* { id: 0 } */);
    this.submitted = false;
  }
}
