import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";

import {
  ILocVersion,
  ILocVersionRequest,
  ILocVersionsGetAllDropdown,
} from "app/system-setup/localization-setup/models/localization-version";
import { LocVersionService } from "app/system-setup/localization-setup/services/localization-version.service";
import { Params } from "@angular/router";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";

@Component({
  selector: "app-add-version",
  templateUrl: "./loc-add-version.component.html",
  styleUrls: ["./loc-add-version.component.scss"],
})
export class LocAddVersionComponent
  extends AppHelpers
  implements OnInit
{
  versionId: number = 0;
  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private versionService: LocVersionService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<LocAddVersionComponent>
  ) {
    super();
    this.activatedRoute.params.subscribe((params: Params) => {
      this.versionId = Number(params.id ?? 0);
    });
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [this.versionId, { nonNullable: true }],
      name: [null, [Validators.required]],
      date: [null, [Validators.required]],
      active: [false, [Validators.required]],
      country_ID: [null, [Validators.required]],
      OldVersionID: [null],
      note: [null],
    });

    if (this.data.value) {
      this.formCtrls.name.setValue(this.data.value);
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.versionId > 0) this.getById();
  }

  getById() {
    this.showLoading();
    this.versionService
      .getById(this.versionId)
      .subscribe((res: IResponse<ILocVersionsGetAllDropdown[]>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.form.patchValue(res.data[0]);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      let body: ILocVersionRequest = {
        OldVersionID: this.formCtrls.OldVersionID.value,
        Version: {
          id: this.formCtrls.id.value,
          name: this.formCtrls.name.value,
          date: HelpersFunctions.mapDateReverse(this.formCtrls.date.value),
          active: this.formCtrls.active.value,
          country_ID: this.formCtrls.country_ID.value,
          note: this.formCtrls.note.value,
        },
      };
      if (body.Version.id == 0) this.create(body);
      else this.update(body);
    }
  }

  create(data: ILocVersionRequest) {
    this.showLoading();
    this.versionService.create(data).subscribe((res: IResponse<ILocVersion>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.versionId = Number(res.data.id);
        this.formCtrls.id.setValue(this.versionId);
        let url = `/localization-setup/${this.moduleId}/version/edit/${this.versionId}`;
        // this.navigateTo(url);
        this.addSuccessfullyMsg(res.message.messageTypeName);
        if (!this.data.isDialog) this.navigateTo(url);
        if (this.data.isDialog) this.dialogRef.close();
      } else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  update(data: ILocVersionRequest) {
    this.showLoading();
    this.versionService
      .update(data.Version)
      .subscribe((res: IResponse<ILocVersion>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res.message.messageTypeName);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({ id: 0, active: false });
    this.submitted = false;
  }
}
