import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {FormControl} from "@angular/forms";
import {ICompetenceModel} from "app/org-management/model/competence";
import {CompetenceService} from "app/org-management/services/competence.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-competence-dropdown",
  templateUrl: "./competence-dropdown.component.html",
  styleUrls: ["./competence-dropdown.component.scss"],
})
export class CompetenceDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() label: string = "competence";
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<ICompetenceModel[]>();
  // viewName: string;
  competenceList: ICompetenceModel[];

  constructor(private competenceService: CompetenceService) {
    super();
    // this.viewName = ViewsManager.add_competence;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllCompetences();
  }

  getAllCompetences() {
    this.competenceService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICompetenceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.competenceList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  onChange(competence: ICompetenceModel[]) {
    this.change.emit(competence);
  }

  // createDialog(value: string) {
  //   this.dialogsService
  //     .addFromDropdownDialog(/* add competence component */, value)
  //     .afterClosed()
  //     .subscribe((res) => {
  //       this.getAllCompetences();
  //     });
  // }
}
