import {Injectable} from "@angular/core";
import {IUserPoliciesModel, IUserPoliciesRequest} from "app/system-setup/general-setup/models/users";
import {IResponse} from "app/shared/interfaces/responces";
import {db} from "app/shared/resources/database-tabels";
import {AppClientService} from "app/shared/services/app-client.service";
import {IPolicyGroupModel} from "app/org-management/model/policy-group";

@Injectable({
  providedIn: "root",
})
export class UserPoliciesService extends AppClientService {
  constructor() {
    super();
  }

  createUserPolicy(data: IUserPoliciesRequest) {
    return this.post<IResponse<IUserPoliciesModel>>("GeneralSetup/User/SaveUser_Policies", data);
  }

  updateUserPolicy(data: IUserPoliciesRequest) {
    return this.post<IResponse<IUserPoliciesModel>>("GeneralSetup/User/EditUser_Policies", data);
  }

  getUserPolicies(userId: number) {
    return this.get<IResponse<IUserPoliciesModel[]>>("GeneralSetup/User/GetUserPolicies", {
      params: {
        $expand: `${db.uM_Users}`,
        $filter: `User_ID eq ${userId}`,
      },
    });
  }

  getUserPoliciesList(params, userId: number) {
    return this.get<IResponse<IPolicyGroupModel[]>>("GeneralSetup/User/GetUserPolicies", {
      params: {
        ...params,
        $expand: `${db.core_PolicyGroup}`,
        $filter: `User_ID eq ${userId}`,
      },
    });
  }

  DeleteUserPolicy(id: number) {
    return this.delete<IResponse>("GeneralSetup/User/DeleteUser_Policies", {
      params: {Id: id},
    });
  }
}
