import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ILocViolationTypeModel,
  ILocViolationTypeRequest,
} from "../models/localization-violation-types";
import { map } from "rxjs/operators";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class LocViolationTypesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ILocViolationTypeRequest) {
    return this.post<IResponse<ILocViolationTypeModel>>(
      "ViolationType/Create",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  update(data: ILocViolationTypeRequest) {
    return this.post<IResponse<ILocViolationTypeModel>>(
      "ViolationType/Edit",
      data,
      {
        params: this._paramsService.versionCountryParams,
      }
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ILocViolationTypeModel[]>>(
      "ViolationType/GetList",
      {
        params: {
          ...params,
          ...this._paramsService.versionCountryParams,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<ILocViolationTypeModel[]>>("ViolationType/GetAll", {
      params: this._paramsService.versionCountryParams,
    }).pipe(
      map((res) => {
        res.data.map((item: ILocViolationTypeModel) => {
          item.ar_Name = item.name;
          item.en_Name = item.name;
        });
        return res;
      })
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("ViolationType/Delete", {
      params: { Id: Id, ...this._paramsService.versionCountryParams },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
