import { Component, EventEmitter, Input, Output } from "@angular/core";
import { Page } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-prime-paginator",
  templateUrl: "./prime-paginator.component.html",
  styleUrls: ["./prime-paginator.component.scss"]
})
export class PrimePaginatorComponent {
  @Input() page: Page;
  @Input() isDialogSize: boolean = false;
  @Output() onChangePage = new EventEmitter<Page>();

  constructor() { }

  // primeng paginator
  changePage(event: {
    first: number;
    page: number;
    pageCount: number;
    rows: number;
  }) {
    // this.params.page = event.page + 1;
    // this.dataSource = null;
    // this.isLoaded = false;
    let page: Page = new Page();
    page.pageSize = event.rows;
    page.pageIndex = event.page;
    page.length = event.rows * event.pageCount;
    this.onChangePage.emit(page);
  }

}
