import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IElemenGroupModel } from "app/system-setup/payroll-setup/models/element-group";
import { ElementGroupService } from "app/system-setup/payroll-setup/services/element-group.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-element-groups-dropdown",
  templateUrl: "./element-groups-dropdown.component.html",
  styleUrls: ["./element-groups-dropdown.component.scss"],
})
export class ElementGroupsDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "elementGroup";

  elementGroupList: IElemenGroupModel[];

  constructor(private elementGroupService: ElementGroupService) {
    super();
  }

  ngOnInit(): void {
    // if (this.businessId > 0 && this.companyId > 0) this.getAllElementGroups();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllElementGroups();
  }

  getAllElementGroups() {
    this.elementGroupService
      .GetAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IElemenGroupModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.elementGroupList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }
}
