import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ICustomPolicyResponse,
  IDeletePolicyGroupDetail,
  IIntersectionEmpRequest,
  IPolicyGroupDetailModel,
  IPolicyGroupIndexModel,
  IPolicyGroupModel,
  IPolicyGroupRequest,
  IPolicyGroupType,
  LoadPoliciesParams,
} from "app/org-management/model/policy-group";
import { IPageParams } from "app/shared/interfaces/params";
import { map } from "rxjs/operators";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class PolicyGroupService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IPolicyGroupRequest) {
    return this.post <ICustomPolicyResponse>(
      "OrgManagement/PolicyGroup/Create", data
    );
  }

  edit(data: IPolicyGroupRequest) {
    return this.post<ICustomPolicyResponse>(
      "OrgManagement/PolicyGroup/Edit",
      data
    );
  }

  getAll() {
    return this.get<IResponse<IPolicyGroupModel[]>>(
      "OrgManagement/PolicyGroup/GetAll",
      {
        params: {
          $expand: db.core_PolicyGroup_Type,
        },
      }
    );
  }

  getById(policyGroupId?: number) {
    return this.get<IResponse<IPolicyGroupModel[]>>(
      "OrgManagement/PolicyGroup/GetAll",
      {
        params: {
          $expand: db.core_PolicyGroup_Type,
          $filter: `id eq ${policyGroupId}`
        },
      }
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IPolicyGroupIndexModel[]>>(
      "OrgManagement/PolicyGroup/GetList",
      {
        params: params,
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("OrgManagement/PolicyGroup/Delete", {
      params: {Id: Id},
    });
  }

  saveIntersectionEmployees(data: IIntersectionEmpRequest[]) {
    return this.post<IResponse<any>>(
      "OrgManagement/PolicyGroup/SaveIntersectionEmployees",
      data
    );
  }

  getDetailsList(params: IPageParams, PolicyGroupId: number) {
    return this.get<IResponseList<IPolicyGroupDetailModel[]>>(
      "OrgManagement/PolicyGroup/PolicyGroupGroupingDetail", {
        params: {...params, PolicyGroupId: PolicyGroupId}
      }
    );
  }

  deleteDetails(params: IDeletePolicyGroupDetail) {
    return this.delete<IResponse>("OrgManagement/PolicyGroup/DeletePolicyGroupGroupingDetail", {
      params: {
        PolicyGroupId: params.PolicyGroupId,
        itemId: params.itemId,
        gropingType: params.gropingType
      },
    });
  }


  loadPolicies(params: LoadPoliciesParams) {
    return this.get<IResponse<IPolicyGroupModel[]>>(
      "OrgManagement/PolicyGroup/loadPolicies",
      {
        params: params,
        //params: this.loadPoliciesParams(params),
      }
    );
  }

  getPolicesByCompany(params: { compId: number }) {
    return this.get<IResponse<IPolicyGroupModel[]>>(
      "OrgManagement/PolicyGroup/GetPoliceiesByCompany",
      {
        params: params,
      }
    );
  }

  loadPoliciesParams(params: LoadPoliciesParams) {
    if (params.orgId) {
      return {orgId: params.orgId};
    }
    if (params.jobTitleId) {
      return {jobTitleId: params.jobTitleId};
    }
    if (params.locatonId) {
      return {locatonId: params.locatonId};
    }
  }


  getAllPolicyGroupType() {
    return this.get<IResponse<IPolicyGroupType[]>>(
      "OrgManagement/PolicyGroup/GetAllPolicyGroupType");
  }

  exportExcel() {
    return this.getAll().pipe(
      map((res) => {
       // res.data = policyGroupDisplay(res.data);
        return res;
      })
    );
  }
}
