import { Component, EventEmitter, Input, OnChanges, OnDestroy, Output, SimpleChanges, } from "@angular/core";
import { IPerson } from "app/personnel-setup/models/employees";
import { MatTableDataSource } from "@angular/material/table";
import { EmployeesService } from "app/personnel-setup/services/employees/employees.service";
import { IResponseList } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IPageParams } from "app/shared/interfaces/params";
import { msgTranslate } from "app/shared/resources/helpers-functions";
import { Constants } from "app/shared/resources/constants";

@Component({
  selector: "app-employee-select-table",
  templateUrl: "./employee-select-table.component.html",
  styleUrls: ["./employee-select-table.component.scss"]
})
export class EmployeeSelectTableComponent extends AppHelpers implements OnChanges, OnDestroy {
  @Input() reset: boolean;
  @Output() onSelect = new EventEmitter<IPerson[]>();
  @Output() length = new EventEmitter<number>();
  employeeList: IPerson[] = [];
  selectedItemsList: IPerson[] = [];
  // is table vars
  mandatoryColomns: string[] = [
    "employee",
    Constants.selectColName,
  ];
  dataSource: MatTableDataSource<IPerson>;
  isLoaded: boolean = false;


  constructor(private employeeServ: EmployeesService) {
    super();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.reset) {
      this.dataSource?.data.map(e => e.isSelected = false);
      this.selectedItemsList = [];
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getList(this.firstPage);
    }
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) {
      this.page.pageSize = pageSize;
    }
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.employeeServ
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IPerson[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          res.data.map((e: any | IPerson) => e.employee = msgTranslate(e.ar_Name, e.en_Name));
          this.dataSource = new MatTableDataSource(res.data);
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
          this.length.emit(res.meta.totalItemCount);
          this.isLoaded = true;

          // check selected items
          this.selectedItemsList.forEach(selected => {
            let item = this.dataSource.data.find(x => x.id == selected.id);
            if (item) {
              item.isSelected = true;
            }
          });
        }
        else {
          this.errorList(res.message?.messages);
        }
      });
  }

  selectedItems(list: IPerson[]) {
    this.dataSource.data.map((ele, index) => {
      // search if item selected before
      let i = this.selectedItemsList.findIndex(x => x.id == ele.id);

      // START: select & unselect items
      if (ele.isSelected) {
        if (i < 0) {
          this.selectedItemsList.push(ele);
        }
      }
      else if (i >= 0) {
        this.selectedItemsList.splice(i, 1);
        ele.isSelected = false;
      }
    });
    this.onSelect.emit(this.selectedItemsList);
  }


  // onChange(jobTitle: IPerson[]) {
  //   this.change.emit(jobTitle);
  // }

  ngOnDestroy() {
    this.length.emit(0);
  }
}
