import { Injectable } from "@angular/core";
import { ICustodyModel } from "app/system-setup/org-management-setup/models/custodies";
import {
  IEmpCustodyModel,
  IEmpCustodyRequest,
} from "app/personnel-setup/models/emp-employment-info/custodies";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeCustodiesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmpCustodyRequest) {
    return this.post<IResponse<IEmpCustodyModel>>(
      "Personnel/Employees/Create_EmployeeCustodies",
      data
    );
  }

  update(data: IEmpCustodyRequest) {
    return this.post<IResponse<IEmpCustodyModel>>(
      "Personnel/Employees/Edit_EmployeeCustodies",
      data
    );
  }

  getList(personInstancId: number, params: IPageParams) {
    return this.get<IResponseList<IEmpCustodyModel[]>>(
      "Personnel/Employees/GetList_EmployeeCustodies",
      {
        params: {
          ...params,
          $filter: `personInstanceId eq ${personInstancId}`,
          $expand: db.core_Covenant,
        },
      }
    );
  }

  getAll(personInstancId: number) {
    return this.get<IResponse<IEmpCustodyModel[]>>(
      "Personnel/Employees/GetAll_EmployeeCustodies",
      {
        params: {
          $filter: `personInstanceId eq ${personInstancId}`,
          $expand: db.core_Covenant,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeCustodies",
      {
        params: { Id: Id },
      }
    );
  }

  getAllCustodiesForEmployee(personInstancId: number) {
    return this.get<IResponse<ICustodyModel[]>>(
      "Personnel/Employees/GetAll_CovenantForEmp",
      {
        params: {
          personInstancId: personInstancId,
        },
      }
    );
  }
}
