import { Injectable } from "@angular/core";
import {
  ICandidateWorkExperienceModel,
  ICandidateWorkExperienceRequest,
} from "app/recruitment/models/candidate/work-experience/work-experience";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class CandidateWorkExperienceService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ICandidateWorkExperienceRequest) {
    return this.post<IResponse<ICandidateWorkExperienceModel>>(
      "Recruitment/WorkingExperince/Add",
      data
    );
  }

  update(data: ICandidateWorkExperienceRequest) {
    return this.post<IResponse<ICandidateWorkExperienceModel>>(
      "Recruitment/WorkingExperince/Edit",
      data
    );
  }

  getAll(candidateId: number) {
    return this.get<IResponse<ICandidateWorkExperienceModel[]>>(
      "Recruitment/WorkingExperince/GetAll",
      {
        params: {
          $filter: `Person_Instance_ID eq ${candidateId}`,
          $expand: db.Core_Resignation_Reason,
        },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("Recruitment/WorkingExperince/Delete", {
      params: { ID: id },
    });
  }

  getAllResignationReason() {
    return this.get<IResponse<Idropdown[]>>("ResignationReason/GetAll");
  }
}
