import { Component, EventEmitter, Input, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { VacationTypeComponent } from "app/system-setup/attendance-setup/components/vacation/vacation-type/vacation-type.component";
import { IVacationModel, IVacationResponse, } from "app/system-setup/attendance-setup/models/vacation";
import { VacationService } from "app/system-setup/attendance-setup/services/vacation/vacation.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { vacationToMap } from "app/system-setup/attendance-setup/mapper/vacation";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-vacation-type-dropdown",
  templateUrl: "./vacation-type-dropdown.component.html",
  styleUrls: ["./vacation-type-dropdown.component.scss"],
})
export class VacationTypeDropdownComponent
  extends BaseDropdown
 {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "vacationType";
  @Input() appendTo: string = "body";
  @Output() change = new EventEmitter<IVacationModel[]>();
  viewsManager = ViewsManager;
  vacationList: IVacationModel[];

  constructor(private vacationServ: VacationService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllVacations();
  }

  getAllVacations() {
    this.vacationServ
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IVacationResponse[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.vacationList = vacationToMap(res.data);
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(att_Vacation: IVacationModel[]) {
    this.change.emit(att_Vacation);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(VacationTypeComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllVacations();
      });
  }
}
