import { Component, OnInit, Inject } from "@angular/core";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { IAttendancePlanRequest } from "app/system-setup/attendance-setup/models/plan";
import { AttendancePlanService } from "app/system-setup/attendance-setup/services/attendance-plan.service";
import { ShiftsService } from "app/system-setup/attendance-setup/services/shifts.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-add-plan",
  templateUrl: "./add-plan.component.html",
  styleUrls: ["./add-plan.component.scss"],
})
export class AddPlanComponent extends AppHelpers implements OnInit {
  planId: number = 0;

  constructor(
    public shiftsService: ShiftsService,
    public planService: AttendancePlanService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<AddPlanComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.planId = Number(params.get("id")) ?? 0;
    });
  }

  submit(model: IAttendancePlanRequest) {
    if (model?.plan?.id >= 0)
      model.plan.id == 0 ? this.create(model) : this.update(model);
  }

  create(model: IAttendancePlanRequest) {
    this.showLoading();
    this.planService
      .create(model)
      .subscribe((res: IResponse<IAttendancePlanRequest>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // this.planId = res.data.plan.id;
          // this.formCtrls.id.setValue(this.planId);
          // let url = `/attendance-setup/${this.moduleId}/plan/edit/${this.planId}`;
          // this.navigateTo(url);
          // console.log("plan", this.planId);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: IAttendancePlanRequest) {
    this.showLoading();
    this.planService
      .update(model)
      .subscribe((res: IResponse<IAttendancePlanRequest>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  onCloseDialog() {
    this.dialogRef.close();
  }
}
