import { Component, EventEmitter, Input, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { NationalityComponent } from "app/system-setup/hr-setup/components/nationality/nationality.component";
import { INationalityModel } from "app/system-setup/hr-setup/models/nationality";
import { NationalityService } from "app/system-setup/hr-setup/services/nationality.service";
import { ViewsManager } from "app/shared/resources/views-manager";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-nationality-dropdown",
  templateUrl: "./nationality-dropdown.component.html",
  styleUrls: ["./nationality-dropdown.component.scss"],
})
export class NationalityDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "nationality";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<INationalityModel[]>();

  nationalities: INationalityModel[];
  viewName: string;

  constructor(private nationalityService: NationalityService) {
    super();
    this.viewName = ViewsManager.nationality;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllNationalities();
  }

  getAllNationalities() {
    this.nationalityService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<INationalityModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.nationalities = res.data;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(NationalityComponent, value)
      .afterClosed()
      .subscribe(() => {
        this.getAllNationalities();
      });
  }

  onChange(values: INationalityModel[]) {
    this.change.emit(values);
  }
}
