import { NgxSpinnerService } from "ngx-spinner";
import { inject, Injectable } from "@angular/core";
import { Subject } from "rxjs";

@Injectable({
  providedIn: "root",
})
export class LoadingService {
  spinner = inject(NgxSpinnerService);
 // $spinnerName = new Subject<string>();
  $display = new Subject<boolean>();

  show(name?: string | any) {
    this.spinner.show(name ?? undefined, {
      type: "ball-triangle-path",
      size: "medium",
      bdColor: "rgba(0, 0, 0, 0.8)",
      color: "#fff",
      fullScreen: true,
    });
    // this.$spinnerName.next(name);
    // this.$display.next(true);
  }

  hide(name?: string | any) {
    this.spinner.hide(name ?? undefined);
    // this.$spinnerName.next(null);
    // this.$display.next(false);
  }

  getSpinner(name: string) {
    this.spinner.getSpinner(name);
  }
}
