import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { ContactCategoryComponent } from "app/system-setup/hr-setup/components/contact-category/contact-category.component";
import {
  IContactCategoryModel,
  IContactCategoryResponse,
} from "app/system-setup/hr-setup/models/contact-category";
import { ContactCategoryService } from "app/system-setup/hr-setup/services/contact-category.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "contact-categories-by-contact-dropdown",
  templateUrl: "./categories-by-contact-type-dropdown.component.html",
  styleUrls: ["./categories-by-contact-type-dropdown.component.scss"],
})
export class CategoriesByContactTypeDropdownComponent
  extends BaseDropdown
  implements OnInit {
  @Input() control: FormControl;
  @Input() contactTypControl: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "contactCategory";
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<Idropdown[]>();

  // added from current structure
  contactCategoryList: IContactCategoryModel[] = [];
  viewName: string;

  constructor(private contactCategoryService: ContactCategoryService) {
    super();
    this.viewName = ViewsManager.contact_category;
  }

  ngOnInit(): void {
    this.control.disable();

    this.contactTypControl?.valueChanges.subscribe((contactTypId) => {
      this.control.setValue(null);
      if (contactTypId > 0) {
        this.getAllCategoriesByContactType();
        if (!this.disable) this.control.enable();
      }
      else this.control.disable();
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.contactTypControl.value > 0 && this.checkNoBusinessOrCompany())
      this.getAllCategoriesByContactType();
  }

  getAllCategoriesByContactType() {
    this.contactCategoryService
      .getAllCategoriesByTypeId(this.contactTypControl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IContactCategoryResponse[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          const list: IContactCategoryModel[] = [];
          res.data.map((item) => {
            list.push(item.comm_Contact_Category);
          });
          this.contactCategoryList = list;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  onChange(contactCategory: Idropdown[]) {
    this.change.emit(contactCategory);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(
        ContactCategoryComponent,
        value,
        this.contactTypControl.value
      )
      .afterClosed()
      .subscribe(() => {
        this.getAllCategoriesByContactType();
      });
  }
}
