export enum Btn {
  primary = 'btn-primary',
  secondary = 'btn-secondary',
  success = 'btn-success',
  danger = 'btn-danger',
  warning = 'btn-warning',
  info = 'btn-info',
  light = 'btn-light',
  dark = 'btn-dark',

  // light
  primaryLight = 'bg-light-primary',
  secondaryLight = 'bg-light-secondary',
  successLight = 'bg-light-success',
  dangerLight = 'bg-light-danger',
  warningLight = 'bg-light-warning',
  infoLight = 'bg-light-info',

  // outlined
  primaryOutline = 'btn-outline-primary',
  secondaryOutline = 'btn-outline-secondary',
  successOutline = 'btn-outline-success',
  dangerOutline = 'btn-outline-danger',
  warningOutline = 'btn-outline-warning',
  infoOutline = 'btn-outline-info',
  lightOutline = 'btn-outline-light',
  darkOutline = 'btn-outline-dark',


  // custom buttons
  lightGreen = 'btn-lightGreen',
  darkBlue = 'btn-darkBlue',
  darkBlueBorder = 'btn-darkBlue-border', // background > white & border > darkBlue
}
