import {Injectable} from "@angular/core";
import {IResponse, IResponseList} from "app/shared/interfaces/responces";
import {AppClientService} from "app/shared/services/app-client.service";
import {IContactCategoryModel, IContactCategoryRequest, IContactCategoryResponse,} from "../models/contact-category";
import {db} from "app/shared/resources/database-tabels";
import {IPageParams} from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class ContactCategoryService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IContactCategoryRequest) {
    return this.post<IResponse<IContactCategoryResponse>>(
      "HRSetup/ContactType/Create_ContactCategory",
      data
    );
  }

  update(data: IContactCategoryRequest) {
    return this.post<IResponse<IContactCategoryResponse>>(
      "HRSetup/ContactType/Edit_ContactCategory",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IContactCategoryResponse[]>>(
      "HRSetup/ContactType/GetList_ContactCategory",
      {
        params: {
          ...params,
          $expand: `${db.comm_Contact_Category},${db.comm_Contact_Type}`,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<IContactCategoryModel[]>>("HRSetup/ContactType/GetAll_TypeCategory", {
      params: {
        $expand: `${db.comm_Contact_Category},${db.comm_Contact_Type}`,
      },
    });
  }

  getAllCategoriesByTypeId(typeId: number) {
    return this.get<IResponse<IContactCategoryModel[]>>("HRSetup/ContactType/GetAll_TypeCategory", {
      params: {
        $filter: `ContactType_ID eq ${typeId}`,
        $expand: `${db.comm_Contact_Category},${db.comm_Contact_Type}`,
      },
    });
  }

  Delete(id: number) {
    return this.delete<IResponse>("HRSetup/ContactType/Delete_ContactCategory", {
      params: {Id: id},
    });
  }

  exportExcel() {
    this.getAll();
  }
}
