import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import {
  EmployeeGeneralInfoDialogComponent
} from "app/personnel-setup/components/employees/employee-general-info/emp-general-info-dialog/emp-general-info-dialog.component";
import { IPerson } from "app/personnel-setup/models/employees";
import { EmployeesService } from "app/personnel-setup/services/employees/employees.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";
import { MatTableDataSource } from "@angular/material/table";
import { Constants } from "app/shared/resources/constants";

@Component({
  selector: "app-employee-dropdown",
  templateUrl: "./employee-dropdown.component.html",
  styleUrls: ["./employee-dropdown.component.scss"],
})
export class EmployeeDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl | AbortController;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() isTable: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() label: string = "employees";
  @Output() change = new EventEmitter<IPerson[]>();
  viewName: string;
  employeeList: IPerson[];

  // is table vars
  mandatoryColomns: string[] = [
    "ar_Name",
    Constants.selectColName,
  ];
  dataSource: MatTableDataSource<IPerson>;
  dataSource1: MatTableDataSource<IPerson>;
  dataSource2: MatTableDataSource<IPerson>;
  isLoaded: boolean = false;


  constructor(private employeeServ: EmployeesService) {
    super();
    // add parent viewname beacuse it does  not have route

    this.viewName = ViewsManager.add_user;
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getAllEmployees();
    }
  }

  getAllEmployees() {
    this.employeeServ
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPerson[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeList = res.data;
          //  let len = res.data?.length / 3;
          let list = [];
          let list1 = [];
          let list2 = [];
          // res.data.forEach((e, i) => {
          //
          //   if (i <= len) {
          //     console.log("step 1", i);
          //     list.push(e);
          //   }
          //
          //   if (i > len && i <= (len * 2)) {
          //     console.log("step 2", i);
          //     list1.push(e);
          //   }
          //
          //   if (i > (len * 2) && i < (len * 3)) {
          //     console.log("step 3", i);
          //     list2.push(e);
          //   }
          //
          // });

          this.dataSource = new MatTableDataSource(list);
          this.dataSource1 = new MatTableDataSource(list1);
          this.dataSource2 = new MatTableDataSource(list2);

          // console.log(list.length);
          // console.log(list1.length);
          // console.log(list2.length);

          // this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        }
        else {
          this.errorList(res.message?.messages);
        }
      });
  }

  onChange(jobTitle: IPerson[]) {
    this.change.emit(jobTitle);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(EmployeeGeneralInfoDialogComponent, value, null, {
        //  panelClass: "p-2",
        height: "550px"
      })
      .afterClosed()
      .subscribe((res) => {
        this.getAllEmployees();
      });
  }
}
