import {
  Component,
  EventEmitter,
  Input, OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
} from "@angular/core";
import { FormControl } from "@angular/forms";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { LanguageService } from "app/shared/services/language.service";
import { MultiSelect } from "primeng/multiselect";
import { Constants } from "app/shared/resources/constants";

@Component({
  selector: "app-prime-dropdown",
  templateUrl: "./dropdown-core.component.html",
  styleUrls: ["./dropdown-core.component.scss"],
})
export class DropdownCoreComponent extends AppHelpers implements OnInit, OnChanges {
  @Input() control: FormControl;
  @Input() label: string;
  @Input() List: Idropdown[] = null;
  @Input() multi: boolean = false;
  @Input() submitted: boolean = false;
  @Input() filter: boolean = true;
  @Input() readonly: boolean = false;
  @Input() disable: boolean = false;
  @Input() optionDisabled: string = null;
  @Input() class: string;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() viewName: string;
  @Output() change = new EventEmitter<any>();
  @Output() createNewDialog = new EventEmitter<any>();
  @Input() alloweCreateNew: boolean = false;
  @ViewChild("multiSelectRef") multiSelectRef!: MultiSelect;
  isFloatLabel: boolean = Constants.defaultFloatLabel;

  filterValue: string;
  isLoaded: boolean = false;

  constructor(public languageService: LanguageService) {
    super();
  }

  ngOnInit() {
    this.control?.valueChanges.subscribe((id) => {
      this.onChange(id);
    });
    this.alloweCreateNew = this.isViewAllowed(this.viewName);
    // if (this.disable) this.control.disable();
    // else this.control.enable();
  }

  ngOnChanges(changes: SimpleChanges) {
    // console.log("List", this.List);
    if (this.List?.length >= 0) this.isLoaded = true;
    if (changes.List?.previousValue?.length == 0 && changes.List?.currentValue?.length != 0) {
      setTimeout(() => {
        this.control.markAsPristine();
      }, 100);
      // if list get its value after control
      if (this.control.value > 0) this.onChange(this.control.value);
    }
    if (changes.List?.currentValue?.length > 0 && this.filterValue && this.multi) {
      this.multiSelectRef.filterValue = '';
      setTimeout(() => {
        this.multiSelectRef.filterValue = this.filterValue;
      }, 100);
    }

    if (changes.disable?.currentValue == true) this.control?.disable();
    else if (changes.disable?.previousValue && changes.disable?.currentValue == false)
      this.control?.enable();
  }

  override onModulesChanged() {
    this.alloweCreateNew = this.isViewAllowed(this.viewName);
  }

  onChange(value: any) {
    // console.log("value", value);
    let selectedObject: any[] = [];
    if (value) {
      if (value?.length > 1) selectedObject = this.List?.filter((ele) => value.includes(ele.id));
      else selectedObject = this.List?.filter((ele) => ele?.id == value);
    }

    // console.log(selectedObject);
    this.change.emit(selectedObject);
  }

  onCreateDialog(value: string) {
    this.createNewDialog.emit(value);
    this.multiSelectRef.hide(); // to hide create new button when dialog is opened
  }

  onFilter($event) {
    this.filterValue = $event.filter;
  }

  isItemDisabled(id: any): boolean {
    return this.List?.find((item) => item.id == id)?.isDisabled ?? false;
  }
}
