import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IMaritalStatusModel } from "../models/marital-status";

@Injectable({
  providedIn: "root",
})
export class MaritalStatusService extends AppClientService {
  constructor() {
    super();
  }

  getAll() {
    return this.get<IResponse<IMaritalStatusModel[]>>(
      "HRSetup/MaritalStatus/GetAll"
    );
  }
}
