import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { KpiComponent } from "app/system-setup/hr-setup/components/kpi/kpi.component";
import { IKpiModel } from "app/system-setup/hr-setup/models/kpi";
import { KpiService } from "app/system-setup/hr-setup/services/kpi.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-kpi-dropdown",
  templateUrl: "./kpi-dropdown.component.html",
  styleUrls: ["./kpi-dropdown.component.scss"],
})
export class KpiDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "kpi";

  kpiList: IKpiModel[];
  viewName: string;

  constructor(private kpiService: KpiService) {
    super();
    this.viewName = ViewsManager.kpi;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.kpiService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IKpiModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.kpiList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(KpiComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }
}
