import { Component, Input, OnInit, SimpleChanges } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
  IS4SystemInstanceMethodModel,
  IS4SystemInstanceMethodRequest,
} from "app/system-setup/s4-setup/models/s4-system-instance";
import { S4SystemInstanceMethodsService } from "app/system-setup/s4-setup/services/s4-system-instance-methods.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-s4-system-instance-methods",
  templateUrl: "./s4-system-instance-methods.component.html",
  styleUrls: ["./s4-system-instance-methods.component.scss"],
})
export class S4SystemInstanceMethodsComponent
  extends AppHelpers
  implements OnInit
{
  @Input() s4SystemInstanceId: number;

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    "id",
    "name",
    "api_Method_Name",
    "actions",
  ];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<IS4SystemInstanceMethodModel>;

  constructor(
    private s4SystemInstanceMethodsService: S4SystemInstanceMethodsService
  ) {
    super();
  }

  ngOnInit(): void {
    // if (this.s4SystemInstanceId && this.CheckNoBusinessOrCompany())
    //   this.getAll();

    this.form = this.fb.group({
      // id: [0, { nonNullable: true }],
      name: [null, [Validators.required]],
      api_Method_Name: [null, [Validators.required]],
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    // if (changes.s4SystemInstanceId.currentValue > 0) this.getAll();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.s4SystemInstanceId && this.checkNoBusinessOrCompany())
      this.getAll();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.s4SystemInstanceMethodsService
      .getAllBySystemInstanceId(this.s4SystemInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IS4SystemInstanceMethodModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        } else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IS4SystemInstanceMethodRequest = {
        S4_instance_Id: this.s4SystemInstanceId,
        // ID: this.form.value.id,
        Name: this.form.value.name,
        Api_Method_Name: this.form.value.api_Method_Name,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IS4SystemInstanceMethodRequest) {
    this.showLoading();
    this.s4SystemInstanceMethodsService
      .create(model)
      .subscribe((res: IResponse<IS4SystemInstanceMethodModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  onDelete(systemInstanceMethod: IS4SystemInstanceMethodModel): void {
    let msg = this.msgTranslate(systemInstanceMethod.name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(systemInstanceMethod);
    });
  }

  delete(systemInstanceMethod: IS4SystemInstanceMethodModel) {
    this.showLoading();
    this.s4SystemInstanceMethodsService
      .Delete(systemInstanceMethod.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset(/* { id: 0 } */);
    this.submitted = false;
  }
}
