import { HttpClient, HttpContext, HttpHeaders, HttpParams } from "@angular/common/http";
import { inject, Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { ParamsService } from "./params.service";
import { IBusinessCompanyParams } from "../interfaces/params";
import * as CryptoJS from "crypto-js";
import { EncryptDecryptDataService } from "app/shared/services/encrypt-decrypt-data.service";

@Injectable({
  providedIn: "root",
})
export class AppClientService {
  https = inject(HttpClient);
  encryptDataService = inject(EncryptDecryptDataService);
  public _paramsService = inject(ParamsService);
  businessCompanyParams: IBusinessCompanyParams;

  constructor() {
    this._paramsService.businessCompanyParams$.subscribe((res) => {
      this.businessCompanyParams = res;
    });
  }

  // RT means response type
  // BT means Body type

  baseUrl = `${environment.baseUrl}/api/`;

  get<RT>(url: string, options?: optionsModel, hasGeneralParams: boolean = true) {
    let opt = this.mergeOptions(options, hasGeneralParams);
    let encryptUrl = this.encryptDataService.encryptUsingAES256(this.baseUrl + url);
    return this.https.get<RT>(this.baseUrl + url, opt);
  }

  post<RT>(url: string, body: any, options?: optionsModel, hasGeneralParams: boolean = true) {
    let opt = this.mergeOptions(options, hasGeneralParams);
    return this.https.post<RT>(this.baseUrl + url, body, opt);
  }

  put<RT>(url: string, body: any, options?: optionsModel, hasGeneralParams: boolean = true) {
    let opt = this.mergeOptions(options, hasGeneralParams);
    return this.https.put<RT>(this.baseUrl + url, body, opt);
  }

  delete<RT>(url: string, options?: optionsModel, hasGeneralParams: boolean = true) {
    let opt = this.mergeOptions(options, hasGeneralParams);
    return this.https.delete<RT>(this.baseUrl + url, opt);
  }

  mergeOptions(options: optionsModel, hasGeneralParams: boolean): optionsModel {
    var mergedOptions: optionsModel;
    if (hasGeneralParams) {
      mergedOptions = {
        ...options,
        params: {
          ...options?.params,
          ...this.businessCompanyParams,
        },
      };
      return mergedOptions;
    }
    else return options;
  }


}

export interface optionsModel {
  headers?: HttpHeaders;
  params?: HttpParams | any;
  context?: HttpContext;
  body?: any;
}
