import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { IViolationTypeModel } from "app/system-setup/penalty-regulations-setup/models/violation-types";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { LocViolationTypesService } from "app/system-setup/localization-setup/services/localization-violation-types.service";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { FormControl } from "@angular/forms";
import { ViewsManager } from "app/shared/resources/views-manager";
import {
  LocViolationTypesComponent
} from "app/system-setup/localization-setup/components/localization-violation-types/loc-violation-types.component";
import { ILocViolationTypeModel } from "app/system-setup/localization-setup/models/localization-violation-types";

@Component({
  selector: "local-violation-types",
  templateUrl: "./local-violation-types.component.html",
  styleUrls: ["./local-violation-types.component.scss"],
})
export class LocalViolationTypesComponent
  extends BaseDropdown
  implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = true;
  @Input() setDefaultVal: boolean = false;
  @Input() label: string = "violationType";
  @Input() appendTo: string | HTMLElement | any;
  @Output() change = new EventEmitter<IViolationTypeModel[]>();

  violationTypesList: ILocViolationTypeModel[] = [];
  viewName: string;

  constructor(private violationTypesService: LocViolationTypesService) {
    super();
    this.viewName = ViewsManager.violation_types;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.checkNoVersionOrCountry())
      this.getAll();
  }

  override onChangeVersionOrCountry() {
    if (this.checkNoBusinessOrCompany() && this.checkNoVersionOrCountry())
      this.getAll();
  }

  ngOnInit(): void {}

  getAll() {
    this.violationTypesService
      .getAll()
      .subscribe((res: IResponse<ILocViolationTypeModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.violationTypesList = res.data;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(LocViolationTypesComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }

  onChange(Violation: IViolationTypeModel[]) {
    this.change.emit(Violation);
  }
}
