import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {FormControl} from "@angular/forms";
import {ICustodyModel} from "app/system-setup/org-management-setup/models/custodies";
import {
  EmployeeCustodiesService
} from "app/personnel-setup/services/employees/employement-information/employee-custodies.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-custodies-by-employee-dropdown",
  templateUrl: "./custodies-by-employee-dropdown.component.html",
  styleUrls: ["./custodies-by-employee-dropdown.component.scss"],
})
export class CustodiesByEmployeeDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "custodies";
  @Input() employeeInstanceId: number = 0;
  // viewName: string;
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<ICustodyModel[]>();

  custodies: ICustodyModel[];

  constructor(private employeeCustodiesService: EmployeeCustodiesService) {
    super();
    // this.viewName = ViewsManager.custodies;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.employeeInstanceId > 0)
      this.getAllCustodies();
  }

  getAllCustodies() {
    this.employeeCustodiesService
      .getAllCustodiesForEmployee(this.employeeInstanceId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICustodyModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.custodies = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  // createDialog(value: string) {
  //   this.dialogsService
  //     .addFromDropdownDialog(CustodiesComponent, value)
  //     .afterClosed()
  //     .subscribe((res) => {
  //       this.getAllCustodies();
  //     });
  // }

  onChange(values: ICustodyModel[]) {
    this.change.emit(values);
  }
}
