import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl, FormGroup } from "@angular/forms";
import { IS4CostCenter } from "app/personnel-setup/models/emp-sap-integration/s4-hana";
import { EmployeeS4HanaService } from "app/personnel-setup/services/employees/sap-integration/employee-s4-hana.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-cost-center-by-s4-system-instance-dropdown",
  templateUrl: "./cost-center-by-s4-system-instance-dropdown.component.html",
  styleUrls: ["./cost-center-by-s4-system-instance-dropdown.component.scss"],
})
export class CostCenterByS4SystemInstanceDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() s4SystemInstanceControl: FormGroup;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "costCenter";
  @Input() appendTo: string = "body";
  @Output() change = new EventEmitter<IS4CostCenter[]>();

  costCenterList: IS4CostCenter[] = [];

  constructor(private employeeS4HanaService: EmployeeS4HanaService) {
    super();
  }

  ngOnInit(): void {
    this.control.disable();

    this.s4SystemInstanceControl?.valueChanges.subscribe(
      (s4SystemInstanceId) => {
        this.control.setValue(null);

        if (s4SystemInstanceId > 0) {
          this.getAllCostCenterBySystemInstance();
          if (!this.disable) this.control.enable();
        } else this.control.disable();
      }
    );
  }

  override onChangeBusinessAndCompanyId(): void {
    if (
      this.s4SystemInstanceControl?.value > 0 &&
      this.checkNoBusinessOrCompany()
    )
      this.getAllCostCenterBySystemInstance();
  }

  getAllCostCenterBySystemInstance() {
    this.employeeS4HanaService
      .getAllS4CostCenterBySystemInstance(this.s4SystemInstanceControl?.value)
      .subscribe((res: IResponse<IS4CostCenter[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.costCenterList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(costCenter: IS4CostCenter[]) {
    this.change.emit(costCenter);
  }
}
