import { Component, Input, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { companyToMap } from "app/system-setup/general-setup/mapper/users";
import {
  IDeleteCompaniesRequest,
  IUserCompaniesModel,
  IUserCompaniesRequest,
  IUserCompanyToDisplay,
} from "app/system-setup/general-setup/models/users";

import { UserCompanyService } from "app/system-setup/general-setup/services/users/user-company.service";
import { IBusinessEntityModel } from "app/org-management/model/business-entity";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-user-companies",
  templateUrl: "./user-companies.component.html",
  styleUrls: ["./user-companies.component.scss"],
})
export class UserCompaniesComponent extends AppHelpers implements OnInit {
  @Input() userId: number;
  form: FormGroup;
  submitted: boolean = false;
  dataSource: MatTableDataSource<IUserCompanyToDisplay>;
  mandatoryColomns: string[] = ["Business_Name", "Company_Name", "actions"];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];
  businessList: IBusinessEntityModel[] = [];

  constructor(private userCompanyService: UserCompanyService) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, {nonNallable: true}],
      user_ID: [this.userId],
      company_ID: [null, [Validators.required]],
      business_Entity_ID: [null, [Validators.required]],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.userId > 0) this.getUserCompaniesList();
    else this.isLoaded = true;
  }

  getUserCompaniesList() {
    this.userCompanyService
      .getUserCompanyList(this.userId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IUserCompaniesModel[]>) => {
        if (res.data) {
          let result: IUserCompanyToDisplay[] = companyToMap(res.data);
          this.dataSource = new MatTableDataSource(result);
          this.isLoaded = true;
        }
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IUserCompaniesRequest = {
        id: this.formCtrls.id.value,
        User_ID: this.userId,
        Company_ID: this.formCtrls.company_ID.value,
        Business_Entity_ID: this.formCtrls.business_Entity_ID.value,
      };
      model.id == 0 ? this.create(model) : this.update(model);
    }
  }

  create(model: IUserCompaniesRequest) {
    this.showLoading();
    this.userCompanyService
      .createUserCompany(model)
      .subscribe((res: IResponse<IUserCompaniesModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.getUserCompaniesList();
          this.userId = res.data.user_ID;
          this.addSuccessfullyMsg(res.message.messageTypeName);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: IUserCompaniesRequest) {
    this.showLoading();
    delete model.id;
    this.userCompanyService
      .updateUserCompany(model)
      .subscribe((res: IResponse<IUserCompaniesModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.getUserCompaniesList();
          this.updateSuccessfullyMsg(res.message.messageTypeName);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  editToUserCompanies(userCompanies: IUserCompaniesModel) {
    this.form.patchValue(userCompanies);
    this.formCtrls.id.setValue(this.userId);
  }

  onDelete(company: IUserCompanyToDisplay): void {
    let msg = this.msgTranslate(company.Company_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(company);
    });
  }

  delete(company: IUserCompanyToDisplay) {
    let body: IDeleteCompaniesRequest = {
      User_ID: company.user_ID,
      Company_ID: company.company_ID,
      Business_Entity_ID: company.business_Entity_ID,
    };
    this.showLoading();
    this.userCompanyService.DeleteUserCompany(body).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.getUserCompaniesList();
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }
}
