import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ILocVersionsGetAllDropdown,
  ILocVersion,
  ILocVersionRequest,
} from "../models/localization-version";

@Injectable({
  providedIn: "root",
})
export class LocVersionService extends AppClientService {
  constructor() {
    super();
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ILocVersion[]>>("Version/GetList", {
      params: {
        ...params,
        ...this._paramsService.versionCountryParams,
      },
    });
  }

  create(data: ILocVersionRequest) {
    return this.post<IResponse<ILocVersion>>("Version/Create", data);
  }

  update(data: ILocVersion) {
    return this.post<IResponse<ILocVersion>>("Version/Edit", data);
  }

  getAll() {
    return this.get<IResponse<ILocVersionsGetAllDropdown[]>>("Version/GetAll", {
      params: this._paramsService.versionCountryParams,
    });
  }

  getById(id: number) {
    return this.get<IResponse<ILocVersionsGetAllDropdown[]>>("Version/GetAll", {
      params: {
        $filter: `id eq ${id}`,
        ...this._paramsService.versionCountryParams,
      },
    });
  }

  Delete(id: number) {
    return this.delete<IResponse>("Version/Delete", {
      params: { id: id, ...this._paramsService.versionCountryParams },
    });
  }

  exportExcel() {
    return this.getAll();
  }
}
