import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { assignJobToOrgToMap } from "app/org-management/mapper/job-title";
import {
  IAssignJobToOrgDeleteRequest,
  IAssignJobToOrgModel,
  IAssignJobToOrgRequest,
  IJobTitleModel,
} from "app/org-management/model/job-title";
import { AssignJobToOrgService } from "app/org-management/services/job-profile/assign-job-to-org.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IOrganizationTypeModel } from "app/system-setup/org-management-setup/models/organization-types";
import { OrganizationTypesService } from "app/system-setup/org-management-setup/services/organization-types.service";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-assign-job-to-org",
  templateUrl: "./assign-job-to-org.component.html",
  styleUrls: ["./assign-job-to-org.component.scss"],
})
export class AssignJobToOrgComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    "jobTitle",
    "manPowerCapacity",
    "currentEmployeeNumber",
    "remaining",
    "actions",
  ];
  isLoaded: boolean = true;
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<IAssignJobToOrgModel>;

  notAssignedJobTitleList: IJobTitleModel[];

  constructor(
    private assignJobToOrgService: AssignJobToOrgService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<AssignJobToOrgComponent>,
    private organizationTypesService: OrganizationTypesService,
  ) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      OrgTypeId: [null, [Validators.required]],
      Org_ID: [null, [Validators.required]],
      Job_Ids: [null, [Validators.required]],
      Max_Employee: [null, [Validators.required, CustomValidators.digits, CustomValidators.gte(0)]],
      IsInfinity: [null],
    });

    if(this.data.isDialog) {
      this.getOrgTypeByOrgId(this.data.id);
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) {
      this.getallJobsNotAssignedInOrg();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    let orgId = this.formCtrls.Org_ID.value;
    if (orgId > 0) {
      if (!this.data.isDialog) {
        this.isLoaded = false;
        this.assignJobToOrgService
          .getAllByOrgId(orgId)
          .pipe(this.cancelRequest())
          .subscribe((res: IResponse<IAssignJobToOrgModel[]>) => {
            if (res.message.messageType == MessageTypes.Success) {
              res.data.map((item) => {
                if (item.max_Employee == 0) {
                  item.manPowerCapacity = this.translate("open");
                  item.remaining = this.translate("unlimited");
                }
              });
              this.dataSource = new MatTableDataSource(assignJobToOrgToMap(res.data));
              this.isLoaded = true;
            }
            else this.errorList(res?.message?.messages);
          });
      }
    }
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IAssignJobToOrgRequest = {
        Org_ID: this.form.value.Org_ID,
        Job_Ids: this.form.value.Job_Ids,
        Max_Employee: this.form.value.Max_Employee,
        IsInfinity: this.form.value.IsInfinity ?? false,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

	create(model: IAssignJobToOrgRequest) {
		this.showLoading();
		this.assignJobToOrgService.create(model).subscribe((res: IResponse<IAssignJobToOrgModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
        if(this.data.isDialog) this.dialogRef.close();
				// update dropdown
				else this.getallJobsNotAssignedInOrg();
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

  onDelete(orgJob: IAssignJobToOrgModel): void {
    let msg = this.msgTranslate(orgJob.jobTitle);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(orgJob);
    });
  }

  delete(orgJob: IAssignJobToOrgModel) {
    let params: IAssignJobToOrgDeleteRequest = {
      Orgid: orgJob.org_ID,
      JobId: orgJob.job_Title_ID,
    };
    this.showLoading();
    this.assignJobToOrgService.Delete(params).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        this.getAll();
        // update dropdown
        this.getallJobsNotAssignedInOrg();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.formCtrls.Job_Ids.reset();
    this.formCtrls.Max_Employee.reset();
    this.formCtrls.IsInfinity.reset();
    this.submitted = false;
  }

  // jobs not assigned in selected org
  getallJobsNotAssignedInOrg() {
    this.notAssignedJobTitleList = [];
    let orgId = this.formCtrls.Org_ID.value ?? 0;
    // if (orgId > 0) {
    this.assignJobToOrgService
      .getAllJobTitleNotAssignedInOrgID(orgId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IJobTitleModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.notAssignedJobTitleList = res.data;
        }
        else this.errorList(res?.message?.messages);
      });
    // }
  }

	onCloseDialog() {
		this.dialogRef.close();
	}

  getOrgTypeByOrgId(orgId: number) {
    this.showLoading();
    this.organizationTypesService
      .getByOrgId(orgId)
      .subscribe((res: IResponse<IOrganizationTypeModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.formCtrls.OrgTypeId.setValue(res.data[0].id);
          this.formCtrls.Org_ID.setValue(orgId);
          this.hideLoading();
        } else this.errorList(res?.message?.messages);
      });
  }
}
