import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { ViewsManager } from "app/shared/resources/views-manager";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { LocationComponent } from "app/system-setup/attendance-setup/components/location/location.component";
import { IOrgLocationModel } from "app/org-management/model/location";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { OrgLocationService } from "app/org-management/services/org-location.service";

@Component({
  selector: "org-location-dropdown",
  templateUrl: "./org-location-dropdown.component.html",
  styleUrls: ["./org-location-dropdown.component.scss"],
})
export class OrgLocationDropdownComponent extends BaseDropdown implements OnInit {
  // ! Important
  //note: if need to get All Locations send governorateControl=null. if need to get location by governorateId send governorateControl
  @Input() governorateControl: FormControl;
  @Input() countryControl: FormControl;
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() filterByGov: boolean = false;
  @Input() filterByCountry: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "location";
  @Input() appendTo: string | HTMLElement | any;
  @Output() change = new EventEmitter<IOrgLocationModel[]>();
  locationsList: IOrgLocationModel[] = [];
  viewName: string;

  constructor(private locationService: OrgLocationService) {
    super();
    this.viewName = ViewsManager.location;
  }

  ngOnInit(): void {
    this.control.disable();
    if (this.filterByGov) {
      this.governorateControl?.valueChanges.subscribe((govId: number) => {
        this.control.setValue(null);
        if (govId > 0) {
          this.getAll(govId, null);
          if (!this.disable) this.control.enable();
        }
        else this.control.disable();
      });
    }

    if (this.filterByCountry) {
      this.countryControl?.valueChanges.subscribe((countryId: number) => {
        this.control.setValue(null);
        if (countryId > 0) {
          this.getAll(null, countryId);
          if (!this.disable) this.control.enable();
        }
        else this.control.disable();
      });
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {

      if (this.filterByGov && this.governorateControl?.value > 0)
        this.getAll(this.governorateControl.value, null);

      if (this.filterByCountry && this.countryControl?.value > 0)
        this.getAll(null, this.countryControl.value);

      else if (!this.filterByGov) this.getAll(null, null);
    }
  }

  getAll(govId: number, countryId: number) {
    this.locationService
      .getAll(govId, countryId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IOrgLocationModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.locationsList = res.data;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(LocationComponent, value)
      .afterClosed()
      .subscribe(() => {
        // this.getAll();
      });
  }
}
