import {inject, Injectable} from "@angular/core";
import {ActivatedRouteSnapshot, CanActivate, RouterStateSnapshot,} from "@angular/router";
import {ParamsService} from "app/shared/services/params.service";

@Injectable({
  providedIn: "root",
})
export class GetModuleIdGuard implements CanActivate {
  paramsService = inject(ParamsService);

  constructor() {
  }

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): boolean {
    this.paramsService.moduleId$.next(route.params.moduleId)
    this.paramsService.parentModuleId$.next(route.queryParams.parentModuleId)
    // let parentModuleId = route.queryParams.parentModuleId;
    // let moduleId = route.params.moduleId;
    // console.log('moduleId', moduleId)
    // console.log('parentModuleId', parentModuleId)
    // Storage.setParentModule(parentModuleId);
    // Storage.setSelectedModule(moduleId);


    return true;
  }
}
