import { Component, Input, OnChanges, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";

@Component({
  selector: "weekdays-checkboxes",
  templateUrl: "./weekdays-checkboxes.component.html",
  styleUrls: ["./weekdays-checkboxes.component.scss"],
})
export class WeekdaysCheckboxesComponent implements OnInit, OnChanges {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  @Input() disable: boolean = false;

  selectedItems: number[] = [];
  defControl = new FormControl();

  weekdaysList: { value: number; label: string; isChecked?: boolean }[] = [
    {
      value: 6,
      label: "saturday",
    },
    {
      value: 0,
      label: "sunday",
    },
    {
      value: 1,
      label: "monday",
    },
    {
      value: 2,
      label: "tuesday",
    },
    {
      value: 3,
      label: "wednesday",
    },
    {
      value: 4,
      label: "thursday",
    },
    {
      value: 5,
      label: "friday",
    },
  ];

  constructor() {
  }

  ngOnInit(): void {}

  ngOnChanges(): void {
    if (this.control.value.length == 0) {
      this.defControl.reset();
      this.weekdaysList = this.weekdaysList?.map((priority) => {
        return {...priority, isChecked: false};
      });
      this.selectedItems = [];
    }
  }

  onChange($event, value: number) {
    if ($event) {
      this.selectedItems.push(value);
    }
    else {
      const index = this.selectedItems.indexOf(value);
      this.selectedItems.splice(index, 1);
    }
    this.control.setValue(this.selectedItems);
  }
}
