import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {FormControl} from "@angular/forms";
import {GovernorateComponent} from "app/system-setup/hr-setup/components/governorate/governorate.component";
import {IGovernorateModel} from "app/system-setup/hr-setup/models/governorate";
import {GovernorateService} from "app/system-setup/hr-setup/services/governorate.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import {ViewsManager} from "app/shared/resources/views-manager";

@Component({
  selector: "app-governorate-by-country-dropdown",
  templateUrl: "./governorate-by-country-dropdown.component.html",
  styleUrls: ["./governorate-by-country-dropdown.component.scss"],
})
export class GovernorateByCountryDropdownComponent
  extends BaseDropdown
  implements OnInit {
  @Input() control: FormControl;
  @Input() countryControl: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "governorate";
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<IGovernorateModel[]>();
  viewName: string;

  // added from current structure
  governorateList: IGovernorateModel[] = [];

  constructor(private governorateService: GovernorateService) {
    super();
    this.viewName = ViewsManager.governorate;
  }

  ngOnInit(): void {
    this.control.disable();

    this.countryControl?.valueChanges.subscribe((countryId) => {
      this.control.setValue(null);
      if (countryId > 0) {
        this.getAllGovernorateByCountry();
        if (!this.disable) this.control.enable();
      } else this.control.disable();
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.countryControl.value > 0 && this.checkNoBusinessOrCompany())
      this.getAllGovernorateByCountry();
  }

  getAllGovernorateByCountry() {
    this.governorateService
      .getAllByCountryId(this.countryControl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IGovernorateModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.governorateList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(governorate: IGovernorateModel[]) {
    this.change.emit(governorate);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(
        GovernorateComponent,
        value,
        this.countryControl.value
      )
      .afterClosed()
      .subscribe((res) => {
        this.getAllGovernorateByCountry();
      });
  }
}
