import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { FormControl } from "@angular/forms";
import { LocViolationsService } from "app/system-setup/localization-setup/services/localization-violations.service";
import { IResponse } from "app/shared/interfaces/responces";
import { ILocViolationModel, ILocViolationResponse } from "app/system-setup/localization-setup/models/localization-violations";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { locViolationToMap } from "app/system-setup/localization-setup/mapper/localization-violations";
import { ViewsManager } from "app/shared/resources/views-manager";
import {
  LocAddViolationComponent
} from "app/system-setup/localization-setup/components/localization-violations/localization-add-violation/loc-add-violation.component";

@Component({
  selector: "loc-violation-dropdown",
  templateUrl: "./violation-dropdown.component.html",
  styleUrls: ["./violation-dropdown.component.scss"]
})
export class ViolationDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = true;
  @Input() label: string = "violations";
  @Input() appendTo: string | HTMLElement | any;
  @Output() change = new EventEmitter<ILocViolationModel[]>();
  viewName: string;
  list: ILocViolationModel[];

  constructor(private violationsService: LocViolationsService,) {
    super();
    this.viewName = ViewsManager.localization_violations_index;
  }

  ngOnInit(): void {
  }


  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  override onChangeVersionOrCountry() {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.violationsService
      .getAll()
      .subscribe((res: IResponse<ILocViolationResponse[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.list = locViolationToMap(res.data);
        }
        else this.errorList(res?.message?.messages);
      });
  }


  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(LocAddViolationComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }

  onChange(versions: ILocViolationModel[]) {
    this.change.emit(versions);
  }
}
