import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IRequestReasonModel } from "../models/request-reason";

@Injectable({
  providedIn: "root",
})
export class RequestReasonService extends AppClientService {
  constructor() {
    super();
  }

  getAll() {
    return this.get<IResponse<IRequestReasonModel[]>>(
      "Recruitment/recReason/GetAll"
    );
  }
}
