import {Component, Input} from "@angular/core";
import {FormControl} from "@angular/forms";
import {IBloodTypeModel} from "app/system-setup/hr-setup/models/blood-type";
import {BloodTypeService} from "app/system-setup/hr-setup/services/blood-type.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-blood-type-dropdown",
  templateUrl: "./blood-type-dropdown.component.html",
  styleUrls: ["./blood-type-dropdown.component.scss"],
})
export class BloodTypeDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "bloodType";
  @Input() appendTo: string | HTMLElement;

  bloodTypeList: IBloodTypeModel[];

  constructor(private bloodTypeService: BloodTypeService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllBloodTypes();
  }

  getAllBloodTypes() {
    this.bloodTypeService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: any) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.bloodTypeList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }
}
