import { IPolicyGroupToDisplay, IUserPoliciesModel } from "../models/users";
import { msgTranslate } from "app/shared/resources/helpers-functions";

export function policyToMap(
  policies: IUserPoliciesModel[]
): IPolicyGroupToDisplay[] {
  return policies.map((policy) => {
    return <IPolicyGroupToDisplay>{
      PolicyGroup_Name: msgTranslate(
        policy.i_Core_PolicyGroup_UM_User?.core_PolicyGroup?.ar_Name,
        policy.i_Core_PolicyGroup_UM_User?.core_PolicyGroup?.en_Name
      ),
      Business_Name: msgTranslate(
        policy.business_Entity?.ar_Name,
        policy.business_Entity?.en_Name
      ),
      Company_Name: msgTranslate(
        policy.company?.ar_Name,
        policy.company?.en_Name
      ),

      company_ID: policy.company?.id,
      business_Entity_ID: policy.business_Entity?.id,
      policyGroupId: policy.i_Core_PolicyGroup_UM_User?.policyGroupId,
      id: policy.i_Core_PolicyGroup_UM_User?.id,
    };
  });
}
