import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AddInstructorComponent } from "app/training/components/instructor/add-instructor/add-instructor.component";
import { IInstructorModel } from "app/training/models/instructor";
import { InstructorService } from "app/training/services/instructor.service";

@Component({
  selector: "app-instructor-dropdown",
  templateUrl: "./instructor-dropdown.component.html",
  styleUrls: ["./instructor-dropdown.component.scss"],
})
export class IstructorDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() label: string = "instructors";
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<IInstructorModel[]>();
  instructorList: IInstructorModel[] = [];
  viewName: string;

  constructor(private instructorService: InstructorService) {
    super();
    this.viewName = ViewsManager.instructor_index;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllInstructors();
  }

  getAllInstructors() {
    this.instructorService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IInstructorModel[]>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.instructorList = res.data;
        }
        else this.errorList(res.message?.messages);
      });
  }

  onChange(instructor: IInstructorModel[]) {
    this.change.emit(instructor);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(AddInstructorComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllInstructors();
      });
  }
}
