import {Injectable} from '@angular/core';
import {IResponse, IResponseList} from 'app/shared/interfaces/responces';
import {AppClientService} from 'app/shared/services/app-client.service';
import {IBusinessObjectiveModel, ITrainingReqModel, ITrainingReqRequest, ITrainingRequestIndexModel,} from '../models/training-request';
import {db} from 'app/shared/resources/database-tabels';
import {map} from 'rxjs/operators';
import {MessageTypes} from 'app/shared/enums/message-types.enum';
import {trainingRequestToDisplay} from '../mapper/training-request';
import {IPageParams} from 'app/shared/interfaces/params';

@Injectable({
  providedIn: 'root',
})
export class TrainingRequestService extends AppClientService {
  constructor() {
    super();
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ITrainingRequestIndexModel[]>>(
      'Training/TrainingRequest/GetList',
      {
        params: {...params, $expand: `${db.TRA_Course},${db.TRA_Reason}`},
      }
    );
  }

  getAll() {
    return this.get<IResponse<ITrainingRequestIndexModel[]>>(
      'Training/TrainingRequest/GetAll',
      {
        params: {
          $expand: `${db.TRA_Training_Request}.${db.TRA_Course},
                    ${db.TRA_Training_Request}.${db.TRA_Reason},
                    ${db.Core_Person_Instance}.${db.Core_Person}`,
        },
      }
    ).pipe(
      map((res: IResponse<ITrainingRequestIndexModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          return trainingRequestToDisplay(res.data);
        }
      })
    );
  }

  getById(id: number) {
    return this.get<IResponse<ITrainingReqModel>>(
      'Training/TrainingRequest/GetTrainingRequest',
      {
        params: {ID: id},
      }
    );
  }

  create(data: ITrainingReqRequest) {
    return this.post<IResponse<ITrainingReqRequest>>('Training/trainingrequest/Create', data);
  }

  update(data: ITrainingReqRequest) {
    return this.post<IResponse<ITrainingReqRequest>>('Training/trainingrequest/Edit', data);
  }

  Delete(Id: number) {
    return this.delete<IResponse>('Training/trainingrequest/Delete', {
      params: {Id: Id},
    });
  }

  loadBusinessObjective() {
    return this.get<IResponse<IBusinessObjectiveModel[]>>('Training/TrainingRequest/LoadBusinessObjectiveDropDownList');
  }

  exportExcel() {
    return this.getList({pageSize: 10, PageNumber: 1});
  }
}
