import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";
import { PlanCoreComponent } from "app/training/components/training-plan/add-paln/plan-core/plan-core.component";
import { IPlanCourseModel } from "app/training/models/training-plan/plan-courses-expenses";
import { PlanCoursesService } from "app/training/services/traning-plan/plan-courses.service";

@Component({
  selector: "app-course-by-training-plan-dropdown",
  templateUrl: "./course-by-training-plan-dropdown.component.html",
  styleUrls: ["./course-by-training-plan-dropdown.component.scss"],
})
export class CourseByTrainingPlanDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() planControl: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "planCourse";
  @Input() appendTo: string | HTMLElement = "body";
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<IPlanCourseModel[]>();
  courseList: IPlanCourseModel[] = [];
  viewName: string;

  constructor(private courseService: PlanCoursesService) {
    super();
    this.viewName = ViewsManager.add_training_plan;
  }

  ngOnInit(): void {
    this.control.disable();
    this.planControl.valueChanges.subscribe((planId) => {
      this.control.setValue(null);
      if (planId > 0) {
        this.getAllCourseByPlanId();
        if (!this.disable) this.control.enable();
      } else this.control.disable();
    });
  }

  getAllCourseByPlanId() {
    this.courseService
      .getAll(this.planControl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPlanCourseModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.courseList = res.data;
        } else {
          this.errorList(res.message?.messages);
        }
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(PlanCoreComponent, value, this.planControl.value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllCourseByPlanId();
      });
  }

  onChange(course: IPlanCourseModel[]) {
    this.change.emit(course);
  }
}
