import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AbstractControl, FormControl} from '@angular/forms';
import {MessageTypes} from 'app/shared/enums/message-types.enum';
import {IResponse} from 'app/shared/interfaces/responces';
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import {TrainingRequestService} from 'app/training/services/training-request.service';
import {IBusinessObjectiveModel} from 'app/training/models/training-request';

@Component({
  selector: 'business-objective-dropdown',
  templateUrl: './business-objective-dropdown.component.html',
  styleUrls: ['./business-objective-dropdown.component.scss']
})
export class BusinessObjectiveDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl | AbstractControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = 'businessObjective';
  @Input() appendTo: string | HTMLElement = 'body';
  @Output() change = new EventEmitter<IBusinessObjectiveModel[]>();

  //viewName: string;
  objectiveList: IBusinessObjectiveModel[];

  constructor(private trainingRequestService: TrainingRequestService) {
    super();
  }

  ngOnInit(): void {
  }

  onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.loadBusinessObjective();
    }
  }

  loadBusinessObjective() {
    this.trainingRequestService
      .loadBusinessObjective()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IBusinessObjectiveModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.objectiveList = res.data;
        } else {
          this.errorList(res?.message?.messages);
        }
      });
  }

  onChange(model: IBusinessObjectiveModel[]) {
    this.change.emit(model);
  }

}
