import { msgTranslate } from "app/shared/resources/helpers-functions";
import { IGovernorateModel } from "../models/governorate";

export function governorateToMap(governorates: IGovernorateModel[]): IGovernorateModel[] {
	return governorates.map((governorate) => {
		return <IGovernorateModel>{
			...governorate,
			country: msgTranslate(governorate.comm_Country?.ar_Name, governorate.comm_Country?.en_NAme),
		};
	});
}
