import { ISapSystemInstancePurposeModel } from "../models/system-instance";

export function sapSystemInstancePurposeToMap(
  purposes: ISapSystemInstancePurposeModel[]
): ISapSystemInstancePurposeModel[] {
  return purposes.map((purpose) => {
    return <ISapSystemInstancePurposeModel>{
      ...purpose,
      purpose: purpose.name,
    };
  });
}
