import { Injectable } from "@angular/core";
import { IAttendancePlanModel } from "app/system-setup/attendance-setup/models/plan";
import { IAssignPlanEmployeesRequest } from "app/personnel-setup/models/assign-plan";
import { IEmployeeWorkScheduleModel } from "app/personnel-setup/models/emp-employment-info/work-schedule";
import { IPersonInstanceModel } from "app/personnel-setup/models/employees";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class AssignPlanService extends AppClientService {
  constructor() {
    super();
  }

  getPlansByPolicyGroup(policyGroupId: number) {
    return this.get<IResponse<IAttendancePlanModel[]>>(
      "Personnel/Employees/GetPlanByPolicyGroup",
      {
        params: { PolicyGroupID: policyGroupId },
      }
    );
  }

  getEmployeesByPolicyGroup(policyGroupId: number) {
    return this.get<IResponse<IPersonInstanceModel[]>>(
      "Personnel/Employees/GetEmployeesByPolicyGroup",
      {
        params: { PolicyGroupID: policyGroupId },
      }
    );
  }

  getList(planId: number, params: IPageParams) {
    return this.get<IResponse<IEmployeeWorkScheduleModel[]>>(
      "Personnel/Employees/GetList_EmployeeAttendencePlan",
      {
        params: {
          ...params,
          $filter: `Plan_ID eq ${planId}`,
          $expand: `${db.att_Plan},${db.Core_Person_Instance}.${db.Core_Person}`,
        },
      }
    );
  }

  create(data: IAssignPlanEmployeesRequest) {
    return this.post<IResponse<IEmployeeWorkScheduleModel>>(
      "Personnel/Employees/Create_Many_EmployeeAttendencePlan",
      data
    );
  }

  exportExcel() {
    // return this.getAll();
  }
}
