import { msgTranslate } from "app/shared/resources/helpers-functions";
import { ICourseModel, ICourseToDisplay } from "../models/course/course";

export function courseToDisplay(courses: ICourseModel[]): ICourseToDisplay[] {
  return courses.map((course) => {
    return <ICourseToDisplay>{
      id: course.id,
      courseCode: course.code,
      ar_Name: course.ar_Name,
      en_Name: course.en_Name,
      abbrevation: course.abbrevation,
      isFree: course.isFree == 1 ? true : false,
      courseValue: course.courseValue,
      isHolding: course.isHolding == 2 ? true : false,
      holdingYears: course.holdingYears,
      penaletyPercentege: course.penalty,
      note: course.note,
      outLine: course.outLine,
      outlineDesc: course.outlineDesc,
      threeMontheEvalution: course.threeMontheEvalution,
      sixMontheEvalution: course.sixMontheEvalution,
      trA_Course_Holding_Years: course.trA_Course_Holding_Years,
      trA_TSP_Course: course.trA_TSP_Course?.map((res) => {
        return {
          tsP_ID: res.trA_TSP?.id,
          location: res.trA_TSP?.location,
          Name: msgTranslate(res.trA_TSP?.ar_Name, res.trA_TSP?.en_Name),
        };
      }),
      trA_Instructor_Course: course.trA_Instructor_Course?.map((res) => {
        return {
          instructor_ID: res.trA_Instructor?.id,
          jobTitle: res.trA_Instructor?.job,

          Name: msgTranslate(
            res.trA_Instructor?.ar_Name,
            res.trA_Instructor?.en_Name
          ),
        };
      }),
      course_Type_ID: course.course_Type_ID,
      courseType: msgTranslate(
        course.trA_Course_Type?.ar_Name,
        course.trA_Course_Type?.en_Name
      ),
      course_Category_ID: course.course_Category_ID,
       courseCategory: msgTranslate(
        course.trA_Course_Category?.ar_Name,
        course.trA_Course_Category?.en_Name
      ),
    };
  });
}
