import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { FormControl } from "@angular/forms";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { LocViolationsService } from "app/system-setup/localization-setup/services/localization-violations.service";
import { IResponse } from "app/shared/interfaces/responces";
import { ILocViolationAllType } from "app/system-setup/localization-setup/models/localization-violations";
import { MessageTypes } from "app/shared/enums/message-types.enum";

@Component({
  selector: "app-local-types",
  templateUrl: "./local-types.component.html",
  styleUrls: ["./local-types.component.scss"],
})
export class LocalTypesComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = true;
  @Input() setDefaultVal: boolean = false;
  @Input() label: string = "typeName";
  @Input() appendTo: string | HTMLElement | any;
  @Output() change = new EventEmitter<Idropdown[]>();

  typesList: Idropdown[] = [];

  constructor(private violationsService: LocViolationsService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.checkNoVersionOrCountry())
      this.getAllTypes();
  }

  override onChangeVersionOrCountry() {
    if (this.checkNoBusinessOrCompany() && this.checkNoVersionOrCountry())
      this.getAllTypes();
  }

  ngOnInit(): void {}

  getAllTypes() {
    this.violationsService
      .getAllTypes()
      .subscribe((res: IResponse<ILocViolationAllType[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.typesList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(versions: Idropdown[]) {
    this.change.emit(versions);
  }
}
