import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {Constants} from "app/shared/resources/constants";

@Component({
  selector: "submit-btn",
  templateUrl: "./submit-btn.component.html",
  styleUrls: ["./submit-btn.component.scss"],
})
export class SubmitBtnComponent implements OnInit {
  @Input() class?: string = "";
  @Input() label?: string;
  @Input() icon?: string;
  @Input() iconPositionBefore?: boolean = true;
  @Input() color: string = Constants.defaultButtonColor; // css color class
  @Input() id: number = 0;
  @Input() disabled: boolean = false;
  @Output() onClick? = new EventEmitter<any>();

  constructor() {
  }

  ngOnInit(): void {
  }

  click($event) {
    this.onClick.emit($event);
  }

  getLabel(): string {
    if (this.label) return this.label
    else if (this.id > 0) return "General.update"
    else return "General.save"
  }
}
