import { msgTranslate } from "app/shared/resources/helpers-functions";
import { IEmployeeCardModel } from "../models/employees";

export function employeeToMap(
  employees: IEmployeeCardModel[]
): IEmployeeCardModel[] {
  return employees.map((employee) => {
    return <IEmployeeCardModel>{
      id: employee.id,
      code: employee.code,
      en_Name: employee.en_Name,
      ar_Name: employee.ar_Name,
      email: employee.email,
      isActive: employee.isActive,
      //...employee,
      jobTitleName: msgTranslate(
        employee.jobTitleNameAr,
        employee.jobTitleName
      ),
      organization: msgTranslate(
        employee.organizationAr,
        employee.organization
      ),
      status: msgTranslate(employee.statusAr, employee.status),
      statusProperty: msgTranslate(
        employee.statusPropertyAr,
        employee.statusProperty
      ),
      contact: employee.contacts.map((contact) => contact.value).join(", "),
      address: employee.addresses.join(", "),
      img: employee.img == "/" ? "" : employee.img,
      manageSolidEmpNo: employee.solidEmp?.length ?? 0,
      manageDotesEmpNo: employee.dotesEmp?.length ?? 0,
    };
  });
}
