import { Component, Input, OnInit } from "@angular/core";
import { JobBenefitService } from "app/org-management/services/job-profile/job-benefit.service";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IJobBenefitModel, IJobBenefitRequest } from "app/org-management/model/job-benefit";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IBenefitModel } from "app/system-setup/bonus-setup/models/benefit";
import { th } from "date-fns/locale";
import { msgTranslate } from "app/shared/resources/helpers-functions";

@Component({
  selector: "app-job-benefit",
  templateUrl: "./job-benefit.component.html",
  styleUrls: ["./job-benefit.component.scss"]
})
export class JobBenefitComponent extends AppHelpers implements OnInit {
  @Input() jobId: number;

  form: FormGroup;
  submitted: boolean = false;
  mandatoryColomns: string[] = ["Ser", "benefit", "numMonth", "fromMoney", "toMoney", "actions"];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];
  dataSource: MatTableDataSource<IJobBenefitModel>;
  showMoney: boolean = false;

  constructor(private benefitService: JobBenefitService) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      joB_Title_ID: [this.jobId, [Validators.required]],
      benefit_ID: [null, [Validators.required]],
      numMonth: [null, [Validators.required, Validators.min(0)]],
      fromMoney: [null],
      toMoney: [null],
    });

  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany() && this.jobId > 0) {
      this.getAll();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }


  getAll() {
    this.dataSource = new MatTableDataSource(null);
    this.isLoaded = false;
    this.benefitService
      .getAll(this.jobId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IJobBenefitModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          res.data.map(e => e.benefit = msgTranslate(e.bon_Benefit.ar_Name, e.bon_Benefit.en_Name));
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
        }
        else this.errorList(res?.message?.messages);
      });
  }


  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IJobBenefitRequest = {
        joB_Title_ID: this.jobId,
        Benefit_ID: this.formCtrls.benefit_ID.value,
        NumMonth: this.formCtrls.numMonth.value,
        FromMoney: this.formCtrls.fromMoney.value,
        ToMoney: this.formCtrls.toMoney.value
      };
      this.create(model);
    }
    else this.error("General.formNotValid");
    console.log(this.form);
  }

  create(model: IJobBenefitRequest) {
    this.showLoading();
    this.benefitService.create(model).subscribe((res: IResponse<IJobBenefitModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.clearForm();
        this.addSuccessfullyMsg(res?.message?.messageTypeName);
        this.getAll();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  onDelete(benefit: IJobBenefitModel): void {
    this.deleteDialog(benefit.benefit).subscribe((confirm) => {
      if (confirm) this.delete(benefit.joB_Title_ID, benefit.benefit_ID);
    });
  }

  delete(JobId: number, jobBenefitId: number) {
    this.showLoading();
    this.benefitService.Delete(JobId, jobBenefitId).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        this.getAll();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({joB_Title_ID: this.jobId});
    this.submitted = false;
  }

  onChangeBenefit(benefits: IBenefitModel[]) {
    this.showMoney = benefits[0]?.bon_Benefit_Type?.isRange;
    if (this.showMoney) {
      this.formCtrls.fromMoney.setValidators([Validators.required, Validators.min(0)]);
      this.formCtrls.toMoney.setValidators([Validators.required, Validators.min(0)]);
    }
    else {
      this.formCtrls.fromMoney.reset();
      this.formCtrls.toMoney.reset();
      this.formCtrls.fromMoney.removeValidators([Validators.required, Validators.min(0)]);
      this.formCtrls.toMoney.removeValidators([Validators.required, Validators.min(0)]);
    }
    this.formCtrls.fromMoney.updateValueAndValidity();
    this.formCtrls.toMoney.updateValueAndValidity();
  }
}
