import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IFileApiModel, IFileData } from "app/shared/interfaces/files";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { IInstructorModel, IInstructorRequest, } from "app/training/models/instructor";
import { InstructorService } from "app/training/services/instructor.service";

@Component({
  selector: "app-add-instructor",
  templateUrl: "./add-instructor.component.html",
  styleUrls: ["./add-instructor.component.scss"],
})
export class AddInstructorComponent extends AppHelpers implements OnInit {
  instructorId: number = 0;
  form: FormGroup;
  submitted: boolean = false;
  files: IFileData[] = [];
  removeFiles: boolean = false;

  constructor(
    private instructorService: InstructorService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<AddInstructorComponent>
  ) {
    super();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.instructorId > 0 && this.checkNoBusinessOrCompany()) {
      this.getInstructorById();
      this.getInstructorAttachments();
    }
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [this.instructorId, {nonNullable: true}],
      ar_Name: [null, [Validators.required]],
      en_Name: [null, [Validators.required]],
      code: [null, [Validators.required]],
      mobile: [null, CustomValidator.mobileValidators],
      phone: [null],
      email: [null, [Validators.required, Validators.email]],
      qualification: [null],
      is_TOT: [false],
      job: [null],
      note: [null],
      attachmentTypeId: [null],
      files: [],
    });

    this.activatedRoute.params.subscribe((param) => {
      this.instructorId = Number(param.id) ?? 0;
    });

    if (this.data.value) {
      this.formCtrls.ar_Name.setValue(this.data.value);
      this.formCtrls.en_Name.setValue(this.data.value);
    }

  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    this.removeFiles = false;
    this.submitted = true;
    if (this.form.valid) {
      let data = {} as IInstructorRequest;
      data.id = this.formCtrls.id.value;
      data.ar_Name = this.formCtrls.ar_Name.value;
      data.en_Name = this.formCtrls.en_Name.value;
      data.code = this.formCtrls.code.value;
      data.mobile = this.formCtrls.mobile.value;
      data.phone = this.formCtrls.phone.value;
      data.email = this.formCtrls.email.value;
      data.qualification = this.formCtrls.qualification.value;
      data.is_TOT = this.formCtrls.is_TOT.value ?? false;
      data.job = this.formCtrls.job.value;
      data.note = this.formCtrls.qualification.value;
      data.AttachmentTypeID = this.formCtrls.attachmentTypeId.value ?? 0;
      data.files = this.formCtrls.files.value;
      const formData = HelpersFunctions.objectToFormData(data);

      if (data.id == 0) {
        this.create(formData);
      }
      else {
        this.update(formData);
      }
    }
  }

  getInstructorById() {
    this.showLoading();
    this.instructorService
      .getById(this.instructorId)
      .subscribe((res: IResponse<IInstructorModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          //  console.log(res);
          this.form.patchValue(res.data[0]);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  create(data: FormData) {
    this.showLoading();
    this.instructorService
      .create(data)
      .subscribe((res: IResponse<IInstructorModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res.message.messageTypeName);
          this.instructorId = res.data.id;
          this.formCtrls.id.setValue(this.instructorId);
          let url = `/training/${this.moduleId}/instructor/edit/${this.instructorId}`;
          if (!this.data.isDialog) this.navigateTo(url);
          this.formCtrls.files.reset();
          this.removeFiles = true;
          this.getInstructorAttachments();
          if (this.data.isDialog) this.dialogRef.close();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(data: FormData) {
    this.showLoading();
    this.instructorService
      .update(data)
      .subscribe((res: IResponse<IInstructorModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res.message.messageTypeName);
          this.formCtrls.files.reset();
          this.removeFiles = true;
          this.getInstructorAttachments();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  getInstructorAttachments() {
    this.showLoading();
    this.instructorService
      .getAllAttachmentsByInstructorId(this.instructorId)
      .subscribe((res: IResponse<IFileApiModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.files = [];
          res.data.forEach((file) => {
            let attach = {} as IFileData;
            attach.id = file.id;
            attach.size = file.size;
            attach.path = "/" + file.path;
            attach.type = file.mime;
            attach.name = file.en_Name;
            attach.extra = this.msgTranslate(
              file.comm_Attachment_Type.ar_Name,
              file.comm_Attachment_Type.en_Name
            );
            // if (this.formCtrls.attachmentTypeId.value == null) {
            //   // type_ID repeated in all object
            //   this.formCtrls.attachmentTypeId.setValue(file.type_ID);
            // }
            this.files.push(attach);
          });
        }
        else {
          this.errorList(res?.message?.messages);
        }
        this.hideLoading();
      });
  }

  delteAttachOfInstructor(attach: IFileData) {
    this.showLoading();
    this.instructorService
      .deleteInstructorAttachments(this.instructorId, attach.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res.message.messageTypeName);
          this.getInstructorAttachments();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  onCloseDialog() {
    this.dialogRef.close();
  }

  clearForm() {
    this.form.reset({id: 0, is_TOT: false});
    this.submitted = false;
    this.removeFiles = true;
  }

  // isAdd > is select file or remove file
  onselectFile(isAdd: boolean) {
    if (isAdd)
      this.formCtrls.attachmentTypeId.setValidators([Validators.required]);
    else this.formCtrls.attachmentTypeId.clearValidators();
    this.formCtrls.attachmentTypeId.updateValueAndValidity();
  }
}
