import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ITrainingPlanListModel,
  ITrainingPlanModel,
  ITrainingPlanRequest,
} from "app/training/models/training-plan/training-plan";

@Injectable({
  providedIn: "root",
})
export class TrainingPlanIndexService extends AppClientService {
  constructor() {
    super();
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ITrainingPlanListModel[]>>(
      "Training/plan/GetList",
      {
        params: params,
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Training/plan/Delete", {
      params: { Id: Id },
    });
  }

  create(data: ITrainingPlanRequest) {
    return this.post<IResponse<ITrainingPlanModel>>(
      "Training/plan/Create",
      data
    );
  }

  update(data: ITrainingPlanRequest) {
    return this.post<IResponse<ITrainingPlanModel>>("Training/plan/Edit", data);
  }

  // getList(params: IPageParams) {
  //   return this.get<IResponseList<IPlanModel[]>>("Training/Plan/GetList", {
  //     params: params,
  //   });
  // }

  getAll() {
    return this.get<IResponse<ITrainingPlanModel[]>>(
      "Training/Plan/GetAll"
    ).pipe();
  }

  getById(id: number) {
    return this.get<IResponse<ITrainingPlanModel[]>>("Training/Plan/GetAll", {
      params: {
        $filter: `id eq ${id}`,
      },
    });
  }

  deleteInstructorAttachments(instructorID: number, attchmentId: number) {
    return this.delete<IResponse>(
      "Training/Instructor/DeleteInstructorAttachment",
      {
        params: {
          InstructorID: instructorID,
          AttachmentID: attchmentId,
        },
      }
    );
  }

  exportExcel() {
    return this.getAll();
  }
}
