import {
  Component,
  OnInit,
  inject,
  Input,
  OnChanges,
  SimpleChanges,
} from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { DashboardService } from "../../services/dashboard.service";
import {
  IDashboardDataModel,
  IDashboardToDisplay,
} from "../../models/dashboard";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { dashboardDisplay } from "../../mapper/dashboard";
import { MatDialog } from "@angular/material/dialog";
import { DashboardDispalyDetailsComponent } from "../dashboard-dispaly-details/dashboard-dispaly-details.component";
import { FileService } from "app/shared/services/file.service";
import { FormGroup } from "@angular/forms";

@Component({
  selector: "app-dashboard-display",
  templateUrl: "./dashboard-display.component.html",
  styleUrls: ["./dashboard-display.component.scss"],
})
export class DashboardDisplayComponent
  extends AppHelpers
  implements OnInit, OnChanges
{
  @Input() serviceId: number = 0;
  Module_ID: number = 0;
  isExistInHome: boolean = false;
  dashboardList: IDashboardToDisplay[] = [];
  dialog: MatDialog = inject(MatDialog);
  isloading: boolean = false;
  form: FormGroup;

  constructor(
    public dashboardService: DashboardService,
    public fileService: FileService
  ) {
    super();
    if (this.getViewName == this.viewsManager.home_dashboard) {
      this.isExistInHome = true;
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.serviceId > 0 || this.Module_ID > 0) this.getAllDashboard();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      module_Id: [null],
    });

    // this.activatedRoute.params.subscribe((param) => {
    //   this.Module_ID = param.dashboardModuleId;
    // });

    if (this.serviceId > 0 || this.Module_ID > 0 || this.isExistInHome)
      this.getAllDashboard();
  }
  get formCtrls() {
    return this.form.controls;
  }

  getFilter() {
    return {
      $filter:
        this.Module_ID > 0 
          ? `Module_ID eq ${this.Module_ID}`
          : this.serviceId > 0
          ? `Service_ID eq ${this.serviceId}`
          : `Dash_Dashboard/IsExistInHome  eq ${true}`,
    };
  }

  getModuleDasboard() {
    this.isloading = false;
    this.dashboardList = [];
    this.Module_ID = this.formCtrls.module_Id.value;
    this.getAllDashboard();
  }

  getAllDashboard() {
    let params = {
      ...this.businessCompanyParams,
      ...this.getFilter(),
    };
    this.dashboardService
      .getAll(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IDashboardDataModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dashboardList = dashboardDisplay(res.data);
          this.isloading = true;
        } else {
          this.errorList(res.message?.messages);
        }
      });
  }

  dashboardDialog(data: IDashboardToDisplay) {
    return this.dialog.open(DashboardDispalyDetailsComponent, {
      data: {
        link: data.link,
        name: this.languageService.isArabic ? data.ar_Name : data.en_Name,
      },
      minWidth: "60%",
      minHeight: "50vh",
      autoFocus: false,
    });
  }
}
