import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IEmploymentTypeModel } from "../models/employment-type";

@Injectable({
  providedIn: "root",
})
export class EmploymentTypeService extends AppClientService {
  constructor() {
    super();
  }

  getAll() {
    return this.get<IResponse<IEmploymentTypeModel[]>>(
      "Recruitment/EmploymentType/GetAll"
    );
  }
}
