import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IAttLocationModel, IAttLocationRequest } from "app/system-setup/attendance-setup/models/att-location";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class AttLocationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IAttLocationRequest) {
    return this.post<IResponse<IAttLocationModel>>(
      "AttendanceSetup/ProjLocationType/Create",
      data
    );
  }

  update(data: IAttLocationRequest) {
    return this.post<IResponse<IAttLocationModel>>(
      "AttendanceSetup/ProjLocationType/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IAttLocationModel[]>>(
      "AttendanceSetup/ProjLocationType/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IAttLocationModel[]>>(
      "AttendanceSetup/ProjLocationType/GetAll"
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("AttendanceSetup/ProjLocationType/Delete", {
      params: { Id: id },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
