import { AfterViewInit, Component, Input, OnChanges, SimpleChanges } from "@angular/core";
import { PolicyGroupService } from "app/org-management/services/policy-group.service";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { FormControl } from "@angular/forms";
import { IPolicyGroupModel, LoadPoliciesParams, } from "app/org-management/model/policy-group";

@Component({
  selector: "load-policies-group",
  templateUrl: "./load-policies-group.component.html",
  styleUrls: ["./load-policies-group.component.scss"],
})
export class LoadPoliciesGroupComponent extends AppHelpers implements OnChanges, AfterViewInit {
  @Input() control = new FormControl();
  @Input() jobControl = new FormControl();
  @Input() orgControl = new FormControl();
  @Input() locationControl = new FormControl();
  @Input() submitted: boolean = false;
  @Input() filterByOrgAndJob: boolean = false;
  policyGroup: IPolicyGroupModel[];
  selectedPolicyGroup: IPolicyGroupModel[] = [];
  isLoaded: boolean = false;

  constructor(private policyGroupService: PolicyGroupService) { super(); }

  ngAfterViewInit(): void {

    // location control
    this.locationControl?.valueChanges.subscribe((id) => {
      this.control.setValue(null);
      this.policyGroup = [];
      this.selectedPolicyGroup = [];
      if (id > 0) {
        this.loadPolicies();
      }
    });


    // org control
    this.orgControl?.valueChanges.subscribe((id) => {
      this.control.setValue(null);
      this.policyGroup = [];
      this.selectedPolicyGroup = [];
      if (id > 0 && !this.filterByOrgAndJob) {
        console.log("org", id);
        this.loadPolicies();
      }
    });

    // job title control
    this.jobControl?.valueChanges.subscribe((id) => {
      this.control.setValue(null);
      this.policyGroup = [];
      this.selectedPolicyGroup = [];
      if (id > 0) {
        this.loadPolicies();
      }
    });
  }


  ngOnChanges(changes: SimpleChanges): void {
    if (changes?.control && this.policyGroup?.length > 0)
      this.moveFromSourceToTarget();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() &&
      (this.orgControl?.value > 0 || this.jobControl?.value > 0 || this.locationControl?.value > 0)
    )
      this.loadPolicies();
  }

  loadPolicies() {
    let params: LoadPoliciesParams = {
      orgId: this.orgControl?.value ?? 0,
      jobTitleId: this.jobControl?.value ?? 0,
      locatonId: this.locationControl?.value ?? 0,
    };

    this.policyGroupService
      .loadPolicies(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPolicyGroupModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // console.log(res);
          this.policyGroup = res.data;
          this.moveFromSourceToTarget();
          this.isLoaded = true;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  moveFromSourceToTarget() {
    this.policyGroup = [...this.policyGroup, ...this.selectedPolicyGroup];
    this.selectedPolicyGroup = [];
    this.control?.value?.forEach((selected) => {
      let selectedIndex = this.policyGroup?.findIndex(
        (ele) => ele.id == selected
      );
      if (selectedIndex >= 0) {
        this.selectedPolicyGroup.push(this.policyGroup[selectedIndex]);
        this.policyGroup.splice(selectedIndex, 1);
      }
    });
  }

  onMove($event) {
    let selected: number[] = [];
    this.selectedPolicyGroup?.forEach((e) => {
      selected.push(e.id);
    });
    this.control.setValue(selected);
  }
}
