import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {AbstractControl, FormControl} from '@angular/forms';
import {MessageTypes} from 'app/shared/enums/message-types.enum';
import {IResponse} from 'app/shared/interfaces/responces';
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import {ViewsManager} from 'app/shared/resources/views-manager';
import {ReasonsComponent} from 'app/system-setup/training-setup/components/reasons/reasons.component';
import {IReasonsModel} from 'app/system-setup/training-setup/models/reasons';
import {ReasonsService} from 'app/system-setup/training-setup/services/reasons.service';

@Component({
  selector: 'app-reason-dropdown',
  templateUrl: './reason-dropdown.component.html',
  styleUrls: ['./reason-dropdown.component.scss']
})
export class ReasonDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl | AbstractControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = 'reason';
  @Input() disable: boolean = false;
  @Input() appendTo: string | HTMLElement;
  @Input() courseCategoryId: number;
  @Output() change = new EventEmitter<IReasonsModel[]>();

  viewName: string;
  reasonList: IReasonsModel[];

  constructor(private reasonsService: ReasonsService) {
    super();
    this.viewName = ViewsManager.reasons
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  getAll() {
    this.reasonsService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IReasonsModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.reasonList = res.data;
        } else {
          this.errorList(res?.message?.messages);
        }
      });
  }


  onChange(model: IReasonsModel[]) {
    this.change.emit(model);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(ReasonsComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }

}
