import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IAttendancePlanModel } from "app/system-setup/attendance-setup/models/plan";
import { AssignPlanService } from "app/personnel-setup/services/employees/assign-plan.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-plan-by-policy-group-dropdown",
  templateUrl: "./plan-by-policy-group-dropdown.component.html",
  styleUrls: ["./plan-by-policy-group-dropdown.component.scss"],
})
export class PlanByPolicyGroupDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() policyGroupCtrl?: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "plans";
  @Output() change = new EventEmitter<IAttendancePlanModel[]>();

  planList: IAttendancePlanModel[] = [];

  constructor(private assignPlanService: AssignPlanService) {
    super();
  }

  ngOnInit(): void {
    this.control.disable();
    this.policyGroupCtrl?.valueChanges.subscribe((policyId) => {
      this.control.setValue(null);
      if (policyId > 0) {
        this.getPlansByPolicyGroup();
        if (!this.disable) this.control.enable();
      } else {
        this.planList = [];
        this.control.disable();
      }
    });
  }

  getPlansByPolicyGroup() {
    this.assignPlanService
      .getPlansByPolicyGroup(this.policyGroupCtrl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IAttendancePlanModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.planList = res.data;
        } else {
          this.errorList(res?.message?.messages);
        }
      });
  }

  onChange(model: IAttendancePlanModel[]) {
    this.change.emit(model);
  }
}
