import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
  ILocViolationModel,
  ILocViolationRequest,
  ILocViolationResponse,
} from "app/system-setup/localization-setup/models/localization-violations";
import { LocViolationsService } from "app/system-setup/localization-setup/services/localization-violations.service";
import { RadioButtonModel } from "app/shared/components/resources/dynamic-form-field/radio-button/radio-button.component";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { CustomValidators } from "ngx-custom-validators";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";

@Component({
  selector: "localization-add-violation",
  templateUrl: "./loc-add-violation.component.html",
  styleUrls: ["./loc-add-violation.component.scss"],
})
export class LocAddViolationComponent
  extends AppHelpers
  implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  violationId: number;

  constructor(private violationsService: LocViolationsService,
              @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
              public dialogRef: MatDialogRef<LocAddViolationComponent>) {
    super();
  }

  ngOnInit() {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.violationId = Number(params.get("id")) ?? 0;
    });

    this.form = this.fb.group(
      {
        id: [0, {nonNullable: true}],
        en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
        ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
        variableCode: [null, CustomValidator.variableCodeValidators],
        display_In_Result: [false],
        renew_Day: [null, [CustomValidators.gte(0), CustomValidators.digits]],
        fromValue: [
          null,
          [
            Validators.required,
            CustomValidators.gte(0),
            CustomValidators.digits,
          ],
        ],
        toValue: [
          null,
          [
            Validators.required,
            CustomValidators.gte(0),
            CustomValidators.digits,
          ],
        ],
        violation_Type_ID: [null, [Validators.required]],
        type: [null, [Validators.required]],
        renew_By_Period: [false],
        ismoney: [false],
        measureUnit: [
          null,
          [
            Validators.required,
            CustomValidators.gte(0),
            CustomValidators.digits,
          ],
        ],
      },
      {
        validator: CustomValidator.greaterThan("fromValue", "toValue"),
      }
    );


    if (this.data.value) {
      this.formCtrls.en_Name.setValue(this.data.value);
      this.formCtrls.ar_Name.setValue(this.data.value);
    }
  }

  override onChangeBusinessAndCompanyId(): void {
    if (
      this.checkNoBusinessOrCompany() &&
      this.checkNoVersionOrCountry() &&
      this.violationId > 0
    ) this.getById();

  }

  override onChangeVersionOrCountry() {
    this.onChangeBusinessAndCompanyId();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getById() {
    this.showLoading();
    this.violationsService
      .getById(this.violationId)
      .subscribe((res: IResponse<ILocViolationResponse[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.form.patchValue(res.data[0].loc_Violation);
          this.formCtrls.variableCode.patchValue(res.data[0].loc_Variable?.code);
          this.formCtrls.display_In_Result.patchValue(res.data[0].loc_Variable?.display_In_Result);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }


  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ILocViolationRequest = {
        Violation: {
          ID: this.form.value.id,
          En_Name: this.form.value.en_Name,
          Ar_Name: this.form.value.ar_Name,
          Renew_Day: this.form.value.renew_Day,
          FromValue: this.form.value.fromValue,
          ToValue: this.form.value.toValue,
          Violation_Type_ID: this.form.value.violation_Type_ID,
          Type: this.form.value.type,
          Renew_By_Period: this.form.value.renew_By_Period ?? false,
          Ismoney: this.form.value.ismoney,
          MeasureUnit: this.form.value.measureUnit,
        },
        VariableCode: this.form.value.variableCode,
        Display_In_Result: this.form.value.display_In_Result ?? false,
      };

      model.Violation.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: ILocViolationRequest) {
    this.showLoading();
    this.violationsService
      .create(model)
      .subscribe((res: IResponse<ILocViolationModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.violationId = res.data.id;
          this.formCtrls.id.setValue(this.violationId);
          let url = `/localization-setup/${this.moduleId}/violations/edit/${this.violationId}`;
          if (!this.data.isDialog) this.navigateTo(url);
          if (this.data.isDialog) this.dialogRef.close();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: ILocViolationRequest) {
    this.showLoading();
    this.violationsService
      .update(model)
      .subscribe((res: IResponse<ILocViolationModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          // this.violation = res.data;
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    // if (this.violationId == 0)
    this.form.reset({
      id: 0,
      ismoney: false,
      renew_By_Period: false,
      display_In_Result: false,
    });
    // else this.violationToEdit();
    this.submitted = false;
  }

  moneyOrPenaltyRadioList: RadioButtonModel[] = [
    {
      value: true,
      label: "money",
    },
    {
      value: false,
      label: "penalty",
    },
  ];
}
