import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";
import { PlanCoreComponent } from "app/training/components/training-plan/add-paln/plan-core/plan-core.component";
import { ITrainingPlanModel } from "app/training/models/training-plan/training-plan";
import { TrainingPlanIndexService } from "app/training/services/traning-plan/training-plan-index.service";

@Component({
  selector: "app-training-plan-dropdown",
  templateUrl: "./training-plan-dropdown.component.html",
  styleUrls: ["./training-plan-dropdown.component.scss"],
})
export class TrainingPlanDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() label: string = "plan";
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<ITrainingPlanModel[]>();
  planList: ITrainingPlanModel[] = [];
  viewName: string;

  constructor(private trainingPlanIndexService: TrainingPlanIndexService) {
    super();
    this.viewName = ViewsManager.add_training_plan;
  }

  ngOnInit(): void {}
  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.trainingPlanIndexService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ITrainingPlanModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.planList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  onChange(plan: ITrainingPlanModel[]) {
    this.change.emit(plan);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(PlanCoreComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }
}
