import { Component, Input, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { policyToMap } from "app/system-setup/general-setup/mapper/policies";
import {
  ICorePolicyGroupUMUser,
  IPolicyGroupToDisplay,
  IUserPoliciesModel,
  IUserPoliciesRequest,
} from "app/system-setup/general-setup/models/users";
import { UserPoliciesService } from "app/system-setup/general-setup/services/users/user-policies.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-user-policy-groups",
  templateUrl: "./user-policy-groups.component.html",
  styleUrls: ["./user-policy-groups.component.scss"],
})
export class UserPolicyGroupsComponent extends AppHelpers implements OnInit {
  @Input() userId: number;
  form: FormGroup;
  submitted: boolean = false;
  dataSource: MatTableDataSource<IPolicyGroupToDisplay>;
  mandatoryColomns: string[] = ["Business_Name", "Company_Name", "PolicyGroup_Name", "actions"];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];

  constructor(private userPoliciesService: UserPoliciesService) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, {nonNallable: true}],
      user_ID: [this.userId],
      policyGroupId: [0, [Validators.required]],
      company_ID: [0],
    });
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.userId > 0) this.getUserPolicyList();
    else this.isLoaded = true;
  }

  getUserPolicyList() {
    this.isLoaded = false;
    this.userPoliciesService
      .getUserPolicies(this.userId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IUserPoliciesModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          let result: IPolicyGroupToDisplay[] = policyToMap(res.data);
          this.dataSource = new MatTableDataSource(result);
          this.isLoaded = true;
        }
        else {
          this.errorList(res?.message?.messages);
        }
      });
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model = {} as IUserPoliciesRequest;
      const policyGroup = {} as ICorePolicyGroupUMUser;
      policyGroup.id = this.formCtrls.id.value;
      policyGroup.user_ID = this.userId;
      policyGroup.policyGroupId = this.formCtrls.policyGroupId.value;
      model.I_Core_PolicyGroup_UM_User = policyGroup;

      if (model.I_Core_PolicyGroup_UM_User.id == 0) {
        this.create(model);
      }
      else {
        this.update(model);
      }
    }
  }

  create(model: IUserPoliciesRequest) {
    this.showLoading();
    this.userPoliciesService
      .createUserPolicy(model)
      .subscribe((res: IResponse<IUserPoliciesModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getUserPolicyList();
          this.addSuccessfullyMsg(res.message.messageTypeName);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: IUserPoliciesRequest) {
    this.showLoading();
    this.userPoliciesService
      .updateUserPolicy(model)
      .subscribe((res: IResponse<IUserPoliciesModel>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.clearForm();
          this.getUserPolicyList();
          this.updateSuccessfullyMsg(res.message.messageTypeName);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  editToPolicyGroup(policyGroup: IUserPoliciesModel) {
    this.form.patchValue(policyGroup);
  }

  onDelete(PolicyGroup: IPolicyGroupToDisplay): void {
    let msg = this.msgTranslate(PolicyGroup.PolicyGroup_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(PolicyGroup);
    });
  }

  delete(PolicyGroup: IPolicyGroupToDisplay) {
    this.showLoading();
    this.userPoliciesService.DeleteUserPolicy(PolicyGroup.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.getUserPolicyList();
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({id: 0, user_ID: this.userId});
    this.submitted = false;
  }
}
