import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IResponse } from "app/shared/interfaces/responces";
import { IBenefitModel } from "app/system-setup/bonus-setup/models/benefit";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root"
})
export class BenefitService extends AppClientService {
  constructor() {
    super();
  }


  getAll() {
    return this.get<IResponse<IBenefitModel[]>>("Benefit/GetAll",{
      params:{
        $expand:`${db.bon_Benefit_Type}`
      }
    });
  }
}
