import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";

import { RadioButtonModel } from "app/shared/components/resources/dynamic-form-field/radio-button/radio-button.component";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { PropertiesService } from "app/system-setup/dashboard-setup/services/properties.service";
import {
  IPropertyModel,
  IPropertyRequest,
} from "app/system-setup/dashboard-setup/models/properties";
import { EDashboardPropertiesType } from "app/shared/enums/dashboard-properties-type.enum";

@Component({
  selector: "app-properties",
  templateUrl: "./properties.component.html",
  styleUrls: ["./properties.component.scss"],
})
export class PropertiesComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  mandatoryColomns: string[] = [
    "Ser",
    "id",
    "en_Name",
    "ar_Name",
    "type",
    "actions",
  ];
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<IPropertyModel>;
  isLoaded: boolean = false;

  constructor(
    public propertiesService: PropertiesService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<PropertiesComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      en_Name: [null, [Validators.required]],
      ar_Name: [null],
      property_Type: [null, [Validators.required]],
    });

    if (this.data.value) {
      this.formCtrls.en_Name.setValue(this.data.value);
      this.formCtrls.ar_Name.setValue(this.data.value);
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId() {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany()) this.getList(this.firstPage);
  }

  getList(page: number, pageSize?: number) {
    this.page.pageIndex = page;
    if (pageSize) this.page.pageSize = pageSize;
    let params: IPageParams = {
      PageNumber: page,
      pageSize: this.page.pageSize,
    };
    this.propertiesService
      .getList(params)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IPropertyModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // translate property type
          res.data.map((ele) => (ele.type = this.translate(ele.property_Type)));
          this.dataSource = new MatTableDataSource(res.data);
          this.isLoaded = true;
          this.page.length = res.meta.totalItemCount;
          this.page.meta = res.meta;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IPropertyRequest = {
        id: this.form.value.id,
        ar_Name: this.form.value.ar_Name,
        en_Name: this.form.value.en_Name,
        property_Type: this.form.value.property_Type,
      };

      model.id == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IPropertyRequest) {
    this.showLoading();
    this.propertiesService
      .create(model)
      .subscribe((res: IResponse<IPropertyModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.firstPage);
          if (this.data.isDialog) this.dialogRef.close();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  update(model: IPropertyRequest) {
    this.showLoading();
    this.propertiesService
      .update(model)
      .subscribe((res: IResponse<IPropertyModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getList(this.currentPage);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  propertyToEdit(property: IPropertyModel) {
    this.form.patchValue(property);
  }

  onDelete(property: IPropertyModel): void {
    let msg = this.msgTranslate(property.ar_Name, property.en_Name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(property);
    });
  }

  delete(property: IPropertyModel) {
    this.showLoading();
    this.propertiesService.Delete(property.id).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        if (this.dataSource.data.length == 1 && this.currentPage > 1) {
          // if last item in list: get previous page
          this.getList(this.prevPage, this.page.pageSize);
        }
        else {
          // if not last item in list: get current page
          this.getList(this.currentPage, this.page.pageSize);
        }
        if (property.id == this.formCtrls.id.value) this.clearForm();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

  propertyTypeRadioList: RadioButtonModel[] = [
    {
      label: EDashboardPropertiesType.number,
      value: EDashboardPropertiesType.number,
    },
    {
      label: EDashboardPropertiesType.text,
      value: EDashboardPropertiesType.text,
    },
  ];

  onCloseDialog() {
    this.dialogRef.close();
  }
}
