import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ILocPenaltyModel,
  ILocPenaltyRequest,
} from "../models/localization-penalties";
import { map } from "rxjs/operators";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class LocPenaltiesService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ILocPenaltyRequest) {
    return this.post<IResponse<ILocPenaltyModel>>("Penalities/Create", data, {
      params: this._paramsService.versionCountryParams,
    });
  }

  update(data: ILocPenaltyRequest) {
    return this.post<IResponse<ILocPenaltyModel>>("Penalities/Edit", data, {
      params: this._paramsService.versionCountryParams,
    });
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ILocPenaltyModel[]>>("Penalities/GetList", {
      params: {
        ...params,
        ...this._paramsService.versionCountryParams,
      },
    });
  }

  getAll() {
    return this.get<IResponse<ILocPenaltyModel[]>>("Penalities/GetAll", {
      params: this._paramsService.versionCountryParams,
    }).pipe(
      map((res) => {
        res.data.map((item: ILocPenaltyModel) => {
          item.ar_Name = item.name;
          item.en_Name = item.name;
        });
        return res;
      })
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Penalities/Delete", {
      params: {Id: Id, ...this._paramsService.versionCountryParams},
    });
  }

  exportExcel() {
    this.getAll();
  }
}
