import { Injectable } from "@angular/core";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import { courseToDisplay } from "app/training/mapper/course";
import { map } from "rxjs/operators";
import {
  ICourseInstructorRequstModel,
  ICourseModel,
  ICourseTspRequstModel,
} from "../../models/course/course";

@Injectable({
  providedIn: "root",
})
export class CourseService extends AppClientService {
  constructor() {
    super();
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ICourseModel[]>>("Training/Course/GetList", {
      params: { ...params, $expand: db.TRA_Course_Holding_Years },
    });
  }

  getAll() {
    return this.get<IResponse<ICourseModel[]>>("Training/Course/GetAll", {
      params: {
        PageNumber: 1,
        pageSize: 1,
        $expand: `${db.TRA_Course_Holding_Years}`,
      },
    }).pipe(
      map((res: IResponse<any[]>) => {
        res.data = courseToDisplay(res.data)
        return res;
      })
    );
  }

  getById(id: number) {
    return this.get<IResponse<ICourseModel[]>>("Training/Course/GetAll", {
      params: {
        $filter: `id eq ${id}`,
        $expand: `${db.TRA_Course_Holding_Years},${db.Tra_Instructor_course}.${db.Tra_Instructor},${db.tra_TSP_Course}.${db.TRA_TSP}`,
      },
    });
  }

  getCourseByCategoryId(id: number) {
    return this.get<IResponse<ICourseModel[]>>("Training/Course/GetAll", {
      params: {
        $filter: `Course_Category_ID  eq ${id}`,
      },
    });
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Training/Course/Delete", {
      params: { Id: Id },
    });
  }

  addTSPToCourse(data: ICourseTspRequstModel) {
    return this.post<IResponse<ICourseTspRequstModel>>(
      "Training/Course/AddTSPToCourse",
      data
    );
  }

  addInstructorToCourse(data: ICourseInstructorRequstModel) {
    return this.post<IResponse<ICourseInstructorRequstModel>>(
      "Training/Course/AddInstructorsToCourse",
      data
    );
  }

  exportExcel() {
    return this.getAll();
  }
}
