import { Injectable } from "@angular/core";
import { IDashboardDataModel, IDashboardModel } from "../models/dashboard";
import { AppClientService } from "app/shared/services/app-client.service";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class DashboardService extends AppClientService {
  constructor() {
    super();
  }

  create(data: FormData) {
    return this.post<IResponse<IDashboardModel>>(
      "Dashboard/Dashboard/Create",
      data
    );
  }

  update(data: FormData) {
    return this.post<IResponse<IDashboardModel>>(
      "Dashboard/Dashboard/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IDashboardDataModel[]>>(
      "Dashboard/Dashboard/GetList",
      {
        params: {
          ...params,
          $expand: `${db.um_services},${db.dash_dashboard}.${db.Comm_Attachment}`,
        },
      }
    );
  }

  getAll(params) {
    return this.get<IResponseList<IDashboardDataModel[]>>(
      "Dashboard/Dashboard/GetAll",
      {
        params: {
          ...params,
          $expand: `${db.um_services},${db.dash_dashboard}.${db.Comm_Attachment}`,
        },
      }
    );
  }

  getById(dashboardId: number) {
    return this.get<IResponseList<IDashboardDataModel[]>>(
      "Dashboard/Dashboard/GetAll",
      {
        params: {
          $filter: `Dashboard_ID eq ${dashboardId}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Dashboard/Dashboard/Delete", {
      params: { Id: Id },
    });
  }
}
