import {Injectable} from '@angular/core';
import {ISapSystemInstanceModel, ISapSystemInstanceRequest,} from 'app/system-setup/sap-business-one-setup/models/system-instance';
import {IPageParams} from 'app/shared/interfaces/params';
import {IResponse, IResponseList} from 'app/shared/interfaces/responces';
import {AppClientService} from 'app/shared/services/app-client.service';

@Injectable({
  providedIn: "root",
})
export class SystemInstanceService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ISapSystemInstanceRequest) {
    return this.post<IResponse<ISapSystemInstanceModel>>(
      "SystemInstanceS1Setup/Create",
      data
    );
  }

  update(data: ISapSystemInstanceRequest) {
    return this.post<IResponse<ISapSystemInstanceModel>>(
      "SystemInstanceS1Setup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ISapSystemInstanceModel[]>>(
      "SystemInstanceS1Setup/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<ISapSystemInstanceModel[]>>(
      "SystemInstanceS1Setup/GetAll"
    )
    // .pipe(
    //   map((res: IResponse<ISapSystemInstanceModel[]>) => {
    //     return res.data;
    //   })
    // );
  }

  getById(id: number) {
    return this.get<IResponse<ISapSystemInstanceModel[]>>(
      "SystemInstanceS1Setup/GetAll",
      {
        params: {
          $filter: `id eq ${id}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("SystemInstanceS1Setup/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    this.getAll();
  }
}
