import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IResponse } from "app/shared/interfaces/responces";
import { IJobBenefitModel, IJobBenefitRequest } from "app/org-management/model/job-benefit";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root"
})
export class JobBenefitService extends AppClientService {
  constructor() {
    super();
  }


  create(data: IJobBenefitRequest) {
    return this.post<IResponse<IJobBenefitModel>>(
      "OrgManagement/JobTitle/Create_JobTitleBenefit",
      data
    );
  }

  getAll(JobId: number,) {
    return this.get<IResponse<IJobBenefitModel[]>>(
      "OrgManagement/JobTitle/GetAll_JobTitleBenefit", {
        params: {
          $filter: `JOB_Title_ID eq ${JobId}`,
          $expand: `${db.Bon_Benefit}.${db.bon_Benefit_Type}`
        },
      }
    );
  }

  Delete(JobId: number, jobBenefitId: number) {
    return this.delete<IResponse>("OrgManagement/JobTitle/Delete_JobTitleBenefit", {
      params: {JobId: JobId, benefitid: jobBenefitId},
    });
  }
}
