import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";
import { ExpenseComponent } from "app/system-setup/training-setup/components/expense/expense.component";
import { IExpenseModel } from "app/system-setup/training-setup/models/expense";
import { ExpenseService } from "app/system-setup/training-setup/services/expense.service";

@Component({
  selector: "app-expenses-dropdown",
  templateUrl: "./expenses-dropdown.component.html",
  styleUrls: ["./expenses-dropdown.component.scss"],
})
export class ExpensesDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() label: string = "expenses";
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<IExpenseModel[]>();

  expenseList: IExpenseModel[] = [];
  viewName: string;

  constructor(private expenseService: ExpenseService) {
    super();
    this.viewName = ViewsManager.expense;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllexpenses();
  }

  getAllexpenses() {
    this.expenseService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IExpenseModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.expenseList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(expense: IExpenseModel[]) {
    this.change.emit(expense);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(ExpenseComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllexpenses();
      });
  }
}
