import { Component, EventEmitter, Input, Output } from "@angular/core";
import { DomSanitizer } from "@angular/platform-browser";
import { FileService } from "app/shared/services/file.service";

@Component({
  selector: "primeng-file-upload-card",
  templateUrl: "./file-upload-card.component.html",
  styleUrls: ["./file-upload-card.component.scss"],
})
export class FileUploadCardComponent {
  @Input() file: any; // File || IFileData
  @Input() isHexImg: boolean = false;
  @Input() isEdit: boolean = false;
  @Input() isUploaded: boolean = false;
  @Input() iconPath: string;
  @Input() isFileObject: boolean = true;
  @Output() removeFile = new EventEmitter();

  constructor(
    private sanitizer: DomSanitizer,
    public fileService: FileService
  ) {}

  isImage(type: string): boolean {
    return type.includes("image");
  }

  urlFromFile(file: File) {
    let objectURL = URL.createObjectURL(file);
    return this.sanitizer.bypassSecurityTrustUrl(objectURL);
  }

  onRemoveFile(file?: File) {
    this.removeFile.emit(file);
  }
}
