import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";

import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";
import { AddTspComponent } from "app/training/components/tsp/add-tsp/add-tsp.component";
import { ISTPModel } from "app/training/models/tsp";
import { TspService } from "app/training/services/tsp.service";

@Component({
  selector: "app-tsp-dropdown",
  templateUrl: "./tsp-dropdown.component.html",
  styleUrls: ["./tsp-dropdown.component.scss"],
})
export class TspDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() label: string = "TSP";
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<ISTPModel[]>();
  TSPList: ISTPModel[] = [];
  viewName: string;

  constructor(private tspService: TspService) {
    super();
    this.viewName = ViewsManager.tsp_index;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.tspService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ISTPModel[]>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.TSPList = res.data;
        }
        else this.errorList(res.message?.messages);
      });
  }

  onChange(tsp: ISTPModel[]) {
    this.change.emit(tsp);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(AddTspComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }
}
