import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { ISTPModel, ITSPRequest } from "app/training/models/tsp";
import { TspService } from "app/training/services/tsp.service";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-add-tsp",
  templateUrl: "./add-tsp.component.html",
  styleUrls: ["./add-tsp.component.scss"],
})
export class AddTspComponent extends AppHelpers implements OnInit {
  tspId: number = 0;
  form: FormGroup;
  submitted: boolean = false;

  constructor(
    private tspService: TspService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<AddTspComponent>
  ) {
    super();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.tspId > 0 && this.checkNoBusinessOrCompany()) this.getTspById();
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [this.tspId, {nonNullable: true}],
      ar_Name: [null, [Validators.required]],
      en_Name: [null, [Validators.required]],
      breif_Name: [null],
      address: [null],
      mobile: [
        null,
        [
          Validators.pattern(/^(\(?\+?[0-9]*\)?)?[0-9_ \-\(\)\S*$]*$/),
          CustomValidator.MobileStartsWith,
          CustomValidator.mobileLength,
        ],
      ],
      phone: [null],
      fax: [null],
      email: [null, [Validators.required, Validators.email]],
      webSite_URL: [null, CustomValidators.url],
      i_M_C_Code: [null],
      location: [null],
      note: [null],
      contactPersonName: [null],
      contactPersonPhone: [null],
      contactPersonMobile: [
        null,
        [
          Validators.pattern(/^(\(?\+?[0-9]*\)?)?[0-9_ \-\(\)\S*$]*$/),
          CustomValidator.MobileStartsWith,
          CustomValidator.mobileLength,
        ],
      ],
      contactPersonEmail: [null, Validators.email],
    });

    this.activatedRoute.params.subscribe((param) => {
      this.tspId = Number(param.id);
    });

    if (this.data.value) {
      this.formCtrls.ar_Name.setValue(this.data.value);
      this.formCtrls.en_Name.setValue(this.data.value);
    }

    console.log(this.moduleId);
  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      let data = {} as ITSPRequest;
      data.id = this.formCtrls.id.value;
      data.ar_Name = this.formCtrls.ar_Name.value;
      data.en_Name = this.formCtrls.en_Name.value;
      data.breif_Name = this.formCtrls.breif_Name.value;
      data.location = this.formCtrls.location.value;
      data.address = this.formCtrls.address.value;
      data.webSite_URL = this.formCtrls.webSite_URL.value;
      data.email = this.formCtrls.email.value;
      data.mobile = this.formCtrls.mobile.value;
      data.phone = this.formCtrls.phone.value;
      data.fax = this.formCtrls.fax.value;
      data.contactPersonName = this.formCtrls.contactPersonName.value;
      data.contactPersonEmail = this.formCtrls.contactPersonEmail.value;
      data.contactPersonMobile = this.formCtrls.contactPersonMobile.value;
      data.contactPersonPhone = this.formCtrls.contactPersonPhone.value;
      data.i_M_C_Code = this.formCtrls.i_M_C_Code.value;
      data.note = this.formCtrls.note.value;

      if (data.id == 0) this.create(data);
      else this.update(data);
    }
  }

  getTspById() {
    this.showLoading();
    this.tspService
      .getById(this.tspId)
      .subscribe((res: IResponse<ISTPModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          res.data[0].i_M_C_Code = res.data[0].i_M_C_Code?.trim();
          this.form.patchValue(res.data[0]);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  create(data: ITSPRequest) {
    this.showLoading();
    this.tspService.create(data).subscribe((res: IResponse<ISTPModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.addSuccessfullyMsg(res?.message?.messageTypeName);
        this.tspId = res.data.id;
        this.formCtrls.id.setValue(this.tspId);
        let url = `/training/${this.moduleId}/tsp/edit/${this.tspId}`;
        if (!this.data.isDialog) this.navigateTo(url);
        if (this.data.isDialog) this.dialogRef.close();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  update(data: ITSPRequest) {
    this.showLoading();
    this.tspService.update(data).subscribe((res: IResponse<ISTPModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  onCloseDialog() {
    this.dialogRef.close();
  }

  clearForm() {
    this.form.reset();
    this.submitted = false;
  }
}
