import { LanguageService } from "app/shared/services/language.service";
import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { Constants } from "app/shared/resources/constants";
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: "app-button",
  templateUrl: "./button.component.html",
  styleUrls: ["./button.component.scss"],
})
export class ButtonComponent implements OnInit {
  @Input() class?: string = "";
  @Input() label?: string;
  @Input() icon?: string;
  @Input() iconPositionBefore?: boolean = true;
  @Input() color: string = Constants.defaultButtonColor; // css color class
  @Input() id: number = 0;
  @Input() disabled: boolean = false;
  @Input() url: string = null;
  @Input() params: any = null;
  @Input() decreasePaddingWithIcon?: boolean = false;
  @Input() isRaisedBtn: boolean = true;
  @Output() onClick? = new EventEmitter<any>();

  constructor(
    private languageService: LanguageService,
    public route: ActivatedRoute
  ) {}

  ngOnInit(): void {
    if (this.decreasePaddingWithIcon && this.icon)
      (this.iconPositionBefore && this.languageService.isArabic) ||
      (!this.iconPositionBefore && this.languageService.isEnglish)
        ? (this.class += " pright-6 pleft-16")
        : (this.class += " pright-16 pleft-6");
  }

  click($event) {
    this.onClick.emit($event);
  }
}
