import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IPolicyGroupModel } from "app/org-management/model/policy-group";
import { PolicyGroupService } from "app/org-management/services/policy-group.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-policy-groups-dropdown",
  templateUrl: "./policy-groups-dropdown.component.html",
  styleUrls: ["./policy-groups-dropdown.component.scss"],
})
export class PolicyGroupsDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "policyGroups";
  @Input() appendTo: string | HTMLElement;
  @Input() courseCategoryId: number;
  @Output() change = new EventEmitter<IPolicyGroupModel[]>();

  policyGroupsList: IPolicyGroupModel[];

  constructor(private policyGroupService: PolicyGroupService) {
    super();
    // this.viewName = ViewsManager.policy_groups;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  getAll() {
    this.policyGroupService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPolicyGroupModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.policyGroupsList = res.data;
        } else {
          this.errorList(res?.message?.messages);
        }
      });
  }

  onChange(model: IPolicyGroupModel[]) {
    this.change.emit(model);
  }
}
