import { Injectable } from "@angular/core";
import { environment } from "environments/environment";
import { IFileBase64, IFileBinary } from "../interfaces/files";
import { HttpClient } from "@angular/common/http";
import * as FileSaver from "file-saver";
import { ParamsService } from "app/shared/services/params.service";
import { ExcelFormat, ImgFormat, PdfFormat, WordFormat } from "app/shared/enums/files-exe";
import { SvgManager } from "app/shared/resources/assets-manager";

@Injectable({
  providedIn: "root",
})
export class FileService {
  svgManager: SvgManager = new SvgManager();

  constructor(private http: HttpClient, private paramsService: ParamsService) { }

  upload = (files) => {
    if (files.length == 0) {
      return;
    }
    else {
      const formData = new FormData();
      for (var i = 0; i < files.length; i++) {
        formData.append("files", files[i]);
        return formData;
      }
    }
  };

  download(url: string) {
    if (!url.startsWith("/")) {
      url = "/" + url;
    }
    window.open(environment.baseUrl + url, "_blank");
  }

  downloadByAttachId(attachId: number) {
    return this.http
      .get(`${environment.baseUrl}/api/` + "Common/DownloadFile", {
        params: {
          BusinessEntityID: this.paramsService.businessCompanyParams.BusinessEntityID,
          CompanyID: this.paramsService.businessCompanyParams.BusinessEntityID,
          AttachmentID: attachId,
        },
        responseType: "blob",
      });

  }

  downloadFromServer(attachId: number) {
    this.downloadByAttachId(attachId).subscribe((res) => {
      FileSaver.saveAs(res);
    });
  }

  downloadBase64File(base64: string, fileName: string) {
    const byteCharacters = atob(base64.split("base64,")[1]);
    const byteNumbers = new Array(byteCharacters.length);
    for (let i = 0; i < byteCharacters.length; i++) {
      byteNumbers[i] = byteCharacters.charCodeAt(i);
    }
    const byteArray = new Uint8Array(byteNumbers);
    const blob = new Blob([byteArray], { type: "application/octet-stream" });
    const url = URL.createObjectURL(blob);
    const anchor = document.createElement("a");
    anchor.download = fileName;
    anchor.href = url;
    anchor.click();
  }

  preview(url: any) {
    if (url) {
      if (!url.FileAsBase64) {
        if (!url.startsWith("/")) {
          url = "/" + url;
        }
        return environment.baseUrl + url;
      }
      else {
        return url.FileAsBase64;
      }
    }
  }

  resetFileInputs() {
    const FileInputs = document.querySelectorAll("input[type=\"file\"]");
    FileInputs.forEach((input: any) => {
      input.value = null;
    });
  }

  resetFileById(htmlId: string) {
    console.log("htmlId", htmlId);
    if (htmlId != null) {
      (<HTMLInputElement>document?.getElementById(htmlId)).value = null;
    }
  }

  async uploadBase64(files: any) {
    let file = files[0];
    if (file) {
      let data = {} as IFileBase64;
      var reader = new FileReader();
      reader?.readAsDataURL(file);
      reader.onload = (event: any) => {
        data.FileAsBase64 = event.target.result; //replace("data:application/pdf;base64,","");
      };
      data.name = file.name;
      data.size = file.size;
      data.type = file.type;
      data.LastModified = file.lastModified;
      data.LastModifiedDate = file.lastModifiedDate;
      return await data;
    }
    else {
      return null;
    }
  }

  // deal with image as hexstring

  async uploadHexImage(files: File[]) {
    let file: File = files[0];
    if (file) {
      let data = {} as IFileBinary;
      var reader = new FileReader();
      reader.readAsArrayBuffer(file);
      reader.onload = (event: any) => {
        // result > binary data of the image
        data.binaryData = event.target.result;
        data.hexString = this.imgageToHex(data.binaryData);
        // console.log("Hexadecimal:", data.hexString);
      };
      return await data;
    }
    else {
      return null;
    }
  }

  private imgageToHex(buffer: ArrayBuffer): string {
    const uint8Array = new Uint8Array(buffer);
    let hexString = "";
    for (let i = 0; i < uint8Array.length; i++) {
      const hex = uint8Array[i].toString(16);
      hexString += hex.length === 1 ? "0" + hex : hex;
    }
    return hexString;
  }

  previewHexImage(hexString: string) {
    if (hexString) {
      let input = hexString.replace(/[^A-Fa-f0-9]/g, "");
      if (input.length % 2) {
        // console.log("cleaned hex string length is odd.");
        return;
      }

      let binary = new Array();
      for (let i = 0; i < input.length / 2; i++) {
        let h = input.slice(i * 2, i * 2 + 2);
        binary[i] = parseInt(h, 16);
      }

      let base64String = "";
      for (let i = 0; i < binary.length; i++) {
        base64String += String.fromCharCode(binary[i]);
      }

      // btoa > convert binary string into base64
      return "data:image/png;base64," + btoa(base64String);
    }
  }

  createFileFromHex(hexString, fileName, mimeType) {
    let bytes = [];
    for (let i = 0; i < hexString.length; i += 2) {
      bytes.push(parseInt(hexString.substr(i, 2), 16));
    }
    let binaryData = new Uint8Array(bytes);
    let blob = new Blob([binaryData], {type: mimeType});
    let file = new File([blob], fileName, {type: mimeType});
    return file;
  }


  getFileIcon(path: string): string {
    const format = path?.split(".")[1];
    if (ImgFormat.includes(format)) return this.svgManager.img;
    if (WordFormat.includes(format)) return this.svgManager.word;
    if (ExcelFormat.includes(format)) return this.svgManager.excel;
    if (PdfFormat == format) return this.svgManager.pdf;
    else return this.svgManager.file;
  }

}
