import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { ReligionComponent } from "app/system-setup/hr-setup/components/religion/religion.component";
import { IReligionModel } from "app/system-setup/hr-setup/models/religion";
import { ReligionService } from "app/system-setup/hr-setup/services/religion.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "religion-dropdown",
  templateUrl: "./religion-dropdown.component.html",
  styleUrls: ["./religion-dropdown.component.scss"],
})
export class ReligionDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "religion";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IReligionModel[]>();
  religions: IReligionModel[];
  viewsManager = ViewsManager;

  constructor(private religionService: ReligionService) {
    super();
  }

  ngOnInit(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllReligions();
  }

  getAllReligions() {
    this.religionService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IReligionModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.religions = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(ReligionComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllReligions();
      });
  }

  onChange(values: IReligionModel[]) {
    this.change.emit(values);
  }
}
