import {Injectable} from '@angular/core';
import {IAttachmentTypesModel} from 'app/system-setup/general-setup/models/attachment-types';
import {
  IEmpHiringDocumentModel,
  IEmpHiringDocumentDeleteRequest,
} from 'app/personnel-setup/models/emp-employment-info/hiring-documents';
import {IPageParams} from 'app/shared/interfaces/params';
import {IResponse, IResponseList} from 'app/shared/interfaces/responces';
import {AppClientService} from 'app/shared/services/app-client.service';

@Injectable({
  providedIn: "root",
})
export class EmployeeHiringDocumentService extends AppClientService {
  constructor() {
    super();
  }

  create(data: FormData) {
    return this.post<IResponse<IEmpHiringDocumentModel>>(
      "Personnel/Employees/Create_EmployeeDocument",
      data
    );
  }

  update(data: FormData) {
    return this.post<IResponse<IEmpHiringDocumentModel>>(
      "Personnel/Employees/Edit_EmployeeDocument",
      data
    );
  }

  getList(employeeInstanceId: number, params: IPageParams) {
    return this.get<IResponseList<IEmpHiringDocumentModel[]>>(
      "Personnel/Employees/GetList_EmployeeDocument",
      {
        params: {
          ...params,
          $filter: `ID eq ${employeeInstanceId}`,
        },
      }
    );
  }

  getAll(employeeInstanceId: number) {
    return this.get<IResponse<IEmpHiringDocumentModel[]>>(
      "Personnel/Employees/GetAll_EmployeeDocument",
      {
        params: {
          $filter: `ID eq ${employeeInstanceId}`,
        },
      }
    );
  }

  Delete(params: IEmpHiringDocumentDeleteRequest) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeDocument",
      {
        params: params,
      }
    );
  }

  DeleteFile(empId: number, attachId: number) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_Attachment_EmployeeDocument",
      {
        params: { perInsId: empId, AttachmentID: attachId },
      }
    );
  }

  // get document completion percentag
  getCard(employeeInstanceId: number) {
    return this.get<IResponse<number>>("Personnel/Employees/GetDocumentCard", {
      params: {
        perId: employeeInstanceId,
      },
    });
  }

  getAllAttachment(employeeInstanceId: number) {
    return this.get<IResponse<IAttachmentTypesModel[]>>(
      "Personnel/Employees/GetAll_AttachmentType",
      {
        params: {
          perId: employeeInstanceId,
        },
      }
    );
  }
}
