import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { IBankModel, IBankRequest } from "app/system-setup/hr-setup/models/bank";
import { BankService } from "app/system-setup/hr-setup/services/bank/bank.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";

@Component({
  selector: "app-add-bank",
  templateUrl: "./add-bank.component.html",
  styleUrls: ["./add-bank.component.scss"],
})
export class AddBankComponent extends AppHelpers implements OnInit {
  bankId: number = 0;
  form: FormGroup;
  submitted: boolean = false;

  constructor(private bankService: BankService,
              @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
              public dialogRef: MatDialogRef<AddBankComponent>) {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.params.subscribe((params) => {
      this.bankId = params.bankid ?? 0;
      // if (this.CheckNoBusinessOrCompany() && this.bankId > 0) this.getBankById();
    });

    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      ar_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
      en_Name: [null, [Validators.required, CustomValidator.whiteSpace]],
    });
    if (this.data.value) {
      this.formCtrls.ar_Name.setValue(this.data.value);
      this.formCtrls.en_Name.setValue(this.data.value);
    }

  }

  get formCtrls() {
    return this.form.controls;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.bankId > 0) {
      this.getBankById();
    }
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      const model: IBankRequest = {
        ID: this.form.value.id,
        Ar_Name: this.form.value.ar_Name,
        En_Name: this.form.value.en_Name,
      };
      model.ID == 0 ? this.create(model) : this.update(model);
    }
    // this.router.navigate([`hr-setup/${this.moduleId}/bank-index`]);
  }

  create(model: IBankRequest) {
    this.showLoading();
    this.bankService.create(model).subscribe((res: IResponse<IBankModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.addSuccessfullyMsg(res?.message?.messageTypeName);
        this.bankId = res.data.id;
        if (this.data.isDialog) this.dialogRef.close();
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading();
    });
  }

  update(model: IBankRequest) {
    this.showLoading();
    this.bankService.update(model).subscribe((res: IResponse<IBankModel>) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
      }
      else {
        this.errorList(res?.message?.messages);
      }
      this.hideLoading();
    });
  }

  getBankById() {
    this.bankService.getById(this.bankId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IBankModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.formCtrls.id.setValue(res.data[0].id);
          this.formCtrls.ar_Name.setValue(res.data[0].ar_Name);
          this.formCtrls.en_Name.setValue(res.data[0].en_Name);
        }
      });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

  onCloseDialog() {
    this.dialogRef.close();
  }
}
