import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import { CustomValidator } from "app/shared/validators/custom.validator";
import {
  ITrainingPlanModel,
  ITrainingPlanRequest,
} from "app/training/models/training-plan/training-plan";
import { TrainingPlanIndexService } from "app/training/services/traning-plan/training-plan-index.service";
import { CustomValidators } from "ngx-custom-validators";

@Component({
  selector: "app-plan-general-data",
  templateUrl: "./plan-general-data.component.html",
  styleUrls: ["./plan-general-data.component.scss"],
})
export class PlanGeneralDataComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;
  planId: number = 0;
  @Output() changePlanId = new EventEmitter<number>();
  @Output() completed = new EventEmitter<boolean>();
  // use in view
  @Input() plan: ITrainingPlanModel;
  @Output() closeAfterUpdate = new EventEmitter<ITrainingPlanModel>();

  // from add new dialog
  @Input() data: ICreateDialog;

  constructor(private trainingPlanIndexService: TrainingPlanIndexService) {
    super();
  }

  override onChangeBusinessAndCompanyId() {
    if (this.planId > 0 && this.checkNoBusinessOrCompany()) this.getPlanById();
  }

  ngOnInit(): void {
    this.form = this.fb.group(
      {
        id: [this.planId, {nonNullable: true}],
        ar_Name: [null, [Validators.required]],
        en_Name: [null, [Validators.required]],
        from_Date: [null, Validators.required],
        to_Date: [null, Validators.required],
        note: [null],
        budget: [null, [CustomValidators.gte(0)]],
      },
      {
        validator: CustomValidator.compareDates("from_Date", "to_Date"),
      }
    );

    if (this.plan) this.planId = this.plan.id;

    if (this.data?.value) {
      this.formCtrls.ar_Name.setValue(this.data.value);
      this.formCtrls.en_Name.setValue(this.data.value);
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  submit() {
    this.submitted = true;
    if (this.form.valid) {
      let data = {} as ITrainingPlanRequest;
      data.id = this.formCtrls.id.value;
      data.ar_Name = this.formCtrls.ar_Name.value;
      data.en_Name = this.formCtrls.en_Name.value;
      data.from_Date = HelpersFunctions.mapDateReverse(
        this.formCtrls.from_Date.value
      );
      data.to_Date = HelpersFunctions.mapDateReverse(
        this.formCtrls.to_Date.value
      );
      data.budget = this.formCtrls.budget.value;
      data.note = this.formCtrls.note.value;

      if (data.id == 0) this.create(data);
      else this.update(data);
    }
  }

  getPlanById() {
    // edit in view
    if (this.plan) this.form.patchValue(this.plan);
    // edit after add if change company
    else {
      this.showLoading("general");
      this.trainingPlanIndexService
        .getById(this.planId)
        .subscribe((res: IResponse<ITrainingPlanModel[]>) => {
          if ((res.message.messageType = MessageTypes.Success)) {
            this.form.patchValue(res.data[0]);
          }
          else this.errorList(res?.message?.messages);
          this.hideLoading("general");
        });
    }
  }

  create(data: ITrainingPlanRequest) {
    this.showLoading("general");
    this.trainingPlanIndexService
      .create(data)
      .subscribe((res: IResponse<ITrainingPlanModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.planId = res.data.id;
          this.formCtrls.id.setValue(this.planId);
          if (!this.data.isDialog) {
            let url = `/training/${this.moduleId}/training-plan/edit/${this.planId}`;
            this.navigateTo(url);
          }
          this.completed.emit(true);
          this.changePlanId.emit(this.planId);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("general");
      });
  }

  update(data: ITrainingPlanRequest) {
    this.showLoading("general");
    this.trainingPlanIndexService
      .update(data)
      .subscribe((res: IResponse<ITrainingPlanModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.updateSuccessfullyMsg(res?.message?.messageTypeName);
          this.closeAfterUpdate.emit(res.data);
          this.completed.emit(true);
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading("general");
      });
  }

  clearForm() {
    this.form.reset();
    this.submitted = false;
  }
}
