import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IOrganizationModel } from "app/system-setup/org-management-setup/models/organizations";
import { OrganizationsService } from "app/system-setup/org-management-setup/services/organizations.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-organization-by-org-type-dropdown",
  templateUrl: "./organization-by-org-type-dropdown.component.html",
  styleUrls: ["./organization-by-org-type-dropdown.component.scss"],
})
export class OrganizationByOrgTypeDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() orgTypeControl: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "organization";
  @Input() disable: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<IOrganizationModel[]>();

  // added from current structure
  organizationList: IOrganizationModel[] = [];

  constructor(private organizationsService: OrganizationsService) {
    super();
  }

  ngOnInit(): void {
    this.control!.disable();

    this.orgTypeControl?.valueChanges.subscribe((orgTypeId) => {
      this.control!.setValue(null);
      if (orgTypeId > 0) {
        this.getAllOrganizationsByOrgType();
        if (!this.disable) this.control.enable();
      }
      else {
        this.control.disable();
        this.organizationList = null;
      }
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.orgTypeControl.value > 0 && this.checkNoBusinessOrCompany())
      this.getAllOrganizationsByOrgType();
  }

  getAllOrganizationsByOrgType() {
    this.organizationsService
      .GetAllInCurrentStructureByOrgType(this.orgTypeControl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IOrganizationModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.organizationList = res.data;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  onChange(organization: IOrganizationModel[]) {
    //  console.log("organization", organization);
    this.change.emit(organization);
  }
}
