import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IRecruitmentRequestModel } from "app/recruitment/models/recruitment-request";
import { RecruitmentRequestService } from "app/recruitment/services/recruitment-request.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-recruitment-request-by-plan-dropdown",
  templateUrl: "./recruitment-request-by-plan-dropdown.component.html",
  styleUrls: ["./recruitment-request-by-plan-dropdown.component.scss"],
})
export class RecruitmentRequestByPlanDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "recruitmentRequest";
  @Input() appendTo: string | HTMLElement;
  @Input() disable: boolean = false;
  @Input() planControl: FormControl;
  @Output() change = new EventEmitter<IRecruitmentRequestModel[]>();
  requestList: IRecruitmentRequestModel[] = [];
  // viewName: string;

  constructor(private recruitmentRequestService: RecruitmentRequestService) {
    super();
    // this.viewName = ViewsManager.assign_request_to_plan;
  }

  ngOnInit(): void {
    this.control.disable();
    this.planControl.valueChanges.subscribe((planId) => {
      this.control.setValue(null);
      if (planId > 0) {
        this.getAllRequestsByPlanId();
        if (!this.disable) this.control.enable();
      } else this.control.disable();
    });
  }

  getAllRequestsByPlanId() {
    this.recruitmentRequestService
      .getAllByPlanId(this.planControl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IRecruitmentRequestModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.requestList = res.data;
        } else {
          this.errorList(res.message?.messages);
        }
      });
  }

  onChange(request: IRecruitmentRequestModel[]) {
    this.change.emit(request);
  }

  // createDialog(value: string) {
  //   this.dialogsService
  //     .addFromDropdownDialog(
  //       assignRequestToPlan,
  //       value,
  //       this.planControl.value
  //     )
  //     .afterClosed()
  //     .subscribe((res) => {
  //       this.getAllRequestsByPlanId();
  //     });
  // }
}
