import {
  AfterViewChecked,
  Component,
  EventEmitter,
  HostListener,
  inject,
  Input,
  OnChanges,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild,
} from "@angular/core";
import { MatPaginator, PageEvent } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { IRowActionsModel } from "app/shared/interfaces/user-views";
import { AppHelpers, Page } from "app/shared/resources/app-helpers";
import { AssetsManager } from "app/shared/resources/assets-manager";
import { FileService } from "app/shared/services/file.service";
import { Paginator } from "primeng/paginator";
import { HelpersFunctions } from "app/shared/resources/helpers-functions";
import * as FileSaver from "file-saver";
import { Router } from "@angular/router";
import { Constants } from "app/shared/resources/constants";
import { Languages } from "app/shared/enums/languages.enum";
import { Storage } from "app/shared/resources/local-storage";

export interface IMergeImgWithColumnData {
  isMerge: boolean;
  columnImgName: string;
  columnTextName: string;
}

export interface IAttachIconColumnData {
  columnName: string;
  icon: string;
}

@Component({
  selector: "app-material-table",
  templateUrl: "./material-table.component.html",
  styleUrls: ["./material-table.component.scss"],
})
export class MaterialTableComponent implements OnInit, OnChanges, AfterViewChecked {
  public router = inject(Router);

  @Input() dataSource: MatTableDataSource<any> | any;
  @Input() mandatoryColomns: string[] = [];
  @Input() optionalColomns: string[] = [];
  @Input() page: Page;
  @Input() actions: IRowActionsModel;
  @Input() isHexImg: boolean = false;
  @Input() showPaginator: boolean = true;
  @Input() showFilter: boolean = true;
  @Input() showLoader: boolean = true;
  @Input() isLoaded: boolean = false;
  @Input() isDialogSize: boolean = false;
  @Input() isMatPaginator: boolean = false; // for material paginator
  @Input() showSpaceTop: boolean = true;
  @Input() showFirstLastButtons: boolean = true; // for material paginator
  @Input() hidePageSize: boolean = false; // for material paginator
  @Input() class: string = "";
  @Input() isRowClickable: boolean;
  @Input() checkboxColumnName: string = Constants.checkboxColumnName;
  @Input() exportService: any; // send index service to call getAll (ExportExcel) Function from service
  @Input() mergeImgWithName: IMergeImgWithColumnData = null;
  @Input() attachIconColumnData: IAttachIconColumnData = null;
  @Output() onChangePage = new EventEmitter<PageEvent>();
  @Output() deleteRow = new EventEmitter<any>();
  @Output() editRow = new EventEmitter<any>();
  @Output() rowactions = new EventEmitter<any>();
  @Output() selectedItems = new EventEmitter<any[]>(); // emitt selected items list

  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild("PrimePaginator", {static: true}) PrimePaginator: Paginator;

  appHelpers: AppHelpers = new AppHelpers();
  parentView: string;
  params;
  displayedColumns: string[] = [];
  assetsManager: AssetsManager = new AssetsManager();
  input: HTMLInputElement;

  allowClickRow: boolean = false;

  selectedItemsList = []; // use to save list of selected items
  checkedAll = false; // using for check optional Columns if selected all
  constructor(public fileService: FileService) {
  }

  ngOnInit(): void {
    this.displayedColumns = this.mandatoryColomns.slice();
    this.parentView = this.appHelpers.getViewName;
    this.params = {
      parentView: this.parentView,
      ...this.appHelpers.pageParams,
    };
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      !changes.dataSource?.firstChange &&
      changes.dataSource?.currentValue.data
    ) {
      this.isLoaded = true;
      if (this.checkboxColumnName) {
        this.changeCheckbox();
        this.changeCheckBoxPosition(100);
      }
    }

    // check if can using row click or no
    if (this.isRowClickable) {
      let url = this.actions?.basic?.find(e => e.isRow && (e.name.toLowerCase() == "edit" || e.name.toLowerCase() == "view"))?.path;
      if (!url) {
        this.allowClickRow = false;
      }
      else {
        this.allowClickRow = true;
      }
    }

    if(changes.mandatoryColomns) {
      this.displayedColumns = this.mandatoryColomns.slice();
    }
  }

  ngAfterViewChecked() {
    if (this.sort) {
      this.dataSource.sort = this.sort;
    }
  }


  onClickRow(ele) {
    if (this.isRowClickable) {
      let url = this.actions?.basic?.find(e => e.isRow && (e.name.toLowerCase() == "edit" || e.name.toLowerCase() == "view"))?.path;
      this.router.navigate([`${url}/${ele?.id}`], {queryParams: this.params});
    }
  }


  // page change event
  // material paginator
  change(page: PageEvent) {
    this.params.page = page.pageIndex + 1;
    this.onChangePage.emit(page);
    this.dataSource = null;
    this.isLoaded = false;
  }

  // primeng paginator
  changePage(event: {
    first: number;
    page: number;
    pageCount: number;
    rows: number;
  }) {
    this.params.page = event.page + 1;
    this.dataSource = null;
    this.isLoaded = false;
    let page: Page = new Page();
    page.pageSize = event.rows;
    page.pageIndex = event.page;
    page.length = event.rows * event.pageCount;
    // page.meta.totalItemCount = event.rows * event.pageCount;
    this.onChangePage.emit(page);
  }

  onAction(event: { data; fun }) {
    this.rowactions.emit({data: event.data, fun: event.fun});
  }

  onEdit(data) {
    this.editRow.emit(data);
  }

  onDelete(data) {
    this.deleteRow.emit(data);
  }

  // row action event
  // Action(data: any, fun: IGUIModel) {
  //   if (fun.name.toLocaleLowerCase().search('edit') != -1) {
  //     this.edit(data, fun);
  //   }
  //   if (fun.name.toLocaleLowerCase().search('delete') != -1) {
  //     this.delete(data);
  //   } else {
  //     this.rowactions.emit({data, fun: fun});
  //   }
  // }
  //
  // // edit row event
  // edit(data, fun: IGUIModel) {
  //   //let row = {} as any;
  //   data.function = fun;
  //   this.editRow.emit(data);
  // }
  //
  // // delete row event
  // delete(data) {
  //   this.deleteRow.emit(data);
  // }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  editColumnsToView(index: number, selected: boolean) {
    if (selected) {
      this.displayedColumns.splice(
        this.mandatoryColomns.includes("actions")
          ? this.displayedColumns.length - 1
          : this.displayedColumns.length,
        0,
        this.optionalColomns[index]
      );
      let checkedAll = true;
      this.optionalColomns.forEach(e => {
        if (!this.displayedColumns.includes(e)) checkedAll = false;
      });
      this.checkedAll = checkedAll;
    }

    else {
      let indexInTable = this.displayedColumns.findIndex(
        (ele) => ele == this.optionalColomns[index]
      );
      this.displayedColumns.splice(indexInTable, 1);
      let checkedAll = true;
      this.optionalColomns.forEach(e => {
        if (!this.displayedColumns.includes(e)) checkedAll = false;
      });
      this.checkedAll = checkedAll;
      // if (this.mandatoryColomns == this.displayedColumns) {
      //   this.checkedAll = false;
      // }
    }
  }

  // select cols to display (For cols in table)
  selectAllColumnsToView(selected: boolean) {
    let mandatory = [];
    this.mandatoryColomns.forEach(e => {
        if (!mandatory.includes(e)) mandatory.push(e);
      }
    );
    if (selected) {
      this.displayedColumns = mandatory;
      this.displayedColumns.splice(
        this.mandatoryColomns.includes("actions")
          ? this.displayedColumns.length - 1
          : this.displayedColumns.length,
        0,
        ...this.optionalColomns
      );
      this.checkedAll = true;
    }
    else {
      this.optionalColomns.forEach(e => {
        if (this.displayedColumns.includes(e))
          this.displayedColumns.splice(this.displayedColumns.findIndex((ele) => ele == e), 1);
      });
      this.checkedAll = false;
    }
  }

  isImage(col: string, value: any = ""): boolean {
    return !this.isAttach(col) &&
    (col == "logo" ||
      (typeof value === "string" && value.includes("Attachments/")) ||
      // display image with en name
      (this.mergeImgWithName?.isMerge &&
        col == this.mergeImgWithName?.columnTextName))
      ? true
      : false;
  }

  isIcon(col: string): boolean {
    return col == "icon" ? true : false;
  }

  isAttach(col: string): boolean {
    return this.attachIconColumnData?.columnName == col ? true : false;
  }

  isBooleanType(value: any): boolean {
    return typeof value === "boolean";
  }

  isCheckbox(col: string) {
    return col === Constants.selectColName;
  }

  // select & deselect all checkboxes (For data in table)
  selectAll(checkboxValue: boolean) {
    this.dataSource.data.forEach((ele) => {
      ele[this.checkboxColumnName] = checkboxValue;
    });
    this.selectedItemsList = this.dataSource.data.filter(e => e.isSelected);
    this.selectedItems.emit(this.selectedItemsList);
  }

  // update select all checkbox if any single one changed
  isAllCheckboxSelected: boolean = false;

  changeCheckbox() {
    this.isAllCheckboxSelected = this.dataSource.data.every(
      (ele) => ele[this.checkboxColumnName]
    );
    this.selectedItemsList = this.dataSource.data.filter(e => e.isSelected);
    this.selectedItems.emit(this.selectedItemsList);
  }


  /*#region START: Export data to excel */

  // get all table data to export as excel
  getAllDataToExport() {
    if (this.exportService) {
      this.appHelpers.showLoading();
      if (this.exportService?.exportExcel()) {
        this.exportService?.exportExcel().subscribe((res) => {
          this.exportExcel(res.data);
        });
      }
      else {
        this.exportService.getAll().subscribe((res) => {
          this.exportExcel(res.data);
        });
      }
    }
  }

  exportExcel(data) {
    data?.map(e => {
      delete e?.creat_User_ID;
      delete e?.create_Date;
      delete e?.last_Update_Date;
      delete e?.last_Update_User_ID;
      delete e?.is_Delete;
    });
    import("xlsx").then((xlsx) => {
      const worksheet = xlsx.utils?.json_to_sheet(data);
      worksheet["A1"].v = worksheet["A1"].v.substring(0, 32767);
      const workbook = {Sheets: {data: worksheet}, SheetNames: ["data"]};
      const excelBuffer: any = xlsx.write(workbook, {
        bookType: "xlsx",
        type: "array",
      });
      this.saveAsExcelFile(
        excelBuffer,
        this.appHelpers.title.getTitle().toLowerCase()
      );
    });
  }

  saveAsExcelFile(buffer: any, fileName: string): void {
    let EXCEL_TYPE =
      "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8";
    let EXCEL_EXTENSION = ".xlsx";
    const data: Blob = new Blob([buffer], {
      type: EXCEL_TYPE,
    });
    FileSaver.saveAs(
      data,
      fileName +
      "_" +
      HelpersFunctions.mapDate(new Date().toDateString()) +
      EXCEL_EXTENSION
    );
    this.appHelpers.hideLoading();
  }

  @HostListener("window:resize", ["$event"])
  onWindowResize(event) {
    this.changeCheckBoxPosition();
  }

  changeCheckBoxPosition(time = 0) {
    setTimeout(() => {
      let checkboxLabelWidth = document.getElementById(
        "checkbox-header-label"
      )?.offsetWidth;
      document
        .querySelectorAll(".td-checkbox")
        .forEach((element: HTMLElement) => {
          if (Storage.getLocalLang == Languages.ar)
            element.style.marginRight = `${checkboxLabelWidth + 8}px`;
          else element.style.marginLeft = `${checkboxLabelWidth + 8}px`;
        });
    }, time);
  }
}

//#endregion
