import { Component, Input, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IMilitaryStatusModel } from "app/system-setup/hr-setup/models/military";
import { MilitaryService } from "app/system-setup/hr-setup/services/military.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-military-status-dropdown",
  templateUrl: "./military-status-dropdown.component.html",
  styleUrls: ["./military-status-dropdown.component.scss"],
})
export class MilitaryStatusDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "status";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IMilitaryStatusModel[]>();

  militaryStatusList: IMilitaryStatusModel[];

  constructor(private militaryService: MilitaryService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllMilitaryStatus();
  }

  getAllMilitaryStatus() {
    this.militaryService
      .getAllMilitaryStatus()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IMilitaryStatusModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.militaryStatusList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(values: IMilitaryStatusModel[]) {
    this.change.emit(values);
  }
}
