import { Component, Input, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import {
  IPerson,
  IPersonInstanceModel,
} from "app/personnel-setup/models/employees";
import { AssignPlanService } from "app/personnel-setup/services/employees/assign-plan.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-employee-by-policy-group-dropdown",
  templateUrl: "./employee-by-policy-group-dropdown.component.html",
  styleUrls: ["./employee-by-policy-group-dropdown.component.scss"],
})
export class EmployeeByPolicyGroupDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() policyGroupCtrl?: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "employees";

  employeeList: IPerson[] = [];

  constructor(private assignPlanService: AssignPlanService) {
    super();
  }

  ngOnInit(): void {
    this.control.disable();
    this.policyGroupCtrl?.valueChanges.subscribe((policyId) => {
      this.control.setValue(null);
      if (policyId > 0) {
        this.getEmployeesByPolicyGroup();
        if (!this.disable) this.control.enable();
      } else {
        this.employeeList = [];
        this.control.disable();
      }
    });
  }

  getEmployeesByPolicyGroup() {
    this.assignPlanService
      .getEmployeesByPolicyGroup(this.policyGroupCtrl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPersonInstanceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.employeeList = res.data.map((ele) => ele.core_Person);
        } else {
          this.errorList(res?.message?.messages);
        }
      });
  }
}
