import { Component, EventEmitter, Input, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IErpResourceGroupModel } from "app/personnel-setup/models/emp-sap-integration/erp-integration";
import { EmployeeErpIntegrationService } from "app/personnel-setup/services/employees/sap-integration/employee-erp-integration.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-erp-resource-group-dropdown",
  templateUrl: "./erp-resource-group-dropdown.component.html",
  styleUrls: ["./erp-resource-group-dropdown.component.scss"],
})
export class ErpResourceGroupDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "resourceGroup";
  @Input() appendTo: string | HTMLElement;
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<IErpResourceGroupModel[]>();

  resourceGroupList: IErpResourceGroupModel[];

  constructor(
    private employeeErpIntegrationService: EmployeeErpIntegrationService
  ) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.employeeErpIntegrationService
      .getAllErpGroupCodeCustomer()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IErpResourceGroupModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.resourceGroupList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(values: IErpResourceGroupModel[]) {
    this.change.emit(values);
  }
}
