import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IMilitaryGradeModel, IMilitaryStatusModel } from "../models/military";

@Injectable({
  providedIn: "root",
})
export class MilitaryService extends AppClientService {
  constructor() {
    super();
  }

  getAllMilitaryStatus() {
    return this.get<IResponse<IMilitaryStatusModel[]>>(
      "HRSetup/MilitryStatus/GetAll"
    );
  }

  getAllMilitaryGrades() {
    return this.get<IResponse<IMilitaryGradeModel[]>>(
      "HRSetup/MilitryGrade/GetAll"
    );
  }
}
