import { Component, Input, OnInit, SimpleChanges, Inject } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import {
  ISapSystemInstancePurposeModel,
  ISapSystemInstancePurposeRequest,
} from "app/system-setup/sap-business-one-setup/models/system-instance";
import { SystemInstancePurposeService } from "app/system-setup/sap-business-one-setup/services/system-instance-purpose.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { sapSystemInstancePurposeToMap } from "app/system-setup/sap-business-one-setup/mapper/sap-system-instance";

@Component({
  selector: "app-system-instance-purpose",
  templateUrl: "./system-instance-purpose.component.html",
  styleUrls: ["./system-instance-purpose.component.scss"],
})
export class SystemInstancePurposeComponent extends AppHelpers implements OnInit {
  @Input() systemInstanceId: any;

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = ["Ser", "id", "purpose", "procedure_Name", "actions"];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<ISapSystemInstancePurposeModel>;

  constructor(
    private systemInstancePurposeService: SystemInstancePurposeService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<SystemInstancePurposeComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    // if (this.systemInstanceId && this.CheckNoBusinessOrCompany()) this.getAll();

    this.form = this.fb.group({
      id: [0, {nonNullable: true}],
      purpose: [null, [Validators.required]],
      procedureName: [null, [Validators.required]],
    });
    if (this.data.value) {
      this.formCtrls.purpose.setValue(this.data.value);
      this.systemInstanceId = this.data.id;
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    // if (changes.systemInstanceId.currentValue > 0) this.getAll();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.systemInstanceId && this.checkNoBusinessOrCompany()) this.getAll();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    if (!this.data.isDialog) {
      this.isLoaded = false;
      this.systemInstancePurposeService
        .getAllBySystemInstanceId(this.systemInstanceId)
        .pipe(this.cancelRequest())
        .subscribe((res: IResponse<ISapSystemInstancePurposeModel[]>) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.dataSource = new MatTableDataSource(sapSystemInstancePurposeToMap(res.data));
            this.isLoaded = true;
          }
          else this.errorList(res?.message?.messages);
        });
    }
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ISapSystemInstancePurposeRequest = {
        SystemInstance_ID: this.systemInstanceId,
        ID: this.form.value.id,
        Name: this.form.value.purpose,
        Procedure_Name: this.form.value.procedureName,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: ISapSystemInstancePurposeRequest) {
    this.showLoading();
    this.systemInstancePurposeService
      .create(model)
      .subscribe((res: IResponse<ISapSystemInstancePurposeModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (this.data.isDialog) this.dialogRef.close();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  onDelete(systemInstancePurpose: ISapSystemInstancePurposeModel): void {
    let msg = this.msgTranslate(systemInstancePurpose.name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(systemInstancePurpose);
    });
  }

  delete(systemInstancePurpose: ISapSystemInstancePurposeModel) {
    this.showLoading();
    this.systemInstancePurposeService
      .Delete(systemInstancePurpose.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset({id: 0});
    this.submitted = false;
  }

  onCloseDialog() {
    this.dialogRef.close();
  }
}
