import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {InsuranceOfficeService} from "app/system-setup/hr-setup/services/insurance-office.service";
import {AbstractControl, FormControl} from "@angular/forms";
import {IInsuranceOfficeModel} from "app/system-setup/hr-setup/models/insurance-office";
import {ViewsManager} from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import {IResponse} from "app/shared/interfaces/responces";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {InsuranceOfficeComponent} from "app/system-setup/hr-setup/components/insurance-office/insurance-office.component";

@Component({
  selector: 'insurance-office-dropdown',
  templateUrl: './insurance-office-dropdown.component.html',
  styleUrls: ['./insurance-office-dropdown.component.scss']
})
export class InsuranceOfficeDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl | AbstractControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "insuranceOffice";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IInsuranceOfficeModel[]>();

  insuranceOfficeList: IInsuranceOfficeModel[];
  viewName: string;

  constructor(private insuranceOfficeService: InsuranceOfficeService) {
    super();
    this.viewName = ViewsManager.insurance_office;
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(InsuranceOfficeComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }


  getAll() {
    this.insuranceOfficeService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IInsuranceOfficeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.insuranceOfficeList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  onChange(values: IInsuranceOfficeModel[]) {
    this.change.emit(values);
  }

}
