import { Injectable } from "@angular/core";
import {
  IEmployeeDependantModel,
  IEmployeeDependantRequest,
} from "app/personnel-setup/models/emp-personal-info/dependants";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
  providedIn: "root",
})
export class EmployeeDependantService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeDependantRequest) {
    return this.post<IResponse<IEmployeeDependantModel>>(
      "Personnel/Employees/Create_EmployeeDependent",
      data
    );
  }

  update(data: IEmployeeDependantRequest) {
    return this.post<IResponse<IEmployeeDependantModel>>(
      "Personnel/Employees/Edit_EmployeeDependent",
      data
    );
  }

  getAll(employeeId: number) {
    return this.get<IResponse<IEmployeeDependantModel[]>>(
      "Personnel/Employees/GetAll_EmployeeDependent",
      {
        params: {
          $filter: `Person_ID eq ${employeeId}`,
          $expand: `${db.corE_Family_Relation}`,
        },
      }
    );
  }

  getList(employeeId: number, params: IPageParams) {
    return this.get<IResponseList<IEmployeeDependantModel[]>>(
      "Personnel/Employees/GetList_EmployeeDependent",
      {
        params: {
          ...params,
          $filter: `Person_ID eq ${employeeId}`,
          $expand: `${db.corE_Family_Relation}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeDependent",
      {
        params: { Id: Id },
      }
    );
  }

  getAllFamilyRelationships() {
    return this.get<IResponse<Idropdown[]>>(
      "Personnel/Employees/GetAll_Relations"
    );
  }
}
