import {
  Component,
  EventEmitter,
  Input, OnChanges,
  Output,
  SimpleChanges,
} from "@angular/core";
import { FormControl } from "@angular/forms";
import { ThemePalette } from "@angular/material/core";
import { Constants } from "app/shared/resources/constants";

@Component({
  selector: "slider",
  templateUrl: "./slider.component.html",
  styleUrls: ["./slider.component.scss"],
})
export class SliderComponent implements OnChanges {
  @Input() control: FormControl;
  @Input() submitted?: boolean = false;
  @Input() label: string;
  @Input() disabled?: boolean = false;
  @Input() class?: string;
  @Input() boxClass?: string;
  @Input() id?: string;
  @Input() color?: ThemePalette = Constants.defaultSliderColor;
  @Input() min?: number = 0;
  @Input() max?: number = 100;
  @Input() step?: number = 1;
  // show label with value when change slider
  @Input() thumbLabel?: boolean = true;
  // function to format displayed value on slider
  @Input() displayWith?: (value: number) => string;
  @Input() invert?: boolean = false;
  @Input() vertical?: boolean = false;
  // show intervals on slider
  @Input() tickInterval?: number = null;
  @Output() onChange? = new EventEmitter<number>();

  ngOnChanges(changes: SimpleChanges): void {
    if (this.disabled) this.control?.disable();
    else if (!this.disabled) this.control?.enable();
  }

  change(value: number) {
    this.onChange.emit(value);
  }
}
