import {Component, OnInit, Input, Output, EventEmitter} from "@angular/core";
import {IGUIModel, IRowActionsModel} from "app/shared/interfaces/user-views";

@Component({
  selector: "page-title",
  templateUrl: "./page-title.component.html",
  styleUrls: ["./page-title.component.scss"],
})
export class PageTitleComponent implements OnInit {
  @Input() id: number;
  @Input() addTitle: string;
  @Input() editTitle: string;
  @Input() title?: string;
  @Input() color?: string;
  @Input() icon?: string;
  @Input() globalFunctions?: IGUIModel[] = [];
  @Output() action = new EventEmitter<any>();

  constructor() {
  }

  ngOnInit(): void {
  }

  Action(globalFun: IRowActionsModel) {
    this.action.emit(globalFun);
  }
}
