import {Component, EventEmitter, Input, Output} from "@angular/core";
import {IGUIModel} from "app/shared/interfaces/user-views";
import {AppHelpers} from "app/shared/resources/app-helpers";
import { DeviceDetectorService } from "ngx-device-detector";

@Component({
  selector: "app-global-functions",
  templateUrl: "./global-functions.component.html",
  styleUrls: ["./global-functions.component.scss"],
})
export class GlobalFunctionsComponent {
  @Input() functions: IGUIModel[];
  @Output() action = new EventEmitter<any>();
  appSettings: AppHelpers = new AppHelpers();
  parentView: string;
  params: {};

  constructor(public detectorService:DeviceDetectorService) {
  }

  ngOnInit() {
    this.parentView = this.appSettings.getViewName;
    this.params = {
      parentView: this.parentView,
      ...this.appSettings.parentModuleIdParam,
    };
  }


  Action(globalFun: IGUIModel) {
    this.action.emit(globalFun);
  }
}
