import { Component, EventEmitter, inject, Input, Output, SimpleChanges } from "@angular/core";
import { AbstractControl, FormControl } from "@angular/forms";
import { Constants } from "app/shared/resources/constants";
import { LanguageService } from "app/shared/services/language.service";

@Component({
  selector: "app-datepicker",
  templateUrl: "./datepicker.component.html",
  styleUrls: ["./datepicker.component.scss"],
})
export class DatepickerComponent {
  @Input() control: FormControl | AbstractControl;
  @Input() label: string;
  @Input() placeholder?: string;
  @Input() submitted: boolean = false;
  @Input() appearance: string = Constants.defaultInputAppearance;
  @Input() maxDate: Date = null;
  @Input() minDate: Date = null;
  @Input() disabled?: boolean = false;
  @Input() readonly?: boolean = false;

  @Output() keyUp? = new EventEmitter<any>();
  @Output() keyDown? = new EventEmitter<any>();
  @Output() keyPress? = new EventEmitter<any>();
  @Output() keyEnter? = new EventEmitter<any>();
  @Output() change? = new EventEmitter<any>();
  isFloatLabel: boolean = Constants.defaultFloatLabel;

  public languageService = inject(LanguageService);

  constructor() {

  }

  ngOnChanges(changes: SimpleChanges): void {
    if (this.disabled) {
      this.control?.disable();
    }
    else if (!this.disabled) {
      this.control?.enable();
    }
    //	else this.control?.enable();
  }

  onkeyup(value: any) {
    this.keyUp.emit(value);
  }

  onkeydown(value: any) {
    this.keyDown.emit(value);
  }

  onkeypress(value: any) {
    this.keyPress.emit(value);
  }

  onkeyEnter(value: any) {
    this.keyEnter.emit(value);
  }

  onChange(value: any) {
    this.change.emit(value);
  }
}
