import { Injectable } from "@angular/core";
import { SweetAlertIcon } from "sweetalert2";
import Swal from "sweetalert2/dist/sweetalert2.js";
import { LanguageService } from "./language.service";

interface AlertOptions {
	title: string;
	icon: SweetAlertIcon;
	iconColor?: any;
	describtion?: string;
	showCancelButton?: boolean;
	html?: any;
}

@Injectable({
	providedIn: "root",
})
export class SweetAlertService {
	constructor(private langService: LanguageService) {}

	SweetAlert(options: AlertOptions) {
		Swal.fire({
			title: this.langService.translate(options.title),
			text: this.langService.translate(options.describtion ?? ""),
			icon: options.icon,
			iconColor: options.iconColor,
			showCancelButton: options.showCancelButton ?? false,
			confirmButtonColor: "#3085d6",
			cancelButtonColor: "#d33",
			html: options.html,
		});
	}

	public customSwalOptions = {
		showCancelButton: true,
		focusConfirm: false,
		returnFocus: false,
		customClass: {
			confirmButton: "btn btn-info bg-light-info mb-1",
			closeButton: "btn btn-danger bg-light-danger mr-1 mb-1",
			denyButton: "btn btn-danger bg-light-danger mr-1 mb-1",
			cancelButton: "btn btn-danger bg-light-danger mr-1 mb-1",
		},
	};
}
