import { Component, EventEmitter, Input, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { GenderService } from "app/system-setup/hr-setup/services/gender.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { Idropdown } from "app/shared/interfaces/dropdown.interface";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-gender-dropdown",
  templateUrl: "./gender-dropdown.component.html",
  styleUrls: ["./gender-dropdown.component.scss"],
})
export class GenderDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() inculdeBoth: boolean = false;
  @Input() label: string = "gender";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<Idropdown[]>();
  genderList: Idropdown[];

  constructor(private genderService: GenderService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllGenders();
  }

  getAllGenders() {
    (this.inculdeBoth
      ? this.genderService.getAllIncludeBoth()
      : this.genderService.getAll()
    )
      .pipe(this.cancelRequest())
      .subscribe((res: any) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.genderList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(values: Idropdown[]) {
    this.change.emit(values);
  }
}
