import {Component, OnInit} from "@angular/core";
import {MatDialogRef} from "@angular/material/dialog";
import {ISaveEmpGeneralInfo} from "app/personnel-setup/models/employees";
import {EmpGeneralInfoService} from "app/personnel-setup/services/employees/general-info/emp-general-info.service";
import {AppHelpers} from "app/shared/resources/app-helpers";

@Component({
	selector: "app-employee-general-info-dialog",
	templateUrl: "./emp-general-info-dialog.component.html",
	styleUrls: ["./emp-general-info-dialog.component.scss"],
})
export class EmployeeGeneralInfoDialogComponent extends AppHelpers implements OnInit {
	employeeId: number = 0;
	constructor(public dialogRef: MatDialogRef<EmployeeGeneralInfoDialogComponent>) {
		super();
		this.employeeId = EmpGeneralInfoService.EmployeeId;
	}

	ngOnInit(): void {}

	onSave(data: ISaveEmpGeneralInfo) {
		this.dialogRef.close({ isUpdate: data.isUpdate, data: data.data });
	}
}
