import { Injectable } from "@angular/core";
import {
  IEmployeeErpIntegrationModel,
  IEmployeeErpIntegrationRequest,
  IErpGroupCodeCustomerModel,
  IErpGroupCodeVendorModel,
  IErpResourceGroupModel,
} from "app/personnel-setup/models/emp-sap-integration/erp-integration";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class EmployeeErpIntegrationService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeErpIntegrationRequest) {
    return this.post<IResponse<IEmployeeErpIntegrationModel>>(
      "Personnel/Employees/Create_ERP_Integration",
      data
    );
  }

  // update(data: IEmployeeErpIntegrationRequest) {
  //   return this.post<IResponse<IEmployeeErpIntegrationModel>>(
  //     "Personnel/Employees/Edit_ERP_Integration",
  //     data
  //   );
  // }

  getAll(employeeId: number) {
    return this.get<IResponse<IEmployeeErpIntegrationModel[]>>(
      "Personnel/Employees/GetAll_ERP_Integration",
      {
        params: {
          id: employeeId,
        },
      }
    );
  }

  // Delete(Id: number) {
  //   return this.delete<IResponse>(
  //     "Personnel/Employees/Delete_ERP_Integration",
  //     {
  //       params: { Id: Id },
  //     }
  //   );
  // }

  getAllErpGroupCodeVendor() {
    return this.get<IResponse<IErpGroupCodeVendorModel[]>>(
      "Personnel/Employees/GroupCodeVendor"
    ).pipe(
      map((res) => {
        res.data.map((item: IErpGroupCodeVendorModel) => {
          item.id = item.GroupCode;
          item.ar_Name = item.GroupName;
          item.en_Name = item.GroupName;
        });
        return res;
      })
    );
  }

  getAllErpGroupCodeCustomer() {
    return this.get<IResponse<IErpGroupCodeCustomerModel[]>>(
      "Personnel/Employees/GroupCodeCustomer"
    ).pipe(
      map((res) => {
        res.data.map((item: IErpGroupCodeCustomerModel) => {
          item.id = item.GroupCode;
          item.ar_Name = item.GroupName;
          item.en_Name = item.GroupName;
        });
        return res;
      })
    );
  }

  getAllErpResourceGroup() {
    return this.get<IResponse<IErpResourceGroupModel[]>>(
      "Personnel/Employees/SAP_LoadResourceGroup"
    ).pipe(
      map((res) => {
        res.data.map((item: IErpResourceGroupModel) => {
          item.id = item.GroupID;
          item.ar_Name = item.GroupName;
          item.en_Name = item.GroupName;
        });
        return res;
      })
    );
  }
}
