import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {FormControl} from "@angular/forms";
import {
  AddJobProfileComponent
} from "app/org-management/components/jobs-profile/add-job-profile/add-job-profile.component";
import {IJobTitleModel} from "app/org-management/model/job-title";
import {JobTitleService} from "app/org-management/services/job-profile/job-title.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import {ViewsManager} from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-job-title-dropdown",
  templateUrl: "./job-title-dropdown.component.html",
  styleUrls: ["./job-title-dropdown.component.scss"],
})
export class JobTitleDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl | AbortController;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "jobTitle";
  @Output() change = new EventEmitter<IJobTitleModel[]>();
  jobTitleList: IJobTitleModel[];

  viewName: string;

  constructor(private jobTitleService: JobTitleService) {
    super();
    this.viewName = ViewsManager.add_job_profile;
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllJobTitle();
  }

  getAllJobTitle() {
    this.jobTitleService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IJobTitleModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.jobTitleList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(jobTitle: IJobTitleModel[]) {
    this.change.emit(jobTitle);
  }

  createDialog(value: string) {
    console.log(value);
    this.dialogsService
      .addFromDropdownDialog(AddJobProfileComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllJobTitle();
      });
  }
}
