import { Component, Input, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IMilitaryGradeModel } from "app/system-setup/hr-setup/models/military";
import { MilitaryService } from "app/system-setup/hr-setup/services/military.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-military-grade-dropdown",
  templateUrl: "./military-grade-dropdown.component.html",
  styleUrls: ["./military-grade-dropdown.component.scss"],
})
export class MilitaryGradeDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "grade";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IMilitaryGradeModel[]>();

  militaryGradeList: IMilitaryGradeModel[];

  constructor(private militaryService: MilitaryService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllMilitaryGrade();
  }

  getAllMilitaryGrade() {
    this.militaryService
      .getAllMilitaryGrades()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IMilitaryGradeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.militaryGradeList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(values: IMilitaryGradeModel[]) {
    this.change.emit(values);
  }
}
