import { Component, EventEmitter, Inject, Input, OnInit, Output } from "@angular/core";
import { FormArray, FormGroup, Validators } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidator } from "app/shared/validators/custom.validator";
import { courseToDisplay } from "app/training/mapper/course";
import {
  ICourseInstructorRequstModel,
  ICourseToDisplay,
} from "app/training/models/course/course";
import {
  CourseHoldingYearsModel,
  ICourseGeneralRequest,
} from "app/training/models/course/general";
import { CourseGeneralService } from "app/training/services/course/course-general.service";
import { CustomValidators } from "ngx-custom-validators";
import { ICourseCategoryModel } from "app/system-setup/training-setup/models/course-category";
import { ICourseTypeModel } from "app/system-setup/training-setup/models/course-type";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";

@Component({
  selector: "app-add-course-form",
  templateUrl: "./add-course-form.component.html",
  styleUrls: ["./add-course-form.component.scss"],
})
export class AddCourseFormComponent extends AppHelpers implements OnInit {
  @Output() changeCourseId = new EventEmitter<number>();
  @Input() isGeneral: boolean = false;
  @Input() isOutline: boolean = false;
  @Input() CourseInstructor: ICourseInstructorRequstModel;
  @Output() completed = new EventEmitter<boolean>(); // complete step to change icon
  course: ICourseToDisplay;
  form: FormGroup;
  submitted: boolean = false;
  totalPrecentage: number;
  @Input() courseId: number = 0;

  constructor(
    private courseGeneralService: CourseGeneralService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<AddCourseFormComponent>
  ) {
    super();
    this.course = this.courseGeneralService.course$.getValue();
    this.courseGeneralService.course$.subscribe((course) => {
      this.course = course;
      if (this.course != null) this.getCourseById();
    });


  }

  ngOnInit(): void {
    this.form = this.fb.group({
      id: [this.courseId, {nonNullable: true}],
      ar_Name: [null, [CustomValidator.whiteSpace]],
      en_Name: [null, [CustomValidator.whiteSpace]],
      courseCode: [null, [CustomValidator.whiteSpace]],
      abbrevation: [null, Validators.maxLength(10)],
      course_Category_ID: [null],
      course_Type_ID: [null],
      isFree: [true], //1 true 2 false
      isHolding: [false], //2 true 1 false
      courseValue: [null],
      holdingYears: [null, CustomValidators.gt(0)],
      penaletyPercentege: [null],
      sixMontheEvalution: [false],
      threeMontheEvalution: [false],
      note: [null],
      trA_Course_Holding_Years: this.fb.array([]),
      outLine: [null],
      outlineDesc: [null],
    });

    if (this.isGeneral) {
      this.formCtrls.ar_Name.addValidators(Validators.required);
      this.formCtrls.en_Name.addValidators(Validators.required);
      this.formCtrls.courseCode.addValidators(Validators.required);
      this.formCtrls.isFree.addValidators(Validators.required);
    }
    if (this.data.value) {
      this.isGeneral = true;
      this.formCtrls.ar_Name.setValue(this.data.value);
      this.formCtrls.en_Name.setValue(this.data.value);
      this.formCtrls.course_Category_ID.setValue(this.data.id);
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  get TRACourseHoldingYears() {
    return this.form.controls["trA_Course_Holding_Years"] as FormArray;
  }

  getTRACourseHoldingYearsList(value: any) {
    // add new inputs
    let difference = Number(value) - this.TRACourseHoldingYears.controls.length;
    if (difference > 0) {
      for (var i = 0; i < difference; i++) {
        let TRACourseHoldingYear = this.fb.group({
          holding_Year: [i + 1],
          percentage: ["", [Validators.required, CustomValidators.gt(0)]],
        });
        this.TRACourseHoldingYears.push(TRACourseHoldingYear);
      }
    }
    // remove from old inputs
    else if (difference < 0) {
      difference = Math.abs(difference);
      for (var i = 0; i < difference; i++) {
        this.TRACourseHoldingYears.removeAt(
          this.TRACourseHoldingYears.controls.length - 1
        );
      }
    }
    // let length: number = value;
    // this.TRACourseHoldingYears.clear();
    // for (var i = 0; i < length; i++) {
    //   let TRACourseHoldingYear = this.fb.group({
    //     holding_Year: [i + 1],
    //     percentage: ["", [Validators.required, CustomValidators.gt(0)]],
    //   });
    //   this.TRACourseHoldingYears.push(TRACourseHoldingYear);
    // }
  }

  changeSumOfPercentage() {
    // update totalPrecentage
    this.totalPrecentage = 0;
    this.TRACourseHoldingYears.controls.forEach((e) => {
      this.totalPrecentage += e.value.percentage;
    });
  }

  submit() {
    this.submitted = true;

    if (this.form.valid) {
      let data = {} as ICourseGeneralRequest;
      data.Id = this.formCtrls.id.value;
      data.Ar_Name = this.formCtrls.ar_Name.value;
      data.En_Name = this.formCtrls.en_Name.value;
      data.Course_Type_ID = this.formCtrls.course_Type_ID.value;
      data.Course_Category_ID = this.formCtrls.course_Category_ID.value;
      data.Code = this.formCtrls.courseCode.value;
      data.Note = this.formCtrls.note.value;
      data.Abbrevation = this.formCtrls.abbrevation.value;
      data.IsFree = this.formCtrls.isFree.value == true ? 1 : 2;
      data.CourseValue = this.formCtrls.courseValue.value;
      data.IsHolding = this.formCtrls.isHolding.value == true ? 2 : 1;
      data.HoldingYears = this.formCtrls.holdingYears.value;
      data.Penalty = this.formCtrls.penaletyPercentege.value;
      data.ThreeMontheEvalution = this.formCtrls.threeMontheEvalution.value;
      data.SixMontheEvalution = this.formCtrls.sixMontheEvalution.value;
      data.outline = this.formCtrls.outLine.value;
      data.outlinedesc = this.formCtrls.outlineDesc.value;
      data.TRA_Course_Holding_Years = [];
      if (data.Penalty != null) {
        this.changeSumOfPercentage();
        // this.totalPrecentage = 0;
        this.TRACourseHoldingYears.controls.forEach((e) => {
          let Holding_Year = {} as CourseHoldingYearsModel;
          Holding_Year.holding_Year = e.value.holding_Year;
          Holding_Year.percentage = e.value.percentage;
          data.TRA_Course_Holding_Years.push(Holding_Year);
          // this.totalPrecentage += e.value.percentage;
        });
        if (this.totalPrecentage != data.Penalty) {
          return;
        }
      }
      if (this.courseId == 0) this.create(data);
      else this.update(data);
    }
  }

  create(model: ICourseGeneralRequest) {
    this.showLoading("form");
    let url;
    this.courseGeneralService.create(model).subscribe((res) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.addSuccessfullyMsg(res?.message?.messageTypeName);

        this.courseId = res.data.id;
        this.formCtrls.id.setValue(this.courseId);
        url = `/training/${this.moduleId}/course/edit/${this.courseId}`;
        if (!this.data.isDialog) this.navigateTo(url);
        this.courseGeneralService.course$.next(courseToDisplay([res.data])[0]);
        this.changeCourseId.emit(this.courseId);
        if (this.data.isDialog) this.dialogRef.close();
        this.completed.emit(true);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading("form");
      this.submitted = false;
    });
  }

  update(model: ICourseGeneralRequest) {
    this.showLoading("form");
    this.courseGeneralService.update(model).subscribe((res) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        this.courseGeneralService.course$.next(courseToDisplay([res.data])[0]);
        this.completed.emit(true);
      }
      else this.errorList(res?.message?.messages);
      this.hideLoading("form");
      this.submitted = false;
    });
  }

  getCourseById() {
    // this.showLoading("form");
    this.courseId = this.course.id;
    this.form.patchValue(this.course);
    if (this.course?.trA_Course_Holding_Years?.length > 0) {
      this.TRACourseHoldingYears.clear();
      this.course?.trA_Course_Holding_Years.forEach((el, i) => {
        let TRACourseHoldingYear = this.fb.group({
          holding_Year: [el.holding_Year],
          percentage: [el.percentage, [Validators.required, CustomValidators.gt(0)]],
        });
        this.TRACourseHoldingYears.push(TRACourseHoldingYear);
        this.changeSumOfPercentage();
      });
    }
    // this.hideLoading("form");
  }

  onHoldingChange() {
    if (this.formCtrls.isHolding.value == true) {
      this.formCtrls.holdingYears.addValidators(Validators.required);
      this.formCtrls.penaletyPercentege.addValidators(Validators.required);
    }
    else {
      this.formCtrls.holdingYears.setValidators(null);
      this.formCtrls.penaletyPercentege.setValidators(null);
      this.formCtrls.holdingYears.setValue(null);
      this.formCtrls.penaletyPercentege.setValue(null);
      this.TRACourseHoldingYears.clear();
    }
  }

  onFreeChange() {
    if (!this.formCtrls.isFree.value) {
      this.formCtrls.courseValue.addValidators(Validators.required);
    }
    else {
      this.formCtrls.courseValue.clearValidators();
      this.formCtrls.courseValue.setValue(null);
    }
  }

  clearForm() {
    this.form.reset();
    this.submitted = false;
  }
}
