import { Injectable } from "@angular/core";
import {
  IEmployeeS4HanaModel,
  IEmployeeS4HanaRequest,
  IS4CostCenter,
} from "app/personnel-setup/models/emp-sap-integration/s4-hana";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class EmployeeS4HanaService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IEmployeeS4HanaRequest) {
    return this.post<IResponse<IEmployeeS4HanaModel>>(
      "Personnel/Employees/Create_EmployeeS4CostCenter",
      data
    );
  }

  update(data: IEmployeeS4HanaRequest) {
    return this.post<IResponse<IEmployeeS4HanaModel>>(
      "Personnel/Employees/Edit_EmployeeS4CostCenter",
      data
    );
  }

  getList(employeeId: number, params: IPageParams) {
    return this.get<IResponseList<IEmployeeS4HanaModel[]>>(
      "Personnel/Employees/GetList_EmployeeS4CostCenter",
      {
        params: {
          ...params,
          $filter: `person_Instance_ID eq ${employeeId}`,
          $expand: db.s4_SystemInstance,
        },
      }
    );
  }

  getAll(employeeId: number) {
    return this.get<IResponse<IEmployeeS4HanaModel[]>>(
      "Personnel/Employees/GetAll_EmployeeS4CostCenter",
      {
        params: {
          $filter: `person_Instance_ID eq ${employeeId}`,
          $expand: db.s4_SystemInstance,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>(
      "Personnel/Employees/Delete_EmployeeS4CostCenter",
      {
        params: { Id: Id },
      }
    );
  }

  getAllS4CostCenterBySystemInstance(systemInstanceId: number) {
    return this.get<IResponse<IS4CostCenter[]>>(
      "Personnel/Employees/GetAll_s4_LoadCostCenter",
      {
        params: {
          Systemins: systemInstanceId,
        },
      }
    ).pipe(
      map((res) => {
        // res.data = [
        //   {
        //     Cost_Center: "19990220",
        //     CostCenterName: "19990220-kkk",
        //     id: null,
        //   },
        // ];
        res.data.map((item: IS4CostCenter) => {
          item.id = item.Cost_Center;
          item.ar_Name = item.CostCenterName;
          item.en_Name = item.CostCenterName;
          item.costCenter_Text =
            item.CostCenterName.split("-")[1] ?? item.CostCenterName;
        });
        return res;
      })
    );
  }
}
