import { Component, Input, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IJobStatusModel } from "app/system-setup/hr-setup/models/emp-job-status";
import { EmpJobStatusService } from "app/system-setup/hr-setup/services/emp-job-status.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-job-status-dropdown",
  templateUrl: "./job-status-dropdown.component.html",
  styleUrls: ["./job-status-dropdown.component.scss"],
})
export class JobStatusDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() disable: boolean = false;
  @Input() label: string = "status";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IJobStatusModel[]>();

  statustList: IJobStatusModel[];

  constructor(private empJobStatusService: EmpJobStatusService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllStatus();
  }

  getAllStatus() {
    this.empJobStatusService
      .getAllStatus()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IJobStatusModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.statustList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  onChange(values: IJobStatusModel[]) {
    this.change.emit(values);
  }
}
