import { Component, Input, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
  IJobKpiModel,
  IJobKpiRequest,
} from "app/org-management/model/job-title";
import { JobKpiService } from "app/org-management/services/job-profile/job-kpi.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-job-kpi",
  templateUrl: "./job-kpi.component.html",
  styleUrls: ["./job-kpi.component.scss"],
})
export class JobKpiComponent extends AppHelpers implements OnInit {
  @Input() jobId: number;

  form: FormGroup;
  submitted: boolean = false;

  constructor(private jobKpiService: JobKpiService) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      KPI_Ids: [null, [Validators.required]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      if (this.jobId > 0) this.getAll();
    }
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.showLoading();
    this.jobKpiService
      .getAllByJobTitleId(this.jobId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IJobKpiModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.formCtrls.KPI_Ids.setValue(res.data.map((kpi) => kpi.kpI_Id));
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IJobKpiRequest = {
        JobId: this.jobId,
        KPI_Ids: this.form.value.KPI_Ids,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IJobKpiRequest) {
    this.showLoading();
    this.jobKpiService
      .create(model)
      .subscribe((res: IResponse<IJobKpiModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          // this.getAll();
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }
}
