import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IRecruitmentPlanModel } from "app/recruitment/models/plan/recruitment-plan";
import { RecruitmentPlanService } from "app/recruitment/services/plan/recruitment-plan.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-recruitment-plan-dropdown",
  templateUrl: "./recruitment-plan-dropdown.component.html",
  styleUrls: ["./recruitment-plan-dropdown.component.scss"],
})
export class RecruitmentPlanDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() label: string = "plan";
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<IRecruitmentPlanModel[]>();
  planList: IRecruitmentPlanModel[] = [];
  // viewName: string;

  constructor(private recruitmentPlanService: RecruitmentPlanService) {
    super();
    // this.viewName = ViewsManager.add_recruitment_plan;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllPlans();
  }

  getAllPlans() {
    this.recruitmentPlanService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IRecruitmentPlanModel[]>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.planList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  onChange(plan: IRecruitmentPlanModel[]) {
    this.change.emit(plan);
  }

  // createDialog(value: string) {
  //   this.dialogsService
  //     .addFromDropdownDialog(addRecruitmentPlan, value)
  //     .afterClosed()
  //     .subscribe((res) => {
  //       this.getAllPlans();
  //     });
  // }
}
