import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IPriorityModel } from "app/system-setup/general-setup/models/priority";
import { PriorityService } from "app/system-setup/general-setup/services/priority.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-priorities-checkboxs",
  templateUrl: "./priorities-checkboxs.component.html",
  styleUrls: ["./priorities-checkboxs.component.scss"],
})
export class PrioritiesCheckboxsComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean;
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<IPriorityModel[]>();

  priorityList: IPriorityModel[];
  selectedItems: number[] = [];
  defControl = new FormControl();

  constructor(private priorityService: PriorityService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {
      this.getAll();
    }
  }

  ngOnChanges(): void {
    if (this.control.value.length == 0) {
      this.defControl.reset();
      this.priorityList = this.priorityList?.map((priority) => {
        return {...priority, isChecked: false};
      });
      this.selectedItems = [];
    }
  }

  getAll() {
    this.priorityService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPriorityModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.priorityList = res.data;
        }
        else {
          this.errorList(res.message?.messages);
        }
      });
  }

  onChange($event, id: number) {
    if ($event) {
      this.selectedItems.push(id);
    }
    else {
      const index = this.selectedItems.indexOf(id);
      this.selectedItems.splice(index, 1);
    }
    this.control.setValue(this.selectedItems);
    // console.log(this.control.value)
    // this.change.emit(values);
  }
}
