import { Component, EventEmitter, Input, OnChanges, Output, SimpleChanges, } from "@angular/core";
import { FormControl } from "@angular/forms";
import { LanguageService } from "app/shared/services/language.service";
import { Constants } from "app/shared/resources/constants";

@Component({
  selector: "app-input",
  templateUrl: "./input.component.html",
  styleUrls: ["./input.component.scss"],
})
export class InputComponent implements OnChanges {
  @Input() control: FormControl;
  @Input() label: string;
  @Input() submitted?: boolean = false;
  @Input() type?: string = "text";
  @Input() placeholder?: string = "";
  @Input() disabled?: boolean = false;
  @Input() readonly?: boolean = false;
  @Input() appearance?: string = Constants.defaultInputAppearance;
  @Input() class?: string;
  @Input() id?: string;
  @Input() step?: number = 1;
  @Input() isDecimalNumber?: boolean = false;
  @Input() autocomplete?: string;
  @Input() matAutocomplete?: any = null;
  @Output() keyUp? = new EventEmitter<any>();
  @Output() keyDown? = new EventEmitter<any>();
  @Output() keyPress? = new EventEmitter<any>();
  @Output() keyEnter? = new EventEmitter<any>();
  @Output() Change? = new EventEmitter<any>();
  @Output() Blur? = new EventEmitter<any>();
  isFloatLabel: boolean = Constants.defaultFloatLabel;

  constructor(
    public languageService: LanguageService) { }

  validators;

  ngOnChanges(changes: SimpleChanges): void {
    if (this.disabled) {
      this.control?.disable();
    }
    else if (!this.disabled) {
      this.control?.enable();
    }
    //	else this.control?.enable();

    // if (this.control) this.validators = this.control?.validator({} as FormControl);
    // console.log(this.control);
    // console.log(this.control, this.validators);
  }

  onkeyup(value: any) {
    this.keyUp.emit(value);
  }


  onkeydown(value: any) {
    this.keyDown.emit(value);
  }

  onkeypress(value: any) {
    this.keyPress.emit(value);
  }

  onkeyEnter(value: any) {
    this.keyEnter.emit(value);
  }

  onChange(value: any) {
    this.Change.emit(value);
  }

  onInput(value) {
    this.onChange(value);
    this.onkeyEnter(value);
    this.onkeypress(value);
    this.onkeydown(value);
    this.onkeyup(value);
    this.onkeyup(value);
  }

  onBlur(value: any) {
    this.Blur.emit(value);
  }
}
