import { Component, EventEmitter, Input, Output } from "@angular/core";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { FormControl } from "@angular/forms";
import { IBenefitModel } from "app/system-setup/bonus-setup/models/benefit";
import { BenefitService } from "app/system-setup/bonus-setup/services/benefit.service";
import { IResponse } from "app/shared/interfaces/responces";
import { MessageTypes } from "app/shared/enums/message-types.enum";

@Component({
  selector: "app-benefit-dropdown",
  templateUrl: "./benefit-dropdown.component.html",
  styleUrls: ["./benefit-dropdown.component.scss"]
})
export class BenefitDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = true;
  @Input() label: string = "benefit";
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<IBenefitModel[]>();

  list: IBenefitModel[];
  viewName: string;

  constructor(private benefitService: BenefitService) {
    super();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.benefitService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IBenefitModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.list = res.data;
        }
        else this.errorList(res?.message?.messages);
      });
  }

  createDialog(value: string) {
    //     this.dialogsService
    //         .addFromDropdownDialog(NationalityComponent, value)
    //         .afterClosed()
    //         .subscribe(() => {
    //             this.getAll();
    //         });
  }

  onChange(values: IBenefitModel[]) {
    this.change.emit(values);
  }
}
