import {Component, EventEmitter, Input, Output} from "@angular/core";
import {FormControl} from "@angular/forms";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {Idropdown} from "app/shared/interfaces/dropdown.interface";
import {IResponse} from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ViewsManager } from "app/shared/resources/views-manager";
import { CourseTypeComponent } from "app/system-setup/training-setup/components/course-type/course-type.component";
import {ICourseTypeModel} from "app/system-setup/training-setup/models/course-type";
import {CourseTypeService} from "app/system-setup/training-setup/services/course-type.service";

@Component({
  selector: "app-course-type-dropdown",
  templateUrl: "./course-type-dropdown.component.html",
  styleUrls: ["./course-type-dropdown.component.scss"],
})
export class CourseTypeDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "courseType";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<Idropdown[]>();
  courseTypeList: ICourseTypeModel[];
  viewName: string;

  constructor(private courseTypeService: CourseTypeService) {
    super();
    this.viewName = ViewsManager.course_type;

  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllCourseType();
  }

  getAllCourseType() {
    this.courseTypeService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res:IResponse<ICourseTypeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.courseTypeList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  onChange(values: ICourseTypeModel[]) {
    this.change.emit(values);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(CourseTypeComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllCourseType();
      });
  }
}
