import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IPlanCourseExpensesDeleteParams,
  IPlanCourseExpensesModel,
  IPlanCourseExpensesRequest
} from "app/training/models/training-plan/plan-courses-expenses";

@Injectable({
  providedIn: "root",
})
export class PlanExpensesService extends AppClientService {
  constructor() {
    super();
  }

  getAll(planId: number) {
    return this.get<IResponse<IPlanCourseExpensesModel[]>>(
      "Training/PlanCourseExpenses/GetAll",
      {
        params: {
          $filter: `plan_Course_ID eq ${planId}`,
          $expand: `${db.TRA_expense}`,
        },
      }
    );
  }

  create(data: IPlanCourseExpensesRequest) {
    return this.post<IResponse<IPlanCourseExpensesModel>>(
      "Training/PlanCourseExpenses/Create",
      data
    );
  }

  Delete(params: IPlanCourseExpensesDeleteParams) {
    return this.delete<IResponse>("Training/PlanCourseExpenses/Delete", {
      params: params,
    });
  }
}
