import { ErrorHandler, NgModule } from "@angular/core";
import { BrowserModule } from "@angular/platform-browser";

import { HTTP_INTERCEPTORS, HttpClient, HttpClientModule } from "@angular/common/http";
import { BrowserAnimationsModule } from "@angular/platform-browser/animations";
import { NgbModule } from "@ng-bootstrap/ng-bootstrap";
import { TranslateLoader, TranslateModule } from "@ngx-translate/core";
import { TranslateHttpLoader } from "@ngx-translate/http-loader";
import { PERFECT_SCROLLBAR_CONFIG, PerfectScrollbarConfigInterface, PerfectScrollbarModule, } from "ngx-perfect-scrollbar";
import { NgxSpinnerModule } from "ngx-spinner";

import { AppRoutingModule } from "./app-routing.module";
import { AppComponent } from "./app.component";
import { ContentLayoutComponent } from "./layouts/content/content-layout.component";
import { FullLayoutComponent } from "./layouts/full/full-layout.component";
import { SharedModule } from "./shared/shared.module";

import { DatePipe } from "@angular/common";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { JwtModule } from "@auth0/angular-jwt";
import { environment } from "environments/environment";
import { ToastrModule } from "ngx-toastr";
import { MessageService } from "primeng/api";
import { BreadcrumbModule } from "xng-breadcrumb";
import { ErrorInterceptor } from "./auth/services/error-interceptor.service";
import { TokenInterceptorService } from "./auth/services/token-interceptor.service";
import { ToasterPosition } from "./shared/enums/tostPositions.enum";
import { Storage } from "./shared/resources/local-storage";
import { WINDOW_PROVIDERS } from "./shared/services/theme-options/window.service";
import { MAT_DATE_LOCALE } from "@angular/material/core";
import { EncryptDecryptAuthInterceptor } from "app/auth/services/encrypt-decrypt-interceptor";

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true,
  wheelPropagation: false,
};

export function createTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http, "./assets/translate/", ".json");
}

export function tokenGetter() {
  return Storage.getToken;
}

@NgModule({
  declarations: [AppComponent, FullLayoutComponent, ContentLayoutComponent],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    SharedModule,
    HttpClientModule,
    NgbModule,
    NgxSpinnerModule,
    BreadcrumbModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: createTranslateLoader,
        deps: [HttpClient],
      },
    }),

    ToastrModule.forRoot({
      progressBar: false,
      positionClass: ToasterPosition.bottomCenter,
      enableHtml: true,
      preventDuplicates: false,
      //  autoDismiss: false,
      // timeOut: 7000, // use with auto autoDismiss(true)
      // timeOut: 0, // use with auto autoDismiss(true)
      // extendedTimeOut: 0,
      // closeButton: true,
    }),

    JwtModule.forRoot({
      config: {
        tokenGetter: tokenGetter,
        allowedDomains: [environment.baseUrl],
        // disallowedRoutes: ["http://localhost:4200/auth/login"],
      },
    }),

    PerfectScrollbarModule,
  ],
  providers: [
    MessageService,
    {
      provide: PERFECT_SCROLLBAR_CONFIG,
      useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG,
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptorService,
      multi: true,
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorInterceptor,
      multi: true,
    },
    {
      provide: MAT_DIALOG_DATA, useValue: {}
    },
    {
      provide: MatDialogRef, useValue: {}
    },
    {provide: MAT_DATE_LOCALE, useValue: "en-GB"},

    // Using for encrypt data
    // {
    //   provide: "BASE_URL",
    //   useFactory: getBaseUrl
    // },
    // {
    //   provide: HTTP_INTERCEPTORS,
    //   useClass: EncryptDecryptAuthInterceptor,
    //   multi: true
    // },
    WINDOW_PROVIDERS,
    DatePipe,
    // {provide: MAT_FORM_FIELD_DEFAULT_OPTIONS, useValue: {float: "always"}},
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}

export function getBaseUrl() {
  return document.getElementsByTagName("base")[0].href;
}
