import {
  IS4SystemInstanceModel,
  IS4SystemInstancePurposeModel,
} from "../models/s4-system-instance";

export function s4SystemInstancePurposeToMap(
  purposes: IS4SystemInstancePurposeModel[]
): IS4SystemInstancePurposeModel[] {
  return purposes.map((purpose) => {
    return <IS4SystemInstancePurposeModel>{
      ...purpose,
      purpose: purpose.name,
    };
  });
}

export function S4SystemInstanceToDisplay(
  instances: IS4SystemInstanceModel[]
): IS4SystemInstanceModel[] {
  return instances.map((instance) => {
    return <IS4SystemInstanceModel>{
      id: instance.id,
      ar_Name: instance.ar_Name,
      en_Name: instance.en_Name,
      api_Url_Prefix: instance.api_Url_Prefix,
      password: instance.password,
      userName: instance.userName,
      s4_Client_Code: instance.s4_Client_Code,
      s4_Company_Code: instance.s4_Company_Code,
    };
  });
}
