import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { AssignJobToOrgComponent } from "app/org-management/components/jobs-profile/assign-job-to-org/assign-job-to-org.component";
import { IJobTitleModel } from "app/org-management/model/job-title";
import { JobTitleService } from "app/org-management/services/job-profile/job-title.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-job-title-by-org-dropdown",
  templateUrl: "./job-title-by-org-dropdown.component.html",
  styleUrls: ["./job-title-by-org-dropdown.component.scss"],
})
export class JobTitleByOrgDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() organizationControl: FormControl;
  @Input() organizationControlRequired: boolean = false;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "jobTitle";
  @Input() disable: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<IJobTitleModel[]>();
  viewName: string;

  jobTitleList: IJobTitleModel[];
  filteredJobTitleList: IJobTitleModel[];

  constructor(private jobTitleService: JobTitleService) {
    super();
    this.viewName = ViewsManager.assign_job_to_org;
  }

  ngOnInit(): void {
    // if (this.CheckNoBusinessOrCompany()) this.getAllJobTitles();

    if (this.organizationControlRequired) {
      this.control.disable();
      this.filteredJobTitleList = [];
      this.jobTitleList = [];
    }
    this.organizationControl?.valueChanges.subscribe((organizationId) => {
      this.control.setValue(null);
      // if org control is required
      if (this.organizationControlRequired) {
        this.filteredJobTitleList = organizationId > 0 ? null : [];
        this.resetControl();
        if (organizationId > 0) {
          this.getAllJobTitlesByOrgId();
          if (!this.disable) {
            this.control.enable();
          }
        }
        else {
          this.control.disable();
        }
      }

      // if org control not required
      else {
        this.getAllJobTitles();
        // if (organizationId > 0) {
        //   this.resetControl();
        //   this.getAllJobTitlesByOrgId();
        // }
      }
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) {

      // if org control is required
      if (this.organizationControlRequired) {
        this.resetControl();
        if (this.organizationControl.value > 0) {
          this.getAllJobTitlesByOrgId();
          if (!this.disable) {
            this.control.enable();
          }
        }
        else {
          this.control.disable();
        }
      }

      // if org control not required
      else {
        this.getAllJobTitles();
        // if (organizationId > 0) {
        //   this.resetControl();
        //   this.getAllJobTitlesByOrgId();
        // }
      }

      //   this.organizationControl?.value > 0 ? this.getAllJobTitlesByOrgId() : this.getAllJobTitles();
    }
  }

  getAllJobTitlesByOrgId() {
    this.jobTitleService
      .getAllByOrgId(this.organizationControl.value)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IJobTitleModel[]>) => {
        if (res.data) {
          this.filteredJobTitleList = res.data;
          //  console.log("JobTitlesByOrgId", this.filteredJobTitleList);
        }
        // else {
        //   this.errorList(res?.message?.messages);
        // }
      });
  }

  getAllJobTitles() {
    this.jobTitleService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IJobTitleModel[]>) => {
        if ((res.message.messageType == MessageTypes.Success)) {
          this.jobTitleList = res.data;
        }
        else {
          this.errorList(res?.message?.messages);
        }
      });
  }

  resetControl() {
    setTimeout(() => {
      this.control.reset();
    }, 10);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(AssignJobToOrgComponent, value, this.organizationControl.value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllJobTitles();
      });
  }

  onChange(jobTitle: IJobTitleModel[]) {
    this.change.emit(jobTitle);
  }
}
