import { Component, Inject, Input, OnInit, SimpleChanges } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialogRef } from "@angular/material/dialog";
import { MatTableDataSource } from "@angular/material/table";
import { s4SystemInstancePurposeToMap } from "app/system-setup/s4-setup/mapper/s4-system-instance";
import {
  IS4SystemInstancePurposeModel,
  IS4SystemInstancePurposeRequest,
} from "app/system-setup/s4-setup/models/s4-system-instance";
import { S4SystemInstancePurposeService } from "app/system-setup/s4-setup/services/s4-system-instance-purpose.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { ICreateDialog } from "app/shared/interfaces/dropdown.interface";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-s4-system-instance-purpose",
  templateUrl: "./s4-system-instance-purpose.component.html",
  styleUrls: ["./s4-system-instance-purpose.component.scss"],
})
export class S4SystemInstancePurposeComponent extends AppHelpers implements OnInit {
  @Input() s4SystemInstanceId: number;

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = ["Ser", "id", "purpose", "procedure_Name", "actions"];
  isLoaded: boolean = false;
  optionalColomns: string[] = [];

  dataSource: MatTableDataSource<IS4SystemInstancePurposeModel>;

  constructor(
    private s4SystemInstancePurposeService: S4SystemInstancePurposeService,
    @Inject(MAT_DIALOG_DATA) public data: ICreateDialog,
    public dialogRef: MatDialogRef<S4SystemInstancePurposeComponent>
  ) {
    super();
  }

  ngOnInit(): void {
    // if (this.s4SystemInstanceId && this.CheckNoBusinessOrCompany())
    //   this.getAll();

    this.form = this.fb.group({
      // id: [0, { nonNullable: true }],
      purpose: [null, [Validators.required]],
      procedureName: [null, [Validators.required]],
    });
    if (this.data.value) {
      this.formCtrls.purpose.setValue(this.data.value);
      this.s4SystemInstanceId = this.data.id;
    }
  }

  ngOnChanges(changes: SimpleChanges): void {
    // if (changes.s4SystemInstanceId.currentValue > 0) this.getAll();
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
    if (this.s4SystemInstanceId && this.checkNoBusinessOrCompany()) this.getAll();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    if (!this.data.isDialog) {
      this.s4SystemInstancePurposeService
        .getAllBySystemInstanceId(this.s4SystemInstanceId)
        .pipe(this.cancelRequest())
        .subscribe((res: IResponse<IS4SystemInstancePurposeModel[]>) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.dataSource = new MatTableDataSource(s4SystemInstancePurposeToMap(res.data));
            this.isLoaded = true;
          }
          else this.errorList(res?.message?.messages);
        });
    }
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IS4SystemInstancePurposeRequest = {
        SystemInstance_ID: this.s4SystemInstanceId,
        // ID: this.form.value.id,
        Name: this.form.value.purpose,
        Procedure_Name: this.form.value.procedureName,
      };

      this.create(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IS4SystemInstancePurposeRequest) {
    this.showLoading();
    this.s4SystemInstancePurposeService
      .create(model)
      .subscribe((res: IResponse<IS4SystemInstancePurposeModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
          if (this.data.isDialog) this.dialogRef.close();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  onDelete(systemInstancePurpose: IS4SystemInstancePurposeModel): void {
    let msg = this.msgTranslate(systemInstancePurpose.name);
    this.deleteDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(systemInstancePurpose);
    });
  }

  delete(systemInstancePurpose: IS4SystemInstancePurposeModel) {
    this.showLoading();
    this.s4SystemInstancePurposeService
      .Delete(systemInstancePurpose.id)
      .subscribe((res: IResponse) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        }
        else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
  }

  clearForm() {
    this.form.reset(/* { id: 0 } */);
    this.submitted = false;
  }

  onCloseDialog() {
    this.dialogRef.close();
  }
}
