import { Component, Input, OnInit, Output, EventEmitter } from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponseList } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { AttendancePlanService } from "app/system-setup/attendance-setup/services/attendance-plan.service";
import { IAttendancePlanModel } from "app/system-setup/attendance-setup/models/plan";
import { AddPlanComponent } from "app/system-setup/attendance-setup/components/plan/add-plan/add-plan.component";

@Component({
  selector: "app-attendance-plan-dropdown",
  templateUrl: "./attendance-plan-dropdown.component.html",
  styleUrls: ["./attendance-plan-dropdown.component.scss"],
})
export class AttendancePlanDropdownComponent
  extends BaseDropdown
  implements OnInit {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "attendancePlan";
  @Input() appendTo: string | HTMLElement = "body";
  @Output() change = new EventEmitter<IAttendancePlanModel[]>();
  viewName: string;

  plantList: IAttendancePlanModel[];

  constructor(private attendancePlanService: AttendancePlanService) {
    super();
    this.viewName = ViewsManager.add_plan;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllPlans();
  }

  getAllPlans() {
    this.attendancePlanService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponseList<IAttendancePlanModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.plantList = res.data;
        }
        else this.errorList(res.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(AddPlanComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllPlans();
      });
  }

  onChange(values: IAttendancePlanModel[]) {
    this.change.emit(values);
  }
}
