import { Injectable } from "@angular/core";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { IInstructorModel } from "../models/instructor";
import { IFileApiModel } from "app/shared/interfaces/files";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
  providedIn: "root",
})
export class InstructorService extends AppClientService {
  constructor() {
    super();
  }

  create(data: FormData) {
    return this.post<IResponse<IInstructorModel>>(
      "Training/Instructor/Create",
      data
    );
  }

  update(data: FormData) {
    return this.post<IResponse<IInstructorModel>>(
      "Training/Instructor/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IInstructorModel[]>>(
      "Training/Instructor/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IInstructorModel[]>>(
      "Training/Instructor/GetAll"
    );
  }

  getById(id: number) {
    return this.get<IResponseList<IInstructorModel[]>>(
      "Training/Instructor/GetAll",
      {
        params: {
          $filter: `id eq ${id}`,
        },
      }
    );
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Training/Instructor/Delete", {
      params: { Id: Id },
    });
  }

  getAllAttachmentsByInstructorId(InstructorID: number) {
    return this.get<IResponse<IFileApiModel[]>>(
      "Training/Instructor/GetAllInstructorAttachments",
      {
        params: {
          InstructorID: InstructorID,
        },
      }
    );
  }

  deleteInstructorAttachments(instructorID: number, attchmentId: number) {
    return this.delete<IResponse>(
      "Training/Instructor/DeleteInstructorAttachment",
      {
        params: {
          InstructorID: instructorID,
          AttachmentID: attchmentId,
        },
      }
    );
  }

  exportExcel() {
    return this.getAll();
  }
}
