import { Component, OnInit } from "@angular/core";
import { FormControl } from "@angular/forms";
import { ParamsService } from "app/shared/services/params.service";
import { Storage } from "app/shared/resources/local-storage";

@Component({
  selector: "app-localization-core",
  templateUrl: "./localization-core.component.html",
  styleUrls: ["./localization-core.component.scss"]
})
export class LocalizationCoreComponent implements OnInit {
  countryId = new FormControl(null);
  versionId = new FormControl(null);

  constructor(public paramsService: ParamsService) {
    this.countryId.setValue(Storage.getCountryID);
    this.versionId.setValue(Storage.getVersionID);
    // store countryID
    this.countryId.valueChanges.subscribe(countryId => {
      paramsService.CountryID$.next(countryId);
      Storage.setCountryID(countryId);
    });

    // store versionID
    this.versionId.valueChanges.subscribe(versionId => {
      paramsService.VersionID$.next(versionId);
      Storage.setVersionID(versionId);
    });
  }

  ngOnInit(): void {
  }

}
