import { Component, EventEmitter, Input, OnInit, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { IEmploymentTypeModel } from "app/recruitment/models/employment-type";
import { EmploymentTypeService } from "app/recruitment/services/employment-type.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-employment-type-dropdown",
  templateUrl: "./employment-type-dropdown.component.html",
  styleUrls: ["./employment-type-dropdown.component.scss"],
})
export class EmploymentTypeDropdownComponent
  extends BaseDropdown
  implements OnInit
{
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() appendTo: string | HTMLElement = "body";
  @Input() label: string = "employmentType";
  @Input() disable: boolean = false;
  @Output() change = new EventEmitter<IEmploymentTypeModel[]>();

  employmentTypeList: IEmploymentTypeModel[];

  constructor(private employmentTypeService: EmploymentTypeService) {
    super();
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAll();
  }

  getAll() {
    this.employmentTypeService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IEmploymentTypeModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.employmentTypeList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  onChange(employmentType: IEmploymentTypeModel[]) {
    this.change.emit(employmentType);
  }
}
