import {
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  SimpleChanges,
} from "@angular/core";
import { FormControl } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import { ICourseModel } from "app/training/models/course/course";
import { PlanCoursesService } from "app/training/services/traning-plan/plan-courses.service";
import { AddCourseFormComponent } from "app/training/components/courses/add-course/add-course-form/add-course-form.component";
import { ViewsManager } from "app/shared/resources/views-manager";

@Component({
  selector: "app-course-by-plan-dropdown",
  templateUrl: "./course-by-plan-dropdown.component.html",
  styleUrls: ["./course-by-plan-dropdown.component.scss"],
})
export class CourseByPlanDropdownComponent extends BaseDropdown implements OnInit {
  @Input() control: FormControl;
  @Input() planId: number;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "courses";
  @Input() disable: boolean = false;
  @Input() appendTo: string | HTMLElement;
  @Input() courseCategoryId: number;
  @Input() changeList: boolean;
  @Output() change = new EventEmitter<ICourseModel[]>();

  courseList: ICourseModel[];
  viewName: string;

  constructor(private planCoursesService: PlanCoursesService) {
    super();
  //  this.viewName = ViewsManager.add_course;
  }

  ngOnInit(): void {}

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.planId > 0) this.getAll();
    else this.courseList = [];
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      ((changes.planId?.currentValue != changes.planId?.previousValue &&
          !changes.planId?.firstChange) ||
        changes.changeList) &&
      this.checkNoBusinessOrCompany()
    )
      this.getAll();
  }

  // use in add plan course (courses can be assign to this plan)
  getAll() {
    this.planCoursesService
      .getAllCourses(this.planId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICourseModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.courseList = res.data;
        }
        else {
          this.errorList(res?.message?.messages);
        }
      });
  }

  onChange(model: ICourseModel[]) {
    this.change.emit(model);
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(AddCourseFormComponent, value, null, {
        height: "90vh",
        maxHeight: "90vh",
        width: "85vw",
        maxWidth: "100vw"
      })
      .afterClosed()
      .subscribe((res) => {
        this.getAll();
      });
  }

}
