import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IInsuranceOfficeRequest, IInsuranceOfficeModel } from "../models/insurance-office";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class InsuranceOfficeService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IInsuranceOfficeRequest) {
		return this.post<IResponse<IInsuranceOfficeModel>>("HRSetup/InsuranceOffice/Create", data);
	}

	update(data: IInsuranceOfficeRequest) {
		return this.post<IResponse<IInsuranceOfficeModel>>("HRSetup/InsuranceOffice/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IInsuranceOfficeModel[]>>("HRSetup/InsuranceOffice/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<IInsuranceOfficeModel[]>>("HRSetup/InsuranceOffice/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("HRSetup/InsuranceOffice/Delete", {
			params: { Id: id },
		});
	}
  
  exportExcel() {
    this.getAll();
  }
}
