import {Component, Input, Output, EventEmitter} from "@angular/core";
import {FormControl} from "@angular/forms";
import {InstitutionComponent} from "app/system-setup/hr-setup/components/institution/institution.component";
import {IInstitutionModel} from "app/system-setup/hr-setup/models/institution";
import {InstitutionService} from "app/system-setup/hr-setup/services/institution.service";
import {MessageTypes} from "app/shared/enums/message-types.enum";
import {IResponse} from "app/shared/interfaces/responces";
import { BaseDropdown } from "app/shared/resources/base-dropdown";
import {ViewsManager} from "app/shared/resources/views-manager";

@Component({
  selector: "app-institution-dropdown",
  templateUrl: "./institution-dropdown.component.html",
  styleUrls: ["./institution-dropdown.component.scss"],
})
export class InstitutionDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "institution";
  @Input() appendTo: string | HTMLElement;
  @Output() change = new EventEmitter<IInstitutionModel[]>();

  institutiontList: IInstitutionModel[];
  viewName: string;

  constructor(private institutionService: InstitutionService) {
    super();
    this.viewName = ViewsManager.identity_type;
  }

  ngOnInit(): void {
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllIdentityType();
  }

  getAllIdentityType() {
    this.institutionService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IInstitutionModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.institutiontList = res.data;
        } else this.errorList(res.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(InstitutionComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllIdentityType();
      });
  }

  onChange(values: IInstitutionModel[]) {
    this.change.emit(values);
  }
}
