import { Injectable } from "@angular/core";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import { ISTPModel, ITSPRequest } from "app/training/models/tsp";
import { map } from "rxjs/operators";
import { tspTodisplay } from "../mapper/tsp";

@Injectable({
  providedIn: "root",
})
export class TspService extends AppClientService {
  constructor() {
    super();
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<ISTPModel[]>>("Training/TSP/GetList", {
      params: params,
    });
  }

  getAll() {
    return this.get<IResponse<ISTPModel[]>>("Training/TSP/GetAll").pipe(
      map((res: IResponse<any[]>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          res.data = tspTodisplay(res.data);
          return res;
        }
      })
    );
  }

  getById(id: number) {
    return this.get<IResponseList<ISTPModel[]>>("Training/TSP/GetAll", {
      params: {
        $filter: `id eq ${id}`,
      },
    });
  }

  create(data: ITSPRequest) {
    return this.post<IResponse<ISTPModel>>("Training/TSP/Create", data);
  }

  update(data: ITSPRequest) {
    return this.post<IResponse<ISTPModel>>("Training/TSP/Edit", data);
  }

  Delete(Id: number) {
    return this.delete<IResponse>("Training/TSP/Delete", {
      params: { Id: Id },
    });
  }

  exportExcel() {
    return this.getAll();
  }
}
