import {Injectable} from '@angular/core';
import {AppClientService} from 'app/shared/services/app-client.service';
import {IResponse} from 'app/shared/interfaces/responces';
import {IPriorityModel} from 'app/system-setup/general-setup/models/priority';

@Injectable({
  providedIn: 'root',
})
export class PriorityService extends AppClientService {
  constructor() {
    super();
  }

  getAll() {
    return this.get<IResponse<IPriorityModel[]>>('Priority/GetAll');
  }
}
