import { Component, OnInit, Input, SimpleChanges, OnChanges } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import {
	IJobCompetenceRequest,
	IJobCompetenceModel,
	IJobCompetenceDeleteRequest,
} from "app/org-management/model/job-title";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { CustomValidators } from "ngx-custom-validators";
import { MatTableDataSource } from "@angular/material/table";
import { JobCompetencesService } from "app/org-management/services/job-profile/job-competences.service";
import { ICompetenceTypeModel } from "app/system-setup/org-management-setup/models/competence-type";
import { CompetenceTypeService } from "app/system-setup/org-management-setup/services/competence-type.service";
import { MatDialog } from "@angular/material/dialog";
import { JobCompetenceLevelDetailsDialogComponent } from "../job-competence-level-details-dialog/job-competence-level-details-dialog.component";
import { jobCompetenceToMap } from "app/org-management/mapper/job-title";

@Component({
	selector: "app-job-competences",
	templateUrl: "./job-competences.component.html",
	styleUrls: ["./job-competences.component.scss"],
})
export class JobCompetencesComponent extends AppHelpers implements OnInit, OnChanges {
	@Input() jobId: number;
	@Input() showIndexOnly: boolean = false;

	form: FormGroup;
	submitted: boolean = false;

	mandatoryColomns: string[] = [
		"Ser",
		// "id",
		"competence",
		"competenceLevel",
		"competencE_Weight",
		"competenceType",
		"actions",
	];
	isLoaded: boolean = false;
	optionalColomns: string[] = [];

	dataSource: MatTableDataSource<IJobCompetenceModel>;

	competenceTypeList: ICompetenceTypeModel[];

	constructor(
		private jobCompetencesService: JobCompetencesService,
		private competenceTypeService: CompetenceTypeService,
		private dialog: MatDialog
	) {
		super();
	}

	ngOnInit() {
		this.form = this.fb.group({
			// id: [0, { nonNullable: true }],
			competenceId: [null, [Validators.required]],
			competenceLevelId: [{ value: null, disabled: true }, [Validators.required]],
			competencE_Weight: [
				null,
				[
					Validators.required,
					CustomValidators.digits,
					CustomValidators.gte(0),
					CustomValidators.lte(2147483647),
				],
			],
			competenceTypeId: [null, [Validators.required]],
		});

    if(this.showIndexOnly) {
      // remove "ser"
      this.mandatoryColomns.shift();
      // remove "actions"
      this.mandatoryColomns.pop();
    }
	}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.jobId?.currentValue > 0) this.getAll();
  }

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany()) {
			this.getAllCompetenceTypes();
			if (this.jobId > 0) this.getAll();
		}
	}

	get formCtrls() {
		return this.form.controls;
	}

	getAll() {
    this.dataSource = new MatTableDataSource(null)
    this.isLoaded = false;
		this.jobCompetencesService
			.getAllByJobTitleId(this.jobId)
      .pipe(this.cancelRequest())
			.subscribe((res: IResponse<IJobCompetenceModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					this.dataSource = new MatTableDataSource(jobCompetenceToMap(res.data));
					this.isLoaded = true;
				} else this.errorList(res?.message?.messages);
			});
	}

	submit() {
		// console.log(this.form.value);
		this.submitted = true;
		if (this.form.valid) {
			const model: IJobCompetenceRequest = {
				ComptenceID: this.form.value.competenceId,
				ComptenceLevelID: this.form.value.competenceLevelId,
				Comptence_Jobtitle: {
					JOB_Title_ID: this.jobId,
					COMPETENCE_TYPE_ID: this.form.value.competenceTypeId,
					COMPETENCE_Weight: this.form.value.competencE_Weight,
				},
			};

			this.create(model);
		} // else this.toastr.error("General.formNotValid");
	}

	create(model: IJobCompetenceRequest) {
		this.showLoading();
		this.jobCompetencesService.create(model).subscribe((res: IResponse<IJobCompetenceModel>) => {
			if (res?.message?.messageType == MessageTypes.Success) {
				this.clearForm();
				this.addSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	onDelete(jobCompetence: IJobCompetenceModel): void {
		let msg = this.msgTranslate(jobCompetence.competence);
		this.deleteDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(jobCompetence);
		});
	}

	delete(jobCompetence: IJobCompetenceModel) {
		let params: IJobCompetenceDeleteRequest = {
			JobId: this.jobId,
			ComDetailsId: jobCompetence.competencE_Detail_ID,
		};

		this.showLoading();
		this.jobCompetencesService.Delete(params).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				this.getAll();
			} else this.errorList(res?.message?.messages);
			this.hideLoading();
		});
	}

	clearForm() {
		this.form.reset({ id: 0 });
		this.submitted = false;
	}

	openCompetenceLevelDetailsDialog() {
		this.dialog
			.open(JobCompetenceLevelDetailsDialogComponent, {
				data: {
					competenceId: this.formCtrls.competenceId.value,
					competencelevelId: this.formCtrls.competenceLevelId.value,
				},
				position: {
					top: "40px",
				},
        minWidth:"50vw",
				maxWidth: "85vw",
				panelClass: "dialog-emp-form",
				autoFocus: false,
			})
			.afterClosed()
			.subscribe(() => {});
	}

	// Competence Type List
	getAllCompetenceTypes() {
		this.showLoading();
		this.competenceTypeService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICompetenceTypeModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          this.competenceTypeList = res.data;
        } else this.errorList(res?.message?.messages);
        this.hideLoading();
      });
	}
}
