import { Component, inject, OnDestroy, OnInit } from "@angular/core";
import { NavigationEnd, Router } from "@angular/router";
import { Subscription } from "rxjs";
import { filter } from "rxjs/operators";
import { AuthService } from "./auth/services/auth.service";
import { Storage } from "./shared/resources/local-storage";
import { LanguageService } from "./shared/services/language.service";
import { LoadingService } from "app/shared/services/loading.service";
import { NgxSpinnerService } from "ngx-spinner";
import { MessageService } from "primeng/api";

@Component({
    selector: "app-root",
    templateUrl: "./app.component.html",
})
// extends AppSettings
export class AppComponent implements OnInit, OnDestroy {
    subscription: Subscription;
    loading: boolean = true;
    isUserLoggedIn: boolean = false;
    spinner = inject(NgxSpinnerService);

    constructor(
        private router: Router,
        private languageService: LanguageService,
        private authService: AuthService,
        private LoadingService: LoadingService,
        private messageService: MessageService,
    ) {
        this.isUserLoggedIn = Storage.isUserLoggedIn;
        //  if (!this.authService.isTokenExpired) this.authService.CompanyAndBusiness();
        this.authService.isUserLogin$.subscribe((user) => {
            this.isUserLoggedIn = user;

            if (user) {
                //  this.authService.CompanyAndBusiness();
                // this.authService.BusinessList$.subscribe((val) => {
                //   if (!Storage.getBusinessID)
                //     authService.onSelectBusiness(val[0].businessId);
                //   if (!Storage.getCompanyID)
                //     authService.onSelectCompany(val[0].companies[0].id);
                // });
                // this.authService.BusinessID$.subscribe((val) => {
                //   console.log("AppComponent BusinessID$", val);
                // });
                // this.authService.CompanyID$.subscribe((val) => {
                //   console.log("AppComponent CompanyID$", val);
                // });
            }
        });

        // this.authService.BusinessID$.subscribe((val) => {
        //   console.log("BusinessID$", val);
        // });
        // this.authService.CompanyID$.subscribe((val) => {
        //   console.log("CompanyID$", val);
        // });
        let isLoadingDisplay: boolean;
        this.LoadingService.$display.subscribe(show => {
            if (show && !isLoadingDisplay) this.LoadingService.show();
            else this.LoadingService.hide();

            isLoadingDisplay = show;
        });
    }

    ngOnInit() {
        this.subscription = this.router.events
            .pipe(filter((event) => event instanceof NavigationEnd))
            .subscribe(() => window.scrollTo(0, 0));

        let currentLang: string = this.languageService.currentLang;
        this.languageService.changeLanguage(currentLang);

        setTimeout(() => {
            this.loading = false;
        }, 3500);


        // setTimeout(() => {
        //     this.messageService.add({
        //         key: "delete",
        //         sticky: true,
        //         severity: "warn",
        //         summary: "General.confirmDelete",
        //         detail: "this.data"
        //     });
        // }, 3000);
    }


    onConfirm() {
        this.messageService.clear("delete");
    }

    onReject() {
        this.messageService.clear("delete");
    }

    ngOnDestroy() {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
    }
}
