import { Component, EventEmitter, Input, Output } from "@angular/core";
import { FormControl } from "@angular/forms";
import { AddPermissionComponent } from "app/system-setup/attendance-setup/components/permissions/add-permission/add-permission.component";
import { IPermissionTypeModel } from "app/system-setup/attendance-setup/models/permissions";
import { PermissionTypeService } from "app/system-setup/attendance-setup/services/permissions.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { ViewsManager } from "app/shared/resources/views-manager";
import { BaseDropdown } from "app/shared/resources/base-dropdown";

@Component({
  selector: "app-permission-type-dropdown",
  templateUrl: "./permission-type-dropdown.component.html",
  styleUrls: ["./permission-type-dropdown.component.scss"],
})
export class PermissionTypeDropdownComponent extends BaseDropdown {
  @Input() control: FormControl;
  @Input() submitted: boolean = false;
  @Input() multi: boolean = false;
  @Input() filter: boolean = false;
  @Input() label: string = "permissionType";
  @Input() appendTo: string;
  @Output() change = new EventEmitter<IPermissionTypeModel[]>();

  permissionTypeList: IPermissionTypeModel[];
  viewName: string;

  constructor(private permissionTypeService: PermissionTypeService) {
    super();
    this.viewName = ViewsManager.add_permission;
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany()) this.getAllPermissionTypes();
  }

  getAllPermissionTypes() {
    this.permissionTypeService
      .getAll()
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<IPermissionTypeModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.permissionTypeList = res.data;
        } else this.errorList(res?.message?.messages);
      });
  }

  createDialog(value: string) {
    this.dialogsService
      .addFromDropdownDialog(AddPermissionComponent, value)
      .afterClosed()
      .subscribe((res) => {
        this.getAllPermissionTypes();
      });
  }

  onChange(values: IPermissionTypeModel[]) {
    this.change.emit(values);
  }
}
