import { Injectable, inject } from "@angular/core";
import { DialogPosition, MatDialog } from "@angular/material/dialog";
import { DeleteDialogComponent } from "../components/dialogs/delete-dialog/delete-dialog.component";
import { ConfirmDialogComponent } from "../components/dialogs/confirm-dialog/confirm-dialog.component";
import { HelpersFunctions } from "../resources/helpers-functions";

@Injectable({
  providedIn: "root",
})
export class DialogsService {
  dialog: MatDialog = inject(MatDialog);

  constructor() {}

  deleteDialog(data: any) {
    return this.dialog
      .open(DeleteDialogComponent, {
        data: data,
        panelClass: "dialog-no-padding",
      }).afterClosed();
  }

  confirmDialog(data: any) {
    return this.dialog
      .open(ConfirmDialogComponent, {
        data: data,
      }).afterClosed();
  }

  addFromDropdownDialog(
    component,
    value?: string,
    itembyId: number = 0,
    options = new DialogSetting()
  ) {
    return this.dialog.open(component, {
      data: {
        value: value,
        isDialog: true,
        id: itembyId,
      },
      minWidth: options?.minWidth ?? "60%",
      maxHeight: options?.maxHeight ?? "80vh",
      width: options?.width,
      height: options?.height,
      panelClass: options.panelClass ?? "dialog-no-padding",
      autoFocus: false,
    });
  }

  dialogLeftOrRightCard(
    component,
    targetPositions: DOMRect,
    dialogWidth: number,
    isHalf: boolean,
    maxHeight: string,
    data: any,
    calcDialogInLeft: boolean = false
  ) {
    // console.log("from service");
    let dialogPositions: DialogPosition = HelpersFunctions.positionLeftOrRight(
      targetPositions,
      dialogWidth,
      isHalf,
      calcDialogInLeft
    );

    return this.dialog
      .open(component, {
        data: data,
        position: dialogPositions,
        width: `${dialogWidth}px`,
        // maxWidth: "350px",
        maxHeight: maxHeight,
        autoFocus: false,
      }).afterClosed().subscribe((closed: boolean) => {});
  }

  addEditDialog(component, data?: any, setting = new DialogSetting()) {
    return this.dialog.open(component, {
      data: data,
      position: {
        top: setting.top,
      },
      minWidth: setting.minWidth,
      height: setting.height,
      width: setting.width,
      maxWidth: setting.maxWidth,
      maxHeight: setting.maxHeight,
      minHeight: setting.minHeight,
      panelClass: "dialog-emp-form",
      autoFocus: false,
      disableClose: setting.disableClose,
    });
  }
}

export class DialogSetting {
  minWidth?: string = "50vw";
  minHeight?: string = "auto";

  maxWidth?: string = "80vw";
  maxHeight?: string = "98vh";

  width?: string = "auto";
  height?: string = "auto";

  top?: "40px";

  disableClose?: boolean = true;
  panelClass?: string;
}
