import {Injectable} from '@angular/core';
import {IResponse} from 'app/shared/interfaces/responces';
import {AppClientService} from 'app/shared/services/app-client.service';
import {
  IJobStatusEventModel,
  IJobStatusModel,
} from '../models/emp-job-status';
import {db} from 'app/shared/resources/database-tabels';

@Injectable({
  providedIn: 'root',
})
export class EmpJobStatusService extends AppClientService {
  constructor() {
    super();
  }

  getAllStatus() {
    return this.get<IResponse<IJobStatusModel[]>>('HRSetup/Status/GetAll', {
      params: {
        $expand: db.core_Status_Properties
      }
    });
  }

  getAllStatusEvents(statusId: number) {
    return this.get<IResponse<IJobStatusEventModel[]>>(
      'HRSetup/Status/GetAll_StatusProperty',
      {
        params: {$filter: `statusID eq ${statusId}`},
      }
    );
  }
}
